<template>
  <div v-zinx v-if="state">
    <div class="wrapper-mask" @click="onClose"></div>
    <div class="vip_sign_comp" :style="`top: ${top};`">
      <div ref="vipSignRef" class="vip_sign_box">
        <div class="vip_sign_box_anim" :class="{ chest: flipOver == 1, helper: flipOver == 2 }">
          <div v-show="isChest" class="vip_sign_box_body">
            <div class="vip_sign_box_title">
              <div class="lt">
                <span class="vip_sign_box_title_bonus">{{ $t('UI.Vip_Login_Sign_In_Bonus') }}</span>
                <span class="vip_sign_box_title_login">{{ $t('UI.Vip_Login_Sign_In_Login') }}</span>
              </div>
              <img class="rt_calendar" :src="loadIcon('vip_activity_calendar')" alt="" />
            </div>
            <div class="vip_sign_box_info">
              <div class="lt">
                {{ $t('UI.Vip_Login_Sign_In_Current_Level') }}:&nbsp;{{ $t('UI.Vip_Login_Sign_In_Current_Vip')
                }}{{ getVipCurrentLevel() }}
              </div>
              <div class="rt">
                <div class="rt_text">
                  <span class="text">{{ $t('UI.Vip_Login_Sign_In_Current_Vip') }}.</span>
                  <span class="level">{{ currentSwiperLevel }}</span>
                </div>
                <div class="rt_icon" @click="onHelper">
                  <icon-font name="icon-icon_questionmark" :size="isApp ? 14 : 20" dark="t_white" />
                </div>
              </div>
            </div>
            <div class="vip_sign_box_content">
              <swiper ref="vipSignSwiper" :options="swiperOptions">
                <swiper-slide v-for="item in signList" :key="item.vip_id">
                  <div class="vip_sign_box_swiper">
                    <template v-if="item.list && item.list.length">
                      <div
                        class="content_item"
                        :class="{
                          receive: box.sign == 0,
                          disabled:
                            activeInfo.vip_id != item.vip_id || (activeInfo.vip_id == item.vip_id && box.sign == 1)
                        }"
                        v-for="(box, i) in item.list"
                        :key="box.id"
                        @click="onReceive(box)"
                      >
                        <div class="content_item_title">{{ $t('UI.Vip_Login_Sign_In_Day') }}{{ box.day }}</div>
                        <div class="content_item_gift">
                          <!--  ||  -->
                          <template v-if="box.sign == 0 || box.sign == 2">
                            <!--  -->
                            <template v-if="item.list.length == i + 1">
                              <img class="activity_last_gift" :src="loadIcon('vip_activity7_gift')" alt="" />
                            </template>
                            <template v-else>
                              <img class="activity_gift" :src="loadIcon('vip_activity_gift')" alt="" />
                            </template>
                          </template>
                          <!--  -->
                          <template v-if="box.sign == 1">
                            <img
                              class="activity_received"
                              :class="{ last: item.list.length == i + 1 }"
                              :src="loadIcon('vip_activity_received')"
                              alt=""
                            />
                          </template>
                        </div>
                        <div class="content_item_money">{{ currencySymbol }}{{ box.gold }}</div>
                        <!--  -->
                        <template v-if="box.sign == 0">
                          <div v-receive class="content_item_light" :class="{ last: item.list.length == i + 1 }"></div>
                        </template>
                      </div>
                    </template>
                  </div>
                </swiper-slide>
              </swiper>
              <div class="swiper-button-prev">
                <icon-font name="icon-icon_more" :size="isApp ? 14 : 18" dark="t_white" />
              </div>
              <div class="swiper-button-next">
                <icon-font name="icon-icon_more" :size="isApp ? 14 : 18" dark="t_white" />
              </div>
            </div>
            <div class="vip_sign_box_btn">
              <div class="btn" v-zoom @click="onRecharge">
                {{ $t('UI.Vip_Login_Sign_In_Recharge') }}
              </div>
            </div>
          </div>
          <div v-show="!isChest" class="vip_sign_box_help">
            <div class="vip_sign_box_help_title">
              <div class="vip_sign_box_help_title_back" @click="onChest">
                <icon-font name="icon-Icon_left" :size="isApp ? 16 : 24" dark="t_white" />
              </div>
              <div class="vip_sign_box_help_title_text">{{ $t('UI.Vip_Login_Sign_In_Helper') }}</div>
            </div>
            <div class="vip_sign_box_help_content">
              <div ref="vipSignHelpRef" class="vip_sign_box_help_content_text">
                {{ helpText }}
              </div>
              <template v-if="isScrollBtn">
                <div class="vip_sign_box_help_content_arrow" v-zoom @click="onArrow">
                  <icon-font name="icon-icon_more" :size="isApp ? 14 : 20" dark="t_white" />
                </div>
              </template>
            </div>
          </div>
        </div>
        <div class="vip_sign_comp_close" @click="onClose">
          <icon-font name="icon-icon_pop_close1" :size="isApp ? 28 : 32" dark="t_white" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import { POP_TIME } from '@/api/setting.js'
import { getResour } from '@/utils/loadServer.js'
import { getActivitieSignList, getActivitieSignSubmit } from '@/api/modules/config.js'
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import PublishSubscribe from 'publish-subscribe-js'
import loadIcon from '@/assets/icons.js'
import 'swiper/css/swiper.css'

export default {
  components: {
    Swiper,
    SwiperSlide
  },
  directives: {
    // 
    receive: {
      inserted(el) {
        if (!el) return

        const { libsPath = '', galaceanPath = '' } = JSON.parse(process.env.VUE_APP_PATH)
        getResour('galacean', [`${libsPath}/ge_v1.6.5.js`], r => {
          if (!r) return
          if (!window.ge) return

          const player = new window.ge.Player({
            loop: true,
            container: el,
            pixelRatio: window.devicePixelRatio || 2
          })
          player.loadScene(`${galaceanPath}/vip_sign.json`).catch(err => {
            player.dispose()
            if (!(el instanceof HTMLDivElement)) return

            el.style.backgroundImage = `url(${loadIcon('vip_activity_light')})`
            el.style.backgroundRepeat = 'no-repeat'
            el.style.backgroundSize = '100% 80%'
            el.style.backgroundPosition = 'center'
          })
        })
      }
    }
  },
  props: {
    top: {
      type: [Number, String],
      default: '50%'
    },
    // 
    onCancel: {
      type: Function,
      default() {
        return () => {}
      }
    }
  },
  data() {
    return {
      state: false,
      isChest: true, // 
      flipOver: null, //  1: 2:
      activeInfo: {}, // 
      signList: [], // 
      swiperIndex: undefined, // 
      isScrollBtn: false, // 
      // 
      swiperOptions: {}
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'languageType', 'systemConfig']),
    ...mapState('dialog', ['popupState']),
    ...mapState('switchState', ['currencyShow']),
    ...mapGetters('base', ['getVipCurrentLevel']),
    // 
    currencySymbol() {
      const { currency_symbol = '' } = this.systemConfig
      return this.currencyShow ? currency_symbol : ''
    },
    // 
    helpText() {
      const { act_desc = '' } = this.activeInfo

      return act_desc
    },
    // 
    currentSwiperLevel() {
      const { vip_id = 1 } = this.user
      let vipId = 1

      if (typeof this.swiperIndex == 'undefined') {
        vipId = vip_id
      } else {
        const item = this.signList[this.swiperIndex] || {}
        vipId = item.vip_id || 1
      }

      return this.getVipCurrentLevel(vipId)
    },
    // 
    isPhone() {
      const { tel = '' } = this.user
      return !!tel
    }
  },
  watch: {
    // 
    popupState(n) {
      if (!n) {
        this.state = false
        if (this.remove) this.remove()
      }
    }
  },
  methods: {
    loadIcon: key => loadIcon(key, false),
    // 
    async getInfo() {
      const params = { token: this.user.token, language: this.languageType }
      const res = await getActivitieSignList(params).catch(e => e)
      if (!res || res.code != 0) return

      this.activeInfo = res.data && this.$u.lodash.isObject(res.data) ? res.data : {}
      /**
       * 
       * configs 
       * list 
       * vip_id vip
       * sign:0  sign:1  sign:2 
       */
      const { configs = [], list = [], vip_id = 1 } = this.activeInfo
      this.signList = configs.map(item => {
        const { configs = [], ...rest } = item
        const { vip_id: vipId = 1 } = rest
        let setList = configs.filter(f => f.type != 1)
        setList = setList.map((f, i) => {
          return {
            ...f,
            ...(vip_id == vipId ? list[i] : { sign: 2 }),
            day: setList.length == i + 1 ? `${Number(f.day) + 1}+` : Number(f.day) + 1
          }
        })
        return { ...rest, list: setList }
      })
    },
    // 
    show() {
      const { vip_id = 1 } = this.user
      // 
      this.swiperOptions = {
        slidesPerView: 'auto',
        loop: false,
        autoplay: false,
        centeredSlides: true,
        followFinger: false,
        resistanceRatio: 0.1,
        spaceBetween: 5,
        initialSlide: Number(vip_id) - 1,
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        },
        on: {
          slideChange: () => {
            const swiper = this.$refs['vipSignSwiper']?.$swiper
            this.swiperIndex = swiper?.realIndex
          }
        }
      }

      this.state = !this.state
      this.getInfo()

      this.$nextTick(() => {
        const vipSignRef = this.$refs['vipSignRef']
        if (vipSignRef) this.$anim.insert(vipSignRef)
      })
    },
    // 
    hide(callback) {
      this.$nextTick(async () => {
        const vipSignRef = this.$refs['vipSignRef']
        if (vipSignRef) this.$anim.close(vipSignRef)

        setTimeout(() => {
          this.state = false
          if (this.remove) this.remove()
          if (typeof callback == 'function') callback()
        }, POP_TIME)
      })
    },
    // 
    onClose() {
      this.hide(() => {
        this.onCancel()
      })
    },
    // 
    onHelper() {
      this.flipOver = 1
      setTimeout(() => {
        this.isChest = false

        // 
        this.$nextTick(() => {
          const vipSignHelpRef = this.$refs['vipSignHelpRef']
          if (!vipSignHelpRef) return

          const help_h = vipSignHelpRef.clientHeight || vipSignHelpRef.offsetHeight
          const help_scroll_h = vipSignHelpRef.scrollHeight
          this.isScrollBtn = help_scroll_h > help_h
        })
      }, 150)
    },
    // 
    onChest() {
      this.flipOver = 2
      setTimeout(() => {
        this.isChest = true
      }, 150)
    },
    // 
    onArrow() {
      this.$nextTick(() => {
        const vipSignHelpRef = this.$refs['vipSignHelpRef']
        if (!vipSignHelpRef) return

        vipSignHelpRef.scroll({ top: vipSignHelpRef.scrollTop + 50, behavior: 'smooth' })
      })
    },
    /**
     * 
     * @param item
     * sign:0  sign:1  sign:2 
     * bind_tel_switch  0  1 
     * bind_tel_desc 
     */
    async onReceive(item = {}) {
      const { sign = null } = item
      if (sign != 0) return

      const { bind_tel_switch = 0, bind_tel_desc = '' } = this.activeInfo
      if (bind_tel_switch == 1 && !this.isPhone) return this.$pop.message().show({ content: bind_tel_desc })

      const params = { token: this.user.token, language: this.languageType }
      const res = await getActivitieSignSubmit(params).catch(e => e)
      if (!res || res.code != 0) return this.$pop.message().show({ content: res && res.msg })

      item.sign = 1

      const resData = res.data && this.$u.lodash.isObject(res.data) ? res.data : {}
      // 
      this.$store.dispatch('base/showRedDotReceivedFun', {
        version: 2,
        event: 'sign_reward',
        language: this.languageType,
        token: this.user.token
      })
      // 
      this.$pop
        .deposit({
          title: this.$t('UI_Code_Bonus'),
          value: resData.gold || 0
        })
        .show()
    },
    // 
    onRecharge() {
      this.hide(() => {
        this.$store.dispatch('modal/setDepositWithdrawShow')
      })
    }
  }
}
</script>

