<template>
  <div v-zinx v-if="state" class="comp mainten">
    <div class="wrapper-mask" @click="onClose"></div>
    <div class="mainten_box_comp" :style="`top: ${top};`">
      <div ref="maintenBoxRef" class="mainten_box">
        <!--  -->
        <div class="mainten_box_title">
          <span class="mainten_box_text">
            {{ title }}
          </span>
          <icon-font
            class="close_icon"
            name="icon-Icon_Cloose"
            :dark="commonStyle(isApp ? 'mainten_title_close_app_color' : 'mainten_title_close_pc_color')"
            :size="commonStyle(isApp ? 'mainten_title_close_app_size' : 'mainten_title_close_pc_size')"
            @click="onClose"
          />
        </div>
        <!--  -->
        <div class="mainten_box_content break_word" v-html="contentText"></div>
        <!--  -->
        <div class="submit_btn">
          <v-app light style="background: transparent">
            <v-btn class="btn btn_radius" color="t_btn_host_color" @click="onSubmit">
              {{ submitText }}
            </v-btn>
          </v-app>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { getEeplace } from '@/utils/helper'
import { POP_TIME } from '@/api/setting'
import _common from '@style/components/dialog.modules.scss'

export default {
  props: {
    top: {
      type: [Number, String],
      default: '50%'
    },
    title: {
      type: String,
      default: ''
    },
    content: {
      type: String,
      default: ''
    },
    submitText: {
      type: String,
      default: ''
    },
    success: {
      type: Function,
      default() {
        return () => {}
      }
    },
    error: {
      type: Function,
      default() {
        return () => {}
      }
    }
  },
  data() {
    return {
      state: false
    }
  },
  computed: {
    ...mapState('base', ['isApp']),
    ...mapState('dialog', ['popupState']),
    // 
    contentText() {
      return this.content ? getEeplace(this.content) : ''
    }
  },
  watch: {
    // 
    popupState(n) {
      if (!n) {
        this.state = false
        if (this.remove) this.remove()
      }
    }
  },
  methods: {
    commonStyle: (k = '') => _common[k],
    show() {
      this.state = !this.state

      this.$nextTick(() => {
        const maintenBoxRef = this.$refs['maintenBoxRef']
        if (maintenBoxRef) this.$anim.insert(maintenBoxRef)
      })
    },
    hide(callback) {
      this.$nextTick(() => {
        const maintenBoxRef = this.$refs['maintenBoxRef']
        if (maintenBoxRef) this.$anim.close(maintenBoxRef)

        setTimeout(() => {
          this.state = false
          if (this.remove) this.remove()
          if (typeof callback == 'function') callback()
        }, POP_TIME)
      })
    },
    // 
    onSubmit() {
      this.hide(() => {
        this.success()
      })
    },
    // 
    onClose() {
      this.hide(() => {
        this.error()
      })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/dialog.modules.scss';

// 
.mainten_box_comp {
  width: $mainten_app_width;
  position: fixed;
  left: $mainten_app_left;
  transform: $mainten_app_transform;
  .mainten_box {
    width: $mainten_box_app_width;
    background: $mainten_box_app_bg;
    border-radius: $mainten_box_app_radius;
    overflow: hidden;
    .mainten_box_title {
      width: $mainten_box_title_app_width;
      padding: $mainten_box_title_app_padding;
      font-size: $mainten_box_title_app_size;
      font-weight: $mainten_box_title_app_weight;
      color: $mainten_box_title_app_color;
      background: $mainten_box_title_app_bg;
      position: relative;
      @include setBox(row, center, center);
      .close_icon {
        position: absolute;
        right: $mainten_box_title_icon_app_right;
        top: $mainten_box_title_icon_app_top;
        transform: $mainten_box_title_icon_app_transform;
      }
    }
    .mainten_box_content {
      width: $mainten_box_content_app_width;
      padding: $mainten_box_content_app_padding;
      margin: $mainten_box_content_app_margin;
      font-size: $mainten_box_content_app_size;
      font-weight: $mainten_box_content_app_weight;
      color: $mainten_box_content_app_color;
      line-height: $mainten_box_content_app_line;
      text-align: $mainten_box_content_app_align;
    }
    .submit_btn {
      width: $mainten_box_btn_app_width;
      padding: $mainten_box_btn_app_padding;
      margin: $mainten_box_btn_app_margin;
    }
  }
}
// pc
@include breakpoint-only-up() {
  .mainten_box_comp {
    width: $mainten_pc_width;
    position: fixed;
    left: $mainten_pc_left;
    transform: $mainten_pc_transform;
    .mainten_box {
      width: $mainten_box_pc_width;
      background: $mainten_box_pc_bg;
      border-radius: $mainten_box_pc_radius;
      overflow: hidden;
      .mainten_box_title {
        width: $mainten_box_title_pc_width;
        padding: $mainten_box_title_pc_padding;
        font-size: $mainten_box_title_pc_size;
        font-weight: $mainten_box_title_pc_weight;
        color: $mainten_box_title_pc_color;
        background: $mainten_box_title_pc_bg;
        position: relative;
        @include setBox(row, center, center);
        .close_icon {
          position: absolute;
          right: $mainten_box_title_icon_pc_right;
          top: $mainten_box_title_icon_pc_top;
          transform: $mainten_box_title_icon_pc_transform;
        }
      }
      .mainten_box_content {
        width: $mainten_box_content_pc_width;
        padding: $mainten_box_content_pc_padding;
        margin: $mainten_box_content_pc_margin;
        font-size: $mainten_box_content_pc_size;
        font-weight: $mainten_box_content_pc_weight;
        color: $mainten_box_content_pc_color;
        line-height: $mainten_box_content_pc_line;
        text-align: $mainten_box_content_pc_align;
      }
      .submit_btn {
        width: $mainten_box_btn_pc_width;
        padding: $mainten_box_btn_pc_padding;
        margin: $mainten_box_btn_pc_margin;
      }
    }
  }
}
</style>
