<template>
  <div v-zinx v-if="state">
    <div class="wrapper-mask" @click="onClose"></div>
    <div class="disable_login_comp" :style="`top: ${top};`">
      <div ref="disableLoginRef" class="disable_login_box">
        <div class="disable_login_title">
          <div class="lt">
            <icon-font
              class="lt_icon"
              name="icon-icon_IP_ban"
              :size="commonStyle('disable_login_title_back_size')"
              :dark="commonStyle('disable_login_title_back_color')"
            />
            <span class="lt_title text_1">
              {{ $t('UI.Resp_Dialog_Limit_Title') }}
            </span>
          </div>
          <icon-font
            class="rt"
            name="icon-Icon_Cloose"
            :size="commonStyle('disable_login_title_close_size')"
            :dark="commonStyle('disable_login_title_close_color')"
            @click="hide"
          />
        </div>
        <div class="disable_login_text break_word">
          {{ $t('UI.Resp_Daily_Playtime_Text_4') }}
        </div>
        <div class="disable_login_body">
          <div class="item_limit">
            <span class="text">{{ $t('UI.Resp_Daily_Playtime_Text_5') }}</span>
            <div class="time">
              <span class="time_text">{{ formatTime }}</span>
            </div>
          </div>
        </div>
        <div class="disable_login_btn">
          <v-app light style="background: transparent">
            <template v-if="isEdit">
              <v-btn class="btn_radius" :color="commonStyle('limit_login_btn_cancel_color')" @click="onCancel">
                {{ $t('UI.Resp_Dialog_Limit_Btn_Cancel') }}
              </v-btn>
            </template>
            <v-btn v-else class="btn_radius" :color="commonStyle('disable_login_btn_close_color')" @click="onClose">
              {{ $t('UI.Resp_Dialog_Limit_Btn_Close') }}
            </v-btn>
          </v-app>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { POP_TIME } from '@/api/setting'
import loadIcon from '@/assets/icons'
import _common from '@style/components/dialog.modules.scss'
import { loadWebsocket } from '@/server/ws.js'
import { REQUEST_ENUMS } from '@/api/interface/enums.js'
import ROUTES from '@/router/routes'

export default {
  props: {
    top: {
      type: String,
      default: '50%'
    },
    // token
    currentToken: {
      type: String,
      default: ''
    },
    // 
    startH: {
      type: [Number, String],
      default: 0
    },
    // 
    startM: {
      type: [Number, String],
      default: 0
    },
    // 
    endH: {
      type: [Number, String],
      default: 0
    },
    // 
    endM: {
      type: [Number, String],
      default: 0
    },
    // 
    editTime: {
      type: [Number, String],
      default: 0
    },
    // 
    error: {
      type: Function,
      default: () => {}
    }
  },
  data() {
    return {
      state: false
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'languageType']),
    ...mapState('dialog', ['popupState']),
    // 
    formatTime() {
      return `${this.startH}:${this.startM}~${this.endH}:${this.endM}`
    },
    // 
    isEdit() {
      return !Number(this.editTime)
    }
  },
  watch: {
    // 
    popupState(n) {
      if (!n) {
        this.state = false
        if (this.remove) this.remove()
      }
    }
  },
  methods: {
    commonStyle: k => _common[k],
    loadIcon: k => loadIcon(k, false),
    // 
    show() {
      this.state = !this.state
      this.$nextTick(() => {
        const disableLoginRef = this.$refs['disableLoginRef']
        if (disableLoginRef) this.$anim.insert(disableLoginRef)
      })
    },
    // 
    hide(callback) {
      this.$nextTick(() => {
        const disableLoginRef = this.$refs['disableLoginRef']
        if (disableLoginRef) this.$anim.close(disableLoginRef)

        setTimeout(async () => {
          this.state = false
          if (this.remove) this.remove()
          if (typeof callback == 'function') callback()
        }, POP_TIME)
      })
    },
    // 
    async onCancel() {
      const params = {
        token: this.currentToken,
        language: this.languageType,
        edit_type: 'del'
      }
      const res = await this.$store.dispatch('responsible/setDayPlay', params).catch(e => e)
      if (res.code != 0) return this.$pop.message().show({ content: res.msg })

      this.$pop.message().show({ content: res.msg })
      this.hide(() => {
        if (this.error) this.error()
      })
    },
    // 
    onClose() {
      this.hide(async () => {
        this.$pop.loading().show()
        const res = await this.$store.dispatch('base/logout').catch(e => e)
        this.$pop.loading().hide()
        if (res.code == 0) {
          loadWebsocket(this)
          this.$router.push({ name: ROUTES[0].name })
          this.$store.dispatch('modal/setLoginRegistShow')
        } else if (res.code != REQUEST_ENUMS.TOKENINVALID) {
          location.reload()
        }
      })
    }
  }
}
</script>

