<template>
  <div v-zinx v-if="state">
    <div class="wrapper-mask"></div>
    <div class="remind_alarm_comp" :style="`top: ${top};`">
      <div ref="remindAlarmRef" class="remind_alarm_box">
        <div class="remind_alarm_title">
          <div class="lt">
            <span class="text text_1">
              {{ $t('UI.Resp_Dialog_Alarm_Title') }}
            </span>
            <div class="time">{{ remindTimeFilt(remindItem.reminder_total_time) }}</div>
          </div>
          <icon-font
            class="rt"
            name="icon-Icon_Cloose"
            :size="commonStyle('remind_alarm_title_size')"
            :dark="commonStyle('remind_alarm_title_color')"
            @click="onSubmit"
          />
        </div>
        <div class="remind_alarm_text break_word">
          {{ $t('UI.Resp_Dialog_Alarm_Prompt', { time: remindTimeFilt(remindItem.reminder_total_time) }) }}
        </div>
        <div class="remind_alarm_sign break_word">
          {{ $t('UI.Resp_Dialog_Alarm_Sign') }}
        </div>
        <img class="remind_alarm_img" :src="loadIcon('resp_dialog_remind')" alt="" />
        <div class="remind_alarm_btn">
          <v-app light style="background: transparent">
            <template v-if="isSetting">
              <v-btn class="btn_radius" :color="commonStyle('remind_alarm_btn_setting_color')" @click="onSetting">
                {{ $t('UI.Resp_Dialog_Alarm_Setting') }}
              </v-btn>
            </template>
            <v-btn
              class="btn_radius"
              :class="{ all: !isSetting }"
              :color="commonStyle('remind_alarm_btn_submit_color')"
              @click="onSubmit"
            >
              {{ $t('UI.Resp_Dialog_Alarm_Continue') }}
            </v-btn>
          </v-app>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { POP_TIME } from '@/api/setting'
import { datetime } from '@/utils/helper.js'
import loadIcon from '@/assets/icons'
import _common from '@style/components/dialog.modules.scss'

export default {
  props: {
    top: {
      type: String,
      default: '50%'
    },
    // 
    setting: {
      type: Function,
      default: () => {}
    },
    // 
    submit: {
      type: Function,
      default: () => {}
    }
  },
  data() {
    return {
      state: false
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user']),
    ...mapState('responsible', ['remindItem']),
    ...mapState('dialog', ['popupState']),
    // 
    isSetting() {
      const { edit_cd_time = 0 } = this.remindItem
      return Number(edit_cd_time) <= 0
    }
  },
  watch: {
    // 
    popupState(n) {
      if (!n) {
        this.state = false
        if (this.remove) this.remove()
      }
    }
  },
  methods: {
    commonStyle: k => _common[k],
    loadIcon: k => loadIcon(k, false),
    // 
    remindTimeFilt(seco = 0) {
      return datetime.getFormatTime(seco)
    },
    // 
    show() {
      this.state = !this.state
      this.$nextTick(() => {
        const remindAlarmRef = this.$refs['remindAlarmRef']
        if (remindAlarmRef) this.$anim.insert(remindAlarmRef)
      })
    },
    // 
    hide(callback) {
      this.$nextTick(() => {
        const remindAlarmRef = this.$refs['remindAlarmRef']
        if (remindAlarmRef) this.$anim.close(remindAlarmRef)

        setTimeout(() => {
          this.state = false
          if (this.remove) this.remove()
          if (typeof callback == 'function') callback()
        }, POP_TIME)
      })
    },
    // 
    onSetting() {
      this.hide(() => {
        if (this.setting) this.setting()
      })
    },
    // 
    onSubmit() {
      this.hide(() => {
        if (this.submit) this.submit()
      })
    }
  }
}
</script>

