<template>
  <div v-zinx v-if="state">
    <div class="wrapper-mask"></div>
    <div class="relegation_box_comp" :style="`top: ${top};`">
      <div ref="relegationRef" class="relegation_box column_flex" :style="relegationBg">
        <div :class="`relegation_title text_${titleAlign}`">
          {{ title }}
          <icon-font
            class="close_icon cursor_point"
            name="icon-Icon_Cloose"
            :dark="commonStyle('relegation_title_close_color')"
            :size="commonStyle('relegation_title_close_size')"
            @click="onBtnCancel"
          >
          </icon-font>
        </div>
        <div class="relegation_content">
          <div class="relegation_text text_center line_3">{{ content }}</div>
          <div class="relegation_img">
            <div class="relegation_img_money">
              <div class="relegation_money">
                <img :src="relegationMoney" alt="" />
              </div>
              <span class="money_text">{{ moneyText }}</span>
            </div>
            <div class="relegation_img_banus">
              <div class="relegation_banud">
                <img :src="relegationBanud" alt="" />
              </div>
              <span class="money_text">{{ banusText }}</span>
            </div>
          </div>
          <div class="relegation_btn">
            <v-app light style="background: transparent">
              <v-btn color="t_btn_host_color" class="btn_radius" @click="onBtnPrimay">
                {{ submitText }}
              </v-btn>
            </v-app>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { POP_TIME } from '@/api/setting'
import { getCacheIcon } from '@/assets/imgCache.js'
import _common from '@style/components/dialog.modules.scss'

export default {
  props: {
    top: {
      type: [Number, String],
      default: '50%'
    },
    titleAlign: {
      // 
      type: String,
      default: _common.relegation_props_title_align
    },
    title: {
      // 
      type: String,
      default: ''
    },
    content: {
      // 
      type: String,
      default: ''
    },
    moneyText: {
      // money 
      type: String,
      default: ''
    },
    banusText: {
      // banus 
      type: String,
      default: ''
    },
    submitText: {
      // 
      type: String,
      default: ''
    },
    onSubmit: {
      // 
      type: Function,
      default() {
        return () => {}
      }
    },
    onCancel: {
      // 
      type: Function,
      default() {
        return () => {}
      }
    }
  },
  data() {
    return {
      state: false
    }
  },
  computed: {
    ...mapState('dialog', ['popupState']),
    ...mapState('base', ['isApp']),
    // 
    relegationMoney() {
      const result = getCacheIcon('relegation_money')
      return typeof result === 'string' ? result : result.value.src
    },
    // 
    relegationBanud() {
      const result = getCacheIcon('relegation_banud')
      return typeof result === 'string' ? result : result.value.src
    },
    // 
    relegationBg() {
      const result = getCacheIcon('relegation_bg')
      if (result) {
        const src = typeof result === 'string' ? result : result.value.src

        return {
          background: `url(${src})`,
          backgroundSize: '100% 100%',
          backgroundRepeat: 'no-repeat'
        }
      }

      return {}
    }
  },
  watch: {
    // 
    popupState(n) {
      if (!n) {
        this.state = false
        if (this.remove) this.remove()
      }
    }
  },
  methods: {
    commonStyle: (k = '') => _common[k],
    // 
    show() {
      this.state = !this.state
      this.$nextTick(() => {
        const relegationRef = this.$refs['relegationRef']
        if (relegationRef) this.$anim.insert(relegationRef)
      })
    },
    // /
    hide(callback) {
      this.$nextTick(() => {
        const relegationRef = this.$refs['relegationRef']
        if (relegationRef) this.$anim.close(relegationRef)

        setTimeout(() => {
          this.state = false
          if (this.remove) this.remove()
          if (typeof callback == 'function') callback()
        }, POP_TIME)
      })
    },
    // 
    onBtnPrimay() {
      this.hide(() => {
        this.onSubmit()
      })
    },
    // 
    onBtnCancel() {
      this.hide(() => {
        this.onCancel()
      })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/dialog.modules.scss';

.relegation_box_comp {
  width: $relegation_app_width;
  position: fixed;
  left: $relegation_app_fixed_left;
  transform: translateY(-50%);
  .relegation_box {
    position: relative;
    background: $relegation_bg;
    border-radius: $relegation_radius_app;
    overflow: hidden;
    .relegation_title {
      width: $relegation_title_width;
      font-size: $relegation_title_size;
      padding: $relegation_title_padding;
      position: relative;
      color: $relegation_title_color;
      background: $comp_dialog_head_bg;
      text-transform: uppercase;
      .close_icon {
        position: absolute;
        right: $relegation_title_close_right;
        top: $relegation_title_close_top;
        transform: translateY(-50%);
      }
    }
    .relegation_content {
      width: $relegation_content_width;
      padding: $relegation_content_padding;
      .relegation_text {
        width: $relegation_text_width;
        font-size: $relegation_text_size;
        font-weight: $relegation_text_bold;
        color: $relegation_text_color;
      }
      .relegation_img {
        margin: $relegation_img_region_margin;
        @include setBox(row, space-evenly, center);
        .relegation_img_money,
        .relegation_img_banus {
          @include setBox(column, center, center);
          .relegation_money,
          .relegation_banud {
            width: $relegation_img_region_width;
            height: $relegation_img_region_height;
            background: $relegation_img_region_bg;
            border-radius: $relegation_radius_app;
            border: 1px solid #363f49;
            @include setBox(row, center, center);
          }
          .relegation_money img {
            width: $relegation_img_lt_width;
          }
          .relegation_banud img {
            width: $relegation_img_rt_width;
          }
          .money_text {
            font-size: $relegation_img_text_size;
            margin: $relegation_img_text_margin;
            color: $relegation_img_text_color;
          }
        }
      }
      .relegation_btn {
        width: 100%;
        .btn_radius {
          width: $relegation_btn_width;
          height: $relegation_btn_height;
          font-size: $relegation_btn_size;
          font-weight: $relegation_btn_bold;
        }
      }
    }
  }
}
// pc
@include breakpoint-only-up() {
  .relegation_box_comp {
    width: $relegation_pc_width;
    position: fixed;
    left: $relegation_pc_fixed_left;
    transform: translate(-50%, -50%);
    .relegation_box {
      border-radius: $relegation_radius_pc;
      .relegation_content {
        .relegation_img {
          .relegation_img_money,
          .relegation_img_banus {
            .relegation_money,
            .relegation_banud {
              border-radius: $relegation_radius_pc;
            }
          }
        }
      }
    }
  }
}
</style>
