import Vue from 'vue'
import i18n from '@/utils/i18n'
import { lodash } from '@/utils/helper.js'

import deposit from './components/deposit.vue'
import message from './components/message.vue'
import confirm from './components/confirm.vue'
import loading from './components/loading.vue'
import perform from './components/perform.vue'
import downloadDialog from './components/download-dialog.vue'
import game from './components/game.vue'
import vipReward from './components/vip-reward.vue'
import betReward from './components/bet-reward.vue'
import relegation from './components/relegation.vue'
import aviso from './components/aviso.vue'
import notices from './components/notices.vue'
import kickOut from './components/kick-out.vue'
import kefu from './kefu'
import guideMask from './components/guide-mask.vue'
import version from './components/version.vue'
import referReward from './components/refer-reward.vue'
import birthday from './components/birthday.vue'
import redeem from './components/redeem.vue'
import mainten from './components/mainten.vue'
import remind from './components/withdraw-remind.vue'
import responRemind from './components/respon-remind.vue'
import responLimit from './components/respon-limit.vue'
import responLossLimit from './components/respon-loss-limit.vue'
import responDailyPlay from './components/respon-daily-play.vue'
import disableLogin from './components/disable-login.vue'
import remindAlarm from './components/remind-alarm.vue'
import limitLogin from './components/limit-login.vue'
import luckyTurntable from './lucky-turntable'
import careerRecharge from './components/career-recharge.vue'
import inviteGift from './components/invite-gift.vue'
import inviteQuest from './components/invite-quest.vue'
import imgDrag from './img-drag'
import regression from './components/regression.vue'
import guideInstall from './components/guide-install.vue'
import cpfStateInfo from './components/cpf-state-info.vue'
import secondRecharge from './components/second-recharge.vue'
import vipSign from './components/vip-sign.vue'
import screenSite from './components/screen-site.vue'
// 
let _cache_ = {}

function equipartition(comp, props) {
  if (props && lodash.isObject(props)) {
    for (const key in props) {
      comp.$props[key] = props[key]
    }
  }

  return comp
}

/**
 * 
 * @param {*} component 
 * @param {*} props 
 * @param {*} sign 
 */
const setConstructor = (store, router, vuetify, component, props, sign) => {
  if (!sign) return {}
  if (_cache_[sign]) return _cache_[sign]

  const div = document.createElement('div')
  document.body.appendChild(div)

  // 
  const compInit = new (Vue.extend(component))({
    props,
    router,
    store,
    vuetify,
    i18n
  }).$mount(div)

  const comp = equipartition(compInit, props)
  comp.state = false

  // ,
  comp.remove = function () {
    delete _cache_[sign]

    setTimeout(() => {
      this.$off && this.$off()
      this.$destroy && this.$destroy()
    }, 150)
  }

  // 
  _cache_[sign] = comp
  return comp
}

export default function (store, router, vuetify) {
  return {
    deposit: props => setConstructor(store, router, vuetify, deposit, props, 'deposit'),
    message: props => setConstructor(store, router, vuetify, message, props, 'message'),
    confirm: props => setConstructor(store, router, vuetify, confirm, props, 'confirm'),
    loading: props => setConstructor(store, router, vuetify, loading, props, 'loading'),
    perform: props => setConstructor(store, router, vuetify, perform, props, 'perform'),
    kefu: props => setConstructor(store, router, vuetify, kefu, props, 'kefu'),
    game: props => setConstructor(store, router, vuetify, game, props, 'game'),
    downloadDialog: props => setConstructor(store, router, vuetify, downloadDialog, props, 'downloadDialog'),
    vipReward: props => setConstructor(store, router, vuetify, vipReward, props, 'vipReward'),
    betReward: props => setConstructor(store, router, vuetify, betReward, props, 'betReward'),
    relegation: props => setConstructor(store, router, vuetify, relegation, props, 'relegation'),
    aviso: props => setConstructor(store, router, vuetify, aviso, props, 'aviso'),
    notices: props => setConstructor(store, router, vuetify, notices, props, 'notices'),
    kick: props => setConstructor(store, router, vuetify, kickOut, props, 'kickOut'),
    guide: props => setConstructor(store, router, vuetify, guideMask, props, 'guideMask'),
    version: props => setConstructor(store, router, vuetify, version, props, 'version'),
    referReward: props => setConstructor(store, router, vuetify, referReward, props, 'referReward'),
    birthday: props => setConstructor(store, router, vuetify, birthday, props, 'birthday'),
    redeem: props => setConstructor(store, router, vuetify, redeem, props, 'redeem'),
    mainten: props => setConstructor(store, router, vuetify, mainten, props, 'mainten'),
    remind: props => setConstructor(store, router, vuetify, remind, props, 'remind'),
    responRemind: props => setConstructor(store, router, vuetify, responRemind, props, 'responRemind'),
    responLimit: props => setConstructor(store, router, vuetify, responLimit, props, 'responLimit'),
    responLossLimit: props => setConstructor(store, router, vuetify, responLossLimit, props, 'responLossLimit'),
    responDailyPlay: props => setConstructor(store, router, vuetify, responDailyPlay, props, 'responDailyPlay'),
    disableLogin: props => setConstructor(store, router, vuetify, disableLogin, props, 'disableLogin'),
    remindAlarm: props => setConstructor(store, router, vuetify, remindAlarm, props, 'remindAlarm'),
    limitLogin: props => setConstructor(store, router, vuetify, limitLogin, props, 'limitLogin'),
    lucky: props => setConstructor(store, router, vuetify, luckyTurntable, props, 'luckyTurntable'),
    career: props => setConstructor(store, router, vuetify, careerRecharge, props, 'careerRecharge'),
    inviteGift: props => setConstructor(store, router, vuetify, inviteGift, props, 'inviteGift'),
    inviteQuest: props => setConstructor(store, router, vuetify, inviteQuest, props, 'inviteQuest'),
    imgDrag: props => setConstructor(store, router, vuetify, imgDrag, props, 'imgDrag'),
    regression: props => setConstructor(store, router, vuetify, regression, props, 'regression'),
    guideInstall: props => setConstructor(store, router, vuetify, guideInstall, props, 'guideInstall'),
    cpfStateInfo: props => setConstructor(store, router, vuetify, cpfStateInfo, props, 'cpfStateInfo'),
    secondRecharge: props => setConstructor(store, router, vuetify, secondRecharge, props, 'secondRecharge'),
    vipSign: props => setConstructor(store, router, vuetify, vipSign, props, 'vipSign'),
    screenSite: props => setConstructor(store, router, vuetify, screenSite, props, 'screenSite')
  }
}
