<template>
  <div v-zinx v-if="state" class="comp career_recharge">
    <div class="wrapper-mask" @click="onCancel"></div>
    <div class="career_box_comp">
      <div ref="careerRef" class="career_body">
        <div class="career_box">
          <div class="career_box_top">
            <div class="career_title break_word">{{ titleText }}</div>
            <div class="career_tip break_word">{{ signText }}</div>
            <div class="career_gold">{{ currencySymbol }}{{ gold }}</div>
          </div>
          <div class="career_field">
            <!--  -->
            <template v-if="isPhone">
              <v-text-field
                v-model="phone"
                :disabled="!!countNum"
                class="phone_field"
                type="number"
                :placeholder="$t('UI_Dialog_Career_Phone_Place')"
                clearable
                filled
                dense
                solo
              >
                <template slot="prepend-inner">
                  <v-menu
                    v-model="areaVisible"
                    offset-y
                    :nudge-top="commonStyle(isApp ? 'career_menu_nudge_top_app' : 'career_menu_nudge_top_pc')"
                    :nudge-left="commonStyle(isApp ? 'career_menu_nudge_left_app' : 'career_menu_nudge_left_pc')"
                    :disabled="!!countNum"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <div class="area_box" v-bind="attrs" v-on="on">
                        <icon-font
                          name="icon-Icon_info"
                          :size="
                            commonStyle(isApp ? 'career_menu_info_icon_size_app' : 'career_menu_info_icon_size_pc')
                          "
                          :dark="
                            commonStyle(isApp ? 'career_menu_info_icon_color_app' : 'career_menu_info_icon_color_pc')
                          "
                        />
                        <span class="area_text">{{ area ? `+${area}` : '' }}</span>
                        <icon-font
                          :name="areaVisible ? 'icon-launch' : 'icon-more'"
                          :size="
                            commonStyle(isApp ? 'career_menu_arrow_icon_size_app' : 'career_menu_arrow_icon_size_pc')
                          "
                          :dark="
                            commonStyle(isApp ? 'career_menu_arrow_icon_color_app' : 'career_menu_arrow_icon_color_pc')
                          "
                        />
                      </div>
                    </template>
                    <v-list class="comp career_area_list">
                      <v-list-item
                        class="item_area"
                        :class="{ active: item.code == area }"
                        v-for="item in phonePlaces"
                        :key="item.code"
                        @click="area = item.code"
                      >
                        <span class="lt">{{ item.nation }}</span>
                        <span class="rt">{{ `+${item.code}` }}</span>
                      </v-list-item>
                    </v-list>
                  </v-menu>
                </template>
              </v-text-field>
              <div class="line_code">
                <simple-otp-input
                  class="otp_field"
                  v-model="phoneCode"
                  :length="Number(commonStyle(isApp ? 'career_otp_length_app' : 'career_otp_length_pc'))"
                  type="number"
                  @input="onChange"
                />
                <div
                  class="otp_btn btn_radius"
                  :class="{ disabled_btn: !area || !phone || !!countNum }"
                  v-zoom
                  @click="getCode"
                >
                  {{ !!countNum ? `${countNum}s` : $t('UI.Login_0_main_10') }}
                </div>
              </div>
              <div
                class="career_bind_submit btn_radius"
                :class="{ disabled_btn: !(area && phone && phoneCode.trim()) }"
                v-zoom
                @click="onSubmit"
              >
                {{ $t('UI_MINE_vinculativo') }}
              </div>
            </template>
            <!--  -->
            <template v-if="isMail">
              <v-text-field
                v-model="mail"
                class="mail_field"
                :placeholder="$t('UI_Dialog_Career_Email_Place')"
                :disabled="!!countNum"
                clearable
                filled
                dense
                solo
              >
                <template slot="prepend-inner">
                  <icon-font
                    name="icon-career_mail"
                    :size="commonStyle(isApp ? 'career_mail_icon_size_app' : 'career_mail_icon_size_pc')"
                    :dark="commonStyle(isApp ? 'career_mail_icon_color_app' : 'career_mail_icon_color_pc')"
                  />
                </template>
              </v-text-field>
              <div class="line_code">
                <v-text-field
                  v-model="mailCode"
                  class="mail_code_field"
                  type="number"
                  :placeholder="$t('UI_Dialog_Career_Email_Code_Place')"
                  clearable
                  filled
                  dense
                  solo
                >
                  <template slot="prepend-inner">
                    <icon-font
                      name="icon-Icon_shield"
                      :size="commonStyle(isApp ? 'career_mail_code_icon_size_app' : 'career_mail_code_icon_size_pc')"
                      :dark="commonStyle(isApp ? 'career_mail_code_icon_color_app' : 'career_mail_code_icon_color_pc')"
                    />
                  </template>
                </v-text-field>
                <div class="otp_btn btn_radius" :class="{ disabled_btn: !mail || !!countNum }" v-zoom @click="getCode">
                  {{ !!countNum ? `${countNum}s` : $t('UI.Login_0_main_10') }}
                </div>
              </div>
              <div
                class="career_bind_submit btn_radius"
                :class="{ disabled_btn: !(mail && mailCode) }"
                v-zoom
                @click="onSubmit"
              >
                {{ $t('UI_MINE_vinculativo') }}
              </div>
            </template>
          </div>
        </div>
        <icon-font
          class="career_close"
          name="icon-icon_pop_close"
          :size="commonStyle(isApp ? 'career_close_icon_size_app' : 'career_close_icon_size_pc')"
          :dark="commonStyle(isApp ? 'career_close_icon_color_app' : 'career_close_icon_color_pc')"
          @click="onCancel"
        />
      </div>
      <vue-recaptcha-verify
        v-model="googleVisible"
        :titleText="$t('UI.Login_Regist_Tips')"
        :contentText="$t('UI.Login_Regist_Content_Text')"
        @onSuccess="onGoogleSuccess"
        @onBack="googleVisible = false"
      />
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { POP_TIME } from '@/api/setting'
import { getCacheIcon } from '@/assets/imgCache.js'
import { getEmailcode, get_code, userBingAccount, userBingTel } from '@/api/modules/login'
import VueRecaptchaVerify from '@/components/ui/vue-recaptcha-verify/index'
import SimpleOtpInput from 'vue-simple-otp-input'
import _common from '@style/components/dialog.modules.scss'

export default {
  components: {
    VueRecaptchaVerify,
    SimpleOtpInput
  },
  props: {
    // 
    gold: {
      type: [Number, String],
      default: 0
    },
    // 
    cancel: {
      type: Function,
      default: () => {}
    },
    // 
    submit: {
      type: Function,
      default: () => {}
    }
  },
  data() {
    return {
      state: false,
      googleVisible: false, // 
      areaVisible: false, // 
      shakeCode: false, // 
      shakeBind: false, // 
      area: '', // 
      phone: '', // 
      mail: '', // 
      phoneCode: '', // 
      mailCode: '', // 
      googleCode: '', // 
      countNum: 0, // 
      verify_img_params: '' // code
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'languageType', 'deviceid', 'systemConfig']),
    ...mapState('dialog', ['popupState']),
    ...mapState('source', ['phonePlaces']),
    ...mapState('switchState', ['currencyShow']),
    // 
    currencySymbol() {
      const { currency_symbol = '' } = this.systemConfig
      return this.currencyShow ? currency_symbol : ''
    },
    // 
    isPhone() {
      const { tel = '' } = this.user
      return !tel
    },
    // 
    isMail() {
      const { account = '' } = this.user
      return !account
    },
    // 
    careerImg() {
      const result = getCacheIcon('career_bg')
      if (result) {
        const src = typeof result === 'string' ? result : result.value.src

        return {
          background: `url(${src})`,
          backgroundSize: '100% auto',
          backgroundRepeat: 'no-repeat'
        }
      }
    },
    // 
    titleText() {
      if (this.isPhone) return this.$t('UI_Dialog_Career_Phone_Title')
      if (this.isMail) return this.$t('UI_Dialog_Career_Email_Title')
      return ''
    },
    // 
    signText() {
      if (this.isPhone) return this.$t('UI_Dialog_Career_Phone_Sign')
      if (this.isMail) return this.$t('UI_Dialog_Career_Email_Sign')
      return ''
    }
  },
  watch: {
    // 
    popupState(n) {
      if (!n) {
        this.state = false
        if (this.remove) this.remove()
      }
    },
    // ,
    phonePlaces(list = []) {
      const { code = '' } = list[0] || {}
      this.area = code
    }
  },
  methods: {
    commonStyle: k => _common[k],
    // 
    show() {
      this.state = !this.state
      // 
      if (this.phonePlaces && this.phonePlaces.length > 0) {
        const { code = '' } = this.phonePlaces[0] || {}
        this.area = code
      }
      this.$nextTick(() => {
        const careerRef = this.$refs['careerRef']
        if (careerRef) {
          // input placeholder
          const otp_field = careerRef.querySelectorAll('.otp_field')[0]
          if (otp_field) {
            const otp_input = otp_field.querySelectorAll('input') || []
            otp_input.forEach(dom => {
              dom.setAttribute('placeholder', '0')
            })
          }

          this.$anim.insert(careerRef)
        }
      })
    },
    // 
    hide(callback) {
      this.$nextTick(() => {
        const careerRef = this.$refs['careerRef']
        if (careerRef) this.$anim.close(careerRef)

        setTimeout(() => {
          this.state = false
          if (this.remove) this.remove()
          if (typeof callback == 'function') callback()
        }, POP_TIME)
      })
    },
    // 
    onCancel() {
      this.hide(() => {
        if (this.cancel) this.cancel()
      })
    },
    // otp 
    onChange() {
      //  placeholder 
      this.$nextTick(() => {
        const careerRef = this.$refs['careerRef']
        const otp_field = careerRef.querySelectorAll('.otp_field')[0]
        if (otp_field) {
          const otp_input = otp_field.querySelectorAll('input') || []
          otp_input.forEach(dom => {
            dom.value = String(dom.value).trim()
          })
        }
      })
    },
    /**
     * 
     * code: 
     */
    onGoogleSuccess(code = '') {
      this.googleCode = code
      this.getCode({ sign: true })
    },
    // 
    startCountDown() {
      if (this.timer) {
        clearTimeout(this.timer)
        this.timer = null
      }

      this.timer = setTimeout(() => {
        this.countNum--
        if (this.countNum) this.startCountDown()
      }, 1 * 1000)
    },
    /**
     * 
     * sign: 
     */
    async getCode({ sign = false } = {}) {
      if (this.shakeCode) return
      if (this.isPhone && (!this.area || !this.phone || !!this.countNum)) return
      if (this.isMail && (!this.mail || !!this.countNum)) return

      let params = {}
      if (this.isPhone) {
        params = {
          type: 1,
          area: this.area,
          token: this.user?.token,
          tel: this.area + this.phone,
          device_id: this.deviceid,
          language: this.languageType
        }
      }
      if (this.isMail) {
        params = {
          type: 1,
          token: this.user?.token,
          email: this.mail,
          device_id: this.deviceid,
          language: this.languageType
        }
      }
      if (sign) {
        this.googleVisible = false
        params.recaptcha = this.googleCode
      }

      // 
      if (this.verify_img_params) params.verify_img_params = this.verify_img_params
      this.verify_img_params = ''

      this.shakeCode = true
      const getInterface = this.isPhone ? get_code : getEmailcode
      const res = await getInterface(params).catch(e => e)
      this.shakeCode = false
      if (!res || (res.code != 0 && res.code != 300 && res.code != 301))
        return this.$pop.message().show({ content: res && res.msg })
      this.$pop.message().show({ content: res && res.msg })

      // 
      if (res.code == 300) return (this.googleVisible = true)
      // code == 301 
      if (res.code == 301) {
        const { code: checkCode = '', img: baseSrc = '', url: checkSrc = '' } = res.data || {}
        const resDrag = await this.$pop
          .imgDrag({ baseSrc, checkCode, checkSrc })
          .show()
          .catch(e => e)
        if (resDrag == 'cancel') return

        const { encrypted = '' } = resDrag
        this.verify_img_params = encrypted
        this.getCode()
        return
      }

      this.countNum = Number(res.data || 0)
      this.startCountDown()
    },
    // 
    async onSubmit() {
      if (this.shakeBind) return
      if (this.isPhone && !(this.area && this.phone && this.phoneCode.trim())) return
      if (this.isMail && !(this.mail && this.mailCode)) return

      let params = {}
      // 
      if (this.isPhone) {
        params = {
          token: this.user?.token,
          tel: this.phone,
          code: this.phoneCode.trim(),
          area: this.area,
          language: this.languageType,
          device_id: this.deviceid
        }
      }
      // 
      if (this.isMail) {
        params = {
          token: this.user?.token,
          account: this.mail,
          code: this.mailCode,
          language: this.languageType,
          device_id: this.deviceid
        }
      }

      this.shakeBind = true
      const getInterface = this.isPhone ? userBingTel : userBingAccount
      const res = await getInterface(params).catch(e => e)
      this.shakeBind = false
      if (!res || res.code != 0) return this.$pop.message().show({ content: res && res.msg })

      this.$pop.message().show({ content: res && res.msg })

      this.hide(() => {
        if (this.submit) this.submit()
      })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/dialog.modules.scss';

// app
.career_box_comp {
  width: $career_app_width;
  position: $career_app_position;
  top: $career_app_top;
  left: $career_app_left;
  transform: $career_app_transform;
  user-select: none;
  .career_body {
    width: $career_body_app_width;
    padding: $career_body_app_padding;
    border-radius: $career_body_app_radius;
    background: $career_body_app_bg;
    position: $career_body_app_position;
    .career_box {
      width: $career_box_app_width;
      padding: $career_box_app_padding;
      @include setBox(column, flex-start, center);
      .career_box_top {
        width: $career_box_app_width;
        background: $career_box_app_bg;
        background-size: 100% 100%;
        background-repeat: no-repeat;
        position: $career_box_app_position;
        height: $career_box_app_height;
        .career_title {
          font-size: $career_box_title_app_size;
          font-weight: $career_box_title_app_weight;
          color: $career_box_title_app_color;
          line-height: $career_box_title_app_line;
          text-align: $career_box_title_app_align;
        }
        .career_tip {
          font-size: $career_box_tip_app_size;
          font-weight: $career_box_tip_app_weight;
          color: $career_box_tip_app_color;
          margin: $career_box_tip_app_margin;
          line-height: $career_box_tip_app_line;
          text-align: $career_box_tip_app_align;
        }
        .career_gold {
          min-width: $career_box_gold_app_min_width;
          height: $career_box_gold_app_height;
          padding: $career_box_gold_app_padding;
          background: $career_box_gold_app_bg;
          background-size: 100% 100%;
          background-repeat: no-repeat;
          position: $career_box_gold_app_position;
          top: $career_box_gold_app_top;
          right: $career_box_gold_app_right;
          font-size: $career_box_gold_app_size;
          font-weight: $career_box_gold_app_weight;
          color: $career_box_gold_app_color;
          animation-name: career-gold;
          animation-duration: 1.5s;
          animation-timing-function: linear;
          animation-iteration-count: infinite;
          animation-fill-mode: both;
          @include setBox(column, flex-start, center);
          @keyframes career-gold {
            0% {
              transform: scale(1);
            }
            50% {
              transform: scale(1.1);
            }
            100% {
              transform: scale(1);
            }
          }
        }
      }
      .career_field {
        width: $career_box_field_app_width;
        margin: $career_box_field_app_margin;
        .phone_field,
        .mail_field,
        .mail_code_field {
          width: $career_field_all_app_width;
          ::v-deep .v-input__control {
            width: $career_field_all_control_app_width;
            border-radius: $career_field_all_control_app_radius;
            .v-input__prepend-inner {
              .area_box {
                @include setBox(row, flex-start, center);
                .area_text {
                  margin: $career_field_all_control_area_app_margin;
                  font-size: $career_field_all_control_area_app_size;
                  font-weight: $career_field_all_control_area_app_weight;
                  color: $career_field_all_control_area_app_color;
                }
              }
            }
            .v-input__slot {
              width: $career_field_all_control_slot_app_width;
              height: $career_field_all_control_slot_app_height;
              box-shadow: $career_field_all_control_slot_app_shadow;
              margin: $career_field_all_control_slot_app_margin;
              padding: $career_field_all_control_slot_app_padding;
              background: $career_field_all_control_slot_app_bg;
              @include setBox(row, flex-start, center);
              .v-input__prepend-inner {
                margin: $career_field_all_control_slot_prepend_app_margin;
                line-height: $career_field_all_control_slot_prepend_app_line;
              }
              .v-text-field__slot {
                input {
                  padding: $career_field_all_control_slot_field_app_padding;
                  font-size: $career_field_all_control_slot_field_app_size;
                  font-weight: $career_field_all_control_slot_field_app_weight;
                  color: $career_field_all_control_slot_field_app_color;
                  line-height: $career_field_all_control_slot_field_app_line;
                }
                input::placeholder {
                  font-size: $career_field_all_control_slot_field_place_app_size;
                  font-weight: $career_field_all_control_slot_field_place_app_weight;
                  color: $career_field_all_control_slot_field_place_app_color;
                  line-height: $career_field_all_control_slot_field_place_app_line;
                }
              }
              .v-input__append-inner {
                button {
                  color: $career_field_all_control_append_app_color;
                  font-size: $career_field_all_control_append_app_size;
                }
              }
            }
            .v-text-field__details {
              display: none;
            }
          }
        }
        .line_code {
          width: $career_box_line_code_app_width;
          margin: $career_box_line_code_app_margin;
          @include setBox(row, center, center);
          .otp_field {
            flex-shrink: 1;
            flex-grow: 1;
            margin: 0;
            @include setBox(row, flex-start, center);
            ::v-deep .single-input-container {
              width: $career_box_line_code_otp_single_app_width;
              height: $career_box_line_code_otp_single_app_height;
              margin: $career_box_line_code_otp_single_app_right;
              border-radius: $career_box_line_code_otp_single_app_radius;
              background: $career_box_line_code_otp_single_app_bg;
              input {
                width: $career_box_line_code_otp_single_input_app_width;
                height: $career_box_line_code_otp_single_input_app_height;
                font-size: $career_box_line_code_otp_single_input_app_size;
                font-weight: $career_box_line_code_otp_single_input_app_weight;
                color: $career_box_line_code_otp_single_input_app_color;
                text-align: $career_box_line_code_otp_single_input_app_align;
                user-select: none;
                outline: none;
                &::placeholder {
                  font-size: $career_box_line_code_otp_single_input_placeh_app_size;
                  font-weight: $career_box_line_code_otp_single_input_placeh_app_weight;
                  color: $career_box_line_code_otp_single_input_placeh_app_color;
                }
                &:focus-visible {
                  outline: none;
                }
                &::selection {
                  background: transparent;
                }
              }
            }
            ::v-deep .hidden-otp-input {
              display: none;
            }
          }
          .mail_code_field {
            flex-shrink: 1;
            flex-grow: 1;
            margin: $career_box_line_code_mail_field_app_margin;
          }
          .otp_btn {
            flex-shrink: 0;
            flex-grow: 0;
            height: $career_box_line_code_otp_btn_app_height;
            padding: $career_box_line_code_otp_btn_app_padding;
            background: $career_box_line_code_otp_btn_app_bg;
            box-shadow: $career_box_line_code_otp_btn_app_shadow;
            font-size: $career_box_line_code_otp_btn_app_size;
            font-weight: $career_box_line_code_otp_btn_app_weight;
            color: $career_box_line_code_otp_btn_app_color;
            line-height: $career_box_line_code_otp_btn_app_line;
            @include setBox(row, center, center);
          }
        }
      }
      .career_bind_submit {
        width: $career_bind_submit_app_width;
        height: $career_bind_submit_app_height;
        margin: $career_bind_submit_app_margin;
        background: $career_bind_submit_app_bg;
        box-shadow: $career_bind_submit_app_shadow;
        border: $career_bind_submit_app_border;
        font-size: $career_bind_submit_app_size;
        font-weight: $career_bind_submit_app_weight;
        color: $career_bind_submit_app_color;
        line-height: $career_bind_submit_app_line;
        @include setBox(row, center, center);
      }
    }
    .career_close {
      position: $career_close_app_position;
      bottom: $career_close_app_bottom;
      left: $career_close_app_left;
      transform: $career_close_app_transform;
      cursor: pointer;
    }
  }
}
.comp.career_area_list {
  min-width: $career_area_list_min_width;
  max-height: $career_area_list_max_height;
  overflow-y: auto;
  padding: $career_area_list_padding;
  background: $career_area_list_bg;
  border-radius: $career_area_list_radius;
  .item_area {
    min-height: $career_area_list_item_min_height;
    padding: $career_area_list_item_padding;
    @include setBox(row, space-between, center);
    &:after {
      display: none;
    }
    &.active {
      background: $career_area_list_item_active_bg;
    }
    .lt {
      font-size: $career_area_list_item_lt_size;
      font-weight: $career_area_list_item_lt_weight;
      color: $career_area_list_item_lt_color;
      line-height: $career_area_list_item_lt_line;
    }
    .rt {
      font-size: $career_area_list_item_rt_size;
      font-weight: $career_area_list_item_rt_weight;
      color: $career_area_list_item_rt_color;
      line-height: $career_area_list_item_rt_line;
    }
  }
}

// pc
@include breakpoint-only-up() {
  .career_box_comp {
    width: $career_pc_width;
    position: $career_pc_position;
    top: $career_pc_top;
    left: $career_pc_left;
    transform: $career_pc_transform;
    user-select: none;
    .career_body {
      width: $career_body_pc_width;
      padding: $career_body_pc_padding;
      border-radius: $career_body_pc_radius;
      background: $career_body_pc_bg;
      position: $career_body_pc_position;
      .career_box {
        width: $career_box_pc_width;
        padding: $career_box_pc_padding;
        background: $career_box_pc_bg;
        background-size: 100% auto;
        background-repeat: no-repeat;
        position: $career_box_pc_position;
        @include setBox(column, flex-start, center);
        .career_title {
          font-size: $career_box_title_pc_size;
          font-weight: $career_box_title_pc_weight;
          color: $career_box_title_pc_color;
          line-height: $career_box_title_pc_line;
          text-align: $career_box_title_pc_align;
        }
        .career_tip {
          font-size: $career_box_tip_pc_size;
          font-weight: $career_box_tip_pc_weight;
          color: $career_box_tip_pc_color;
          margin: $career_box_tip_pc_margin;
          line-height: $career_box_tip_pc_line;
          text-align: $career_box_tip_pc_align;
        }
        .career_gold {
          min-width: $career_box_gold_pc_min_width;
          height: $career_box_gold_pc_height;
          padding: $career_box_gold_pc_padding;
          background: $career_box_gold_pc_bg;
          background-size: 100% 100%;
          background-repeat: no-repeat;
          position: $career_box_gold_pc_position;
          top: $career_box_gold_pc_top;
          right: $career_box_gold_pc_right;
          font-size: $career_box_gold_pc_size;
          font-weight: $career_box_gold_pc_weight;
          color: $career_box_gold_pc_color;
          @include setBox(column, flex-start, center);
          animation-name: career-gold;
          animation-duration: 1.5s;
          animation-timing-function: linear;
          animation-iteration-count: infinite;
          animation-fill-mode: both;
          @keyframes career-gold {
            0% {
              transform: scale(1);
            }
            50% {
              transform: scale(1.1);
            }
            100% {
              transform: scale(1);
            }
          }
        }
        .career_field {
          width: $career_box_field_pc_width;
          margin: $career_box_field_pc_margin;
          .phone_field,
          .mail_field,
          .mail_code_field {
            width: $career_field_all_pc_width;
            ::v-deep .v-input__control {
              width: $career_field_all_control_pc_width;
              border-radius: $career_field_all_control_pc_radius;
              .v-input__prepend-inner {
                .area_box {
                  @include setBox(row, flex-start, center);
                  .area_text {
                    margin: $career_field_all_control_area_pc_margin;
                    font-size: $career_field_all_control_area_pc_size;
                    font-weight: $career_field_all_control_area_pc_weight;
                    color: $career_field_all_control_area_pc_color;
                  }
                }
              }
              .v-input__slot {
                width: $career_field_all_control_slot_pc_width;
                height: $career_field_all_control_slot_pc_height;
                box-shadow: $career_field_all_control_slot_pc_shadow;
                margin: $career_field_all_control_slot_pc_margin;
                padding: $career_field_all_control_slot_pc_padding;
                background: $career_field_all_control_slot_pc_bg;
                @include setBox(row, flex-start, center);
                .v-input__prepend-inner {
                  margin: $career_field_all_control_slot_prepend_pc_margin;
                  line-height: $career_field_all_control_slot_prepend_pc_line;
                }
                .v-text-field__slot {
                  input {
                    padding: $career_field_all_control_slot_field_pc_padding;
                    font-size: $career_field_all_control_slot_field_pc_size;
                    font-weight: $career_field_all_control_slot_field_pc_weight;
                    color: $career_field_all_control_slot_field_pc_color;
                    line-height: $career_field_all_control_slot_field_pc_line;
                    &::-webkit-inner-spin-button,
                    &::-webkit-outer-spin-button {
                      -moz-appearance: none;
                      -webkit-appearance: none;
                      margin: 0;
                    }
                  }
                  input::placeholder {
                    font-size: $career_field_all_control_slot_field_place_pc_size;
                    font-weight: $career_field_all_control_slot_field_place_pc_weight;
                    color: $career_field_all_control_slot_field_place_pc_color;
                    line-height: $career_field_all_control_slot_field_place_pc_line;
                  }
                }
                .v-input__append-inner {
                  button {
                    color: $career_field_all_control_pcend_pc_color;
                    font-size: $career_field_all_control_pcend_pc_size;
                  }
                }
              }
              .v-text-field__details {
                display: none;
              }
            }
          }
          .line_code {
            width: $career_box_line_code_pc_width;
            margin: $career_box_line_code_pc_margin;
            @include setBox(row, center, center);
            .otp_field {
              flex-shrink: 1;
              flex-grow: 1;
              margin: 0;
              @include setBox(row, flex-start, center);
              ::v-deep .single-input-container {
                width: $career_box_line_code_otp_single_pc_width;
                height: $career_box_line_code_otp_single_pc_height;
                margin: $career_box_line_code_otp_single_pc_right;
                border-radius: $career_box_line_code_otp_single_pc_radius;
                background: $career_box_line_code_otp_single_pc_bg;
                input {
                  width: $career_box_line_code_otp_single_input_pc_width;
                  height: $career_box_line_code_otp_single_input_pc_height;
                  font-size: $career_box_line_code_otp_single_input_pc_size;
                  font-weight: $career_box_line_code_otp_single_input_pc_weight;
                  color: $career_box_line_code_otp_single_input_pc_color;
                  text-align: $career_box_line_code_otp_single_input_pc_align;
                  user-select: none;
                  outline: none;
                  &::placeholder {
                    font-size: $career_box_line_code_otp_single_input_placeh_pc_size;
                    font-weight: $career_box_line_code_otp_single_input_placeh_pc_weight;
                    color: $career_box_line_code_otp_single_input_placeh_pc_color;
                  }
                  &:focus-visible {
                    outline: none;
                  }
                  &::selection {
                    background: transparent;
                  }
                  &::-webkit-inner-spin-button,
                  &::-webkit-outer-spin-button {
                    -moz-appearance: none;
                    -webkit-appearance: none;
                    margin: 0;
                  }
                }
              }
              ::v-deep .hidden-otp-input {
                display: none;
              }
            }
            .mail_code_field {
              flex-shrink: 1;
              flex-grow: 1;
              margin: $career_box_line_code_mail_field_pc_margin;
            }
            .otp_btn {
              flex-shrink: 0;
              flex-grow: 0;
              height: $career_box_line_code_otp_btn_pc_height;
              padding: $career_box_line_code_otp_btn_pc_padding;
              background: $career_box_line_code_otp_btn_pc_bg;
              box-shadow: $career_box_line_code_otp_btn_pc_shadow;
              font-size: $career_box_line_code_otp_btn_pc_size;
              font-weight: $career_box_line_code_otp_btn_pc_weight;
              color: $career_box_line_code_otp_btn_pc_color;
              line-height: $career_box_line_code_otp_btn_pc_line;
              @include setBox(row, center, center);
            }
          }
        }
        .career_bind_submit {
          width: $career_bind_submit_pc_width;
          height: $career_bind_submit_pc_height;
          margin: $career_bind_submit_pc_margin;
          background: $career_bind_submit_pc_bg;
          box-shadow: $career_bind_submit_pc_shadow;
          border: $career_bind_submit_pc_border;
          font-size: $career_bind_submit_pc_size;
          font-weight: $career_bind_submit_pc_weight;
          color: $career_bind_submit_pc_color;
          line-height: $career_bind_submit_pc_line;
          @include setBox(row, center, center);
        }
      }
      .career_close {
        position: $career_close_pc_position;
        bottom: $career_close_pc_bottom;
        left: $career_close_pc_left;
        transform: $career_close_pc_transform;
        cursor: pointer;
      }
    }
  }
}
</style>
