<template>
  <div v-zinx v-if="state">
    <div class="lucky_mask" @click="hide"></div>
    <div class="lucky_turntable_box" :style="`top: ${top};`">
      <div ref="luckyBoxRef" class="lucky_box">
        <template v-if="isApp">
          <div class="lucky_app animation" :style="loadIcon('game_lucky_bg_app', true)">
            <div class="lucky_record_helper">
              <div class="lt">
                <!--  -->
                <icon-font
                  name="icon-Awards"
                  :dark="colorStyle('lucky_record_app_color')"
                  :size="colorStyle('lucky_record_app_size')"
                  @click="recordShow = true"
                />
              </div>
              <div class="rt">
                <!--  -->
                <icon-font
                  name="icon-Icon_question"
                  :dark="colorStyle('lucky_helper_app_color')"
                  :size="colorStyle('lucky_helper_app_size')"
                  @click="helperShow = true"
                />
                <!--  -->
                <icon-font
                  class="close"
                  name="icon-close"
                  :dark="colorStyle('lucky_close_app_color')"
                  :size="colorStyle('lucky_close_app_size')"
                  @click="hide"
                />
              </div>
            </div>
            <!--  -->
            <img v-textimg="isApp" class="title_text" :src="loadIcon('game_lucky_text_app')" alt="" />
            <!--  -->
            <div class="comp_turntable">
              <lucky-turntable ref="turntableRef" @end="onEnd" />
            </div>
            <!--  -->
            <div class="lucky_residue_content">
              <span class="lucky_residue_time">
                {{ $t('UI.Lucky_Remaining_num', { X: config.draw_num || 0 }) }}
              </span>
              <div
                class="lucky_residue_btn btn_radius"
                :class="{ disabled_btn: !isSmoke.disabled }"
                v-zoom
                @click="onSmoke"
              >
                <icon-font
                  class="icon"
                  v-if="isSmoke.lockShow"
                  name="icon-locking"
                  :dark="colorStyle('lucky_residue_lock_app_color')"
                  :size="colorStyle('lucky_residue_lock_app_size')"
                />
                <span class="text">{{ $t('UI.Lucky_Spin') }}</span>
              </div>
              <div class="lucky_limit_time">
                <div v-if="isSmoke.textShow" class="lucky_limit_text">
                  {{ $t('UI.Lucky_Limit_Bind') }}
                </div>
                <div class="lucky_time">{{ config.start_date || '--' }} - {{ config.end_date || '--' }}</div>
              </div>
            </div>
            <!--  -->
            <transition name="record">
              <template v-if="recordShow">
                <div class="record_box">
                  <record-list @recordClose="recordShow = false" />
                </div>
              </template>
            </transition>
            <!--  -->
            <transition name="helper">
              <template v-if="helperShow">
                <div class="lucky_help_box">
                  <lucky-help :desc="config.wheel_desc" @helperClose="helperShow = false" />
                </div>
              </template>
            </transition>
          </div>
        </template>
        <template v-else>
          <div class="lucky_pc animation" :style="loadIcon('game_lucky_bg_pc', true)">
            <div class="lucky_record_helper">
              <div class="lt">
                <!--  -->
                <icon-font
                  name="icon-Awards"
                  :dark="colorStyle('lucky_record_pc_color')"
                  :size="colorStyle('lucky_record_pc_size')"
                  @click="recordShow = true"
                />
              </div>
              <div class="rt">
                <!--  -->
                <icon-font
                  name="icon-Icon_question"
                  :dark="colorStyle('lucky_helper_pc_color')"
                  :size="colorStyle('lucky_helper_pc_size')"
                  @click="helperShow = true"
                />
                <!--  -->
                <icon-font
                  class="close"
                  name="icon-close"
                  :dark="colorStyle('lucky_close_pc_color')"
                  :size="colorStyle('lucky_close_pc_size')"
                  @click="hide"
                />
              </div>
            </div>
            <div class="lucky_top">
              <!--  -->
              <div class="lt">
                <lucky-turntable ref="turntableRef" @end="onEnd" />
              </div>
              <div class="rt">
                <!--  -->
                <img v-textimg="isApp" class="title_text" :src="loadIcon('game_lucky_text_pc')" alt="" />
                <!--  -->
                <div class="lucky_residue_content">
                  <span class="lucky_residue_time">
                    {{ $t('UI.Lucky_Remaining_num', { X: config.draw_num || 0 }) }}
                  </span>
                  <div
                    class="lucky_residue_btn btn_radius"
                    :class="{ disabled_btn: !isSmoke.disabled }"
                    v-zoom
                    @click="onSmoke"
                  >
                    <icon-font
                      class="icon"
                      v-if="isSmoke.lockShow"
                      name="icon-locking"
                      :dark="colorStyle('lucky_residue_lock_pc_color')"
                      :size="colorStyle('lucky_residue_lock_pc_size')"
                    />
                    <span class="text">{{ $t('UI.Lucky_Spin') }}</span>
                  </div>
                  <div class="lucky_limit_time">
                    <div v-if="isSmoke.textShow" class="lucky_limit_text">
                      {{ $t('UI.Lucky_Limit_Bind') }}
                    </div>
                    <div class="lucky_time">{{ config.start_date || '--' }} - {{ config.end_date || '--' }}</div>
                  </div>
                </div>
              </div>
            </div>
            <!--  -->
            <transition name="record">
              <template v-if="recordShow">
                <div class="record_box">
                  <record-list @recordClose="recordShow = false" />
                </div>
              </template>
            </transition>
            <div v-if="recordShow" class="record_mask" @click="recordShow = false"></div>
            <!--  -->
            <transition name="helper">
              <template v-if="helperShow">
                <div class="lucky_help_box">
                  <lucky-help :desc="config.wheel_desc" @helperClose="helperShow = false" />
                </div>
              </template>
            </transition>
            <div v-if="helperShow" class="help_mask" @click="helperShow = false"></div>
          </div>
        </template>
      </div>
    </div>

    <!--  -->
    <lucky-reward ref="rewardRef" />
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import loadIcon from '@/assets/icons'
import luckyTurntable from './ui/lucky-turntable'
import recordList from './ui/record-list'
import luckyHelp from './ui/lucky-help'
import luckyReward from './ui/lucky-reward'
import { getLuckyConfig, getLuckyRun } from '@/api/modules/game.js'
import { luckyType } from '@/events/game'
import PublishSubscribe from 'publish-subscribe-js'
import _color from '@style/components/dialog.lucky.modules.scss'
import { POP_TIME } from '@/api/setting'

export default {
  components: {
    luckyTurntable,
    recordList,
    luckyHelp,
    luckyReward
  },
  props: {
    // 
    top: {
      type: [Number, String],
      default: '50%'
    },
    // 
    cancel: {
      type: Function,
      default() {
        return () => {}
      }
    }
  },
  data() {
    return {
      state: false,
      config: {}, // 
      smokeData: {}, // 
      isInLottery: false, // 
      recordShow: false, // 
      helperShow: false // helper
    }
  },
  computed: {
    ...mapState('base', ['user', 'systemConfig', 'isApp', 'languageType']),
    ...mapState('dialog', ['popupState']),
    ...mapGetters('base', ['isTourist']),
    // 
    isSmoke() {
      //  ---- 
      // --- 
      // draw_num 
      const { draw_num = 0 } = this.config

      return {
        lockShow: this.isTourist, // 
        textShow: this.isTourist, // 
        disabled: draw_num // 
      }
    }
  },
  directives: {
    // 
    textimg: {
      inserted: (el, { value = false }) => {
        if (!el) return
        const proportion = value ? _color.lucky_text_app_tion : _color.lucky_text_pc_tion
        const w = (el.getClientRects()[0] || {}).width
        const h = w / proportion

        el.style.width = `${w}px`
        el.style.height = `${h}px`
      }
    }
  },
  watch: {
    // 
    popupState(n) {
      if (!n) {
        this.state = false
        if (this.remove) this.remove()
      }
    },
    // pc --> 
    isApp(n) {
      this.$nextTick(() => {
        // 
        const { wheel_config = [] } = this.config
        // if (this.$refs['turntableRef']) this.$refs['turntableRef'].init({ list: wheel_config.splice(6) });
        if (this.$refs['turntableRef']) this.$refs['turntableRef'].init({ list: wheel_config })
      })
    },
    //  0 1
    isTourist: {
      handler(n = 0) {
        this.getConfig()
      },
      immediate: true
    }
  },
  methods: {
    loadIcon: (key, bool = false) => loadIcon(key, bool),
    colorStyle: k => _color[k],
    // 
    async getConfig() {
      const params = {
        token: this.user.token,
        language: this.languageType
      }
      const res = await getLuckyConfig(params).catch(e => e)
      if (!res || res.code != 0) return

      this.config = res.data && this.$u.lodash.isObject(res.data) ? res.data : {}

      this.$nextTick(() => {
        // 
        const { wheel_config = [] } = this.config
        // if (this.$refs['turntableRef']) this.$refs['turntableRef'].init({ list: wheel_config.splice(6) });
        if (this.$refs['turntableRef']) this.$refs['turntableRef'].init({ list: wheel_config })
      })
    },
    // 
    getRenew() {
      this.getConfig()
    },
    // 
    async onSmoke() {
      let loadState = true
      if (!this.isSmoke.disabled) return
      if (this.isInLottery) return

      this.isInLottery = true
      const params = {
        token: this.user.token,
        language: this.languageType
      }

      this.timer = setTimeout(() => {
        if (loadState) this.$pop.loading().show()
      }, 1000)
      const res = await getLuckyRun(params).catch(e => e)
      loadState = false
      this.$pop.loading().hide()
      if (this.timer) clearTimeout(this.timer)

      if (!res || res.code != 0) {
        this.isInLottery = false
        this.$pop.message().show({ content: res && res.msg })
        return
      }

      // 
      this.$store.dispatch('base/showRedDotReceivedFun', {
        version: 2,
        event: 'red_dot_wheel_draw_num',
        token: this.user.token,
        language: this.languageType
      })

      this.$nextTick(() => {
        const resData = res.data && this.$u.lodash.isObject(res.data) ? res.data : {}
        this.smokeData = resData
        this.config.draw_num = resData.draw_num

        if (this.$refs['turntableRef']) this.$refs['turntableRef'].rotate(resData)
      })
    },
    // 
    onEnd() {
      this.isInLottery = false
      this.onReward()
    },
    // 
    onReward() {
      this.$nextTick(() => {
        if (this.$refs['rewardRef']) this.$refs['rewardRef'].open(this.smokeData)
      })
    },
    // 
    show() {
      this.state = true

      // 
      this.$nextTick(() => {
        if (this.$refs['turntableRef']) this.$refs['turntableRef'].init()
      })

      // ws
      PublishSubscribe.subscribe(luckyType.RENEW_CONFIG, this.getRenew)

      // this.$nextTick(() => {
      // 	const luckyBoxRef = this.$refs["luckyBoxRef"];
      // 	if (luckyBoxRef) this.$anim.insert(luckyBoxRef);
      // });
    },
    // 
    hide() {
      PublishSubscribe.unsubscribe(luckyType.RENEW_CONFIG)

      this.$nextTick(() => {
        const luckyBoxRef = this.$refs['luckyBoxRef']
        if (luckyBoxRef) this.$anim.close(luckyBoxRef)

        setTimeout(() => {
          this.state = false
          if (this.remove) this.remove()
          if (typeof this.cancel == 'function') this.cancel()
        }, POP_TIME)
      })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/dialog.lucky.modules.scss';

// 
.lucky_mask {
  position: $lucky_mask_position;
  top: $lucky_mask_top;
  right: $lucky_mask_right;
  left: $lucky_mask_left;
  bottom: $lucky_mask_bottom;
  background-color: $lucky_mask_bg;
}
.lucky_turntable_box {
  width: $lucky_turntable_box_app_width;
  position: $lucky_turntable_box_app_position;
  left: $lucky_turntable_box_app_left;
  transform: $lucky_turntable_box_app_transform;
  .lucky_box {
    width: $lucky_box_app_width;
    user-select: none;
    .lucky_app {
      width: $lucky_app_width;
      padding: $lucky_app_padding;
      background-size: $lucky_bg_app_size;
