        // 
        animation-name: animation-bg-in;
        animation-duration: 0.4s;
        animation-timing-function: linear;
        animation-iteration-count: 1;
        @keyframes animation-bg-in {
          0% {
            opacity: 0;
          }
          100% {
            opacity: 1;
          }
        }
        // 
        .comp_turntable {
          animation-name: lucky-turntable-in;
          animation-duration: 0.4s;
          animation-timing-function: linear;
          animation-iteration-count: 1;
        }
        @keyframes lucky-turntable-in {
          0% {
            position: relative;
            left: -110%;
            transform: rotate(-120deg);
          }
          100% {
            position: relative;
            left: 0;
            transform: rotate(0);
          }
        }
        // title
        .title_text,
        .lucky_residue_content {
          animation-name: animation-other-in;
          animation-duration: 0.4s;
          animation-timing-function: linear;
          animation-iteration-count: 1;
        }
        @keyframes animation-other-in {
          0% {
            position: relative;
            right: -110%;
          }
          100% {
            position: relative;
            right: 0;
          }
        }
      }
      .lucky_record_helper {
        width: $lucky_record_helper_app_width;
        @include setBox(row, space-between, center);
