          font-weight: $lucky_residue_content_app_time_weight;
          color: $lucky_residue_content_app_time_color;
          line-height: $lucky_residue_content_app_time_line;
          text-align: $lucky_residue_content_app_time_align;
          background: $lucky_residue_content_app_time_bg;
          position: relative;
          &::after {
            content: '';
            display: $lucky_residue_content_time_after_app_display;
            width: $lucky_residue_content_time_after_app_width;
            height: $lucky_residue_content_time_after_app_height;
            background: $lucky_residue_content_time_after_app_bg;
            position: absolute;
            top: $lucky_residue_content_time_after_app_top;
