          }
          &::before {
            content: '';
            display: $lucky_residue_content_time_before_app_display;
            width: $lucky_residue_content_time_before_app_width;
            height: $lucky_residue_content_time_before_app_height;
            background: $lucky_residue_content_time_before_app_bg;
            position: absolute;
            bottom: $lucky_residue_content_time_before_app_bottom;
            left: $lucky_residue_content_time_before_app_left;
          }
        }
        .lucky_residue_btn {
          min-width: $lucky_residue_content_app_btn_min_width;
          height: $lucky_residue_content_app_btn_height;
          margin: $lucky_residue_content_app_btn_margin;
          background: $lucky_residue_content_app_btn_bg;
          box-shadow: $lucky_residue_content_app_btn_shadow;
          cursor: pointer;
          position: relative;
          z-index: 8;
          @include setBox(row, center, center);
          &:hover {
            @include setFilter($lucky_residue_content_btn_hover_app_filter, $lucky_residue_content_btn_hover_app_show);
          }
          &.disabled_btn {
            opacity: $lucky_residue_content_disabled_app_opacity;
            background: $lucky_residue_content_disabled_app_bg;
            box-shadow: none;
          }
          .icon {
            margin: $lucky_residue_content_app_btn_text_margin;
          }
          .text {
            font-size: $lucky_residue_content_app_btn_size;
            font-weight: $lucky_residue_content_app_btn_weight;
            color: $lucky_residue_content_app_btn_color;
          }
        }
        .lucky_limit_time {
          width: $lucky_limit_time_app_width;
          margin: $lucky_limit_time_app_margin;
          @include setBox(column, center, center);
          .lucky_limit_text {
            width: $lucky_limit_app_width;
            font-size: $lucky_limit_app_size;
            font-weight: $lucky_limit_app_weight;
            color: $lucky_limit_app_color;
            text-align: $lucky_limit_app_align;
            line-height: $lucky_limit_app_line;
            margin: $lucky_limit_app_margin;
          }
          .lucky_time {
            width: $lucky_time_app_width;
            font-size: $lucky_time_app_size;
            font-weight: $lucky_time_app_weight;
            color: $lucky_time_app_color;
            text-align: $lucky_time_app_align;
            line-height: $lucky_time_app_line;
          }
        }
      }
      .record_box {
        position: absolute;
        left: $lucky_record_box_app_left;
        top: $lucky_record_box_app_top;
        right: $lucky_record_box_app_right;
        bottom: $lucky_record_box_app_bottom;
        z-index: 20;
        padding: $lucky_record_box_app_padding;
        background: $lucky_record_box_app_bg;
      }
      .lucky_help_box {
        position: absolute;
        left: $lucky_helper_box_app_left;
        top: $lucky_helper_box_app_top;
        right: $lucky_helper_box_app_right;
        bottom: $lucky_helper_box_app_bottom;
        z-index: 20;
        padding: $lucky_helper_box_app_padding;
        background: $lucky_helper_box_app_bg;
      }
    }
  }
}
// 
.record-enter-active,
.record-leave-active {
  transition: all 0.3s ease;
  opacity: 1;
}
.record-enter,
.record-leave-to {
  transform: translateX(-100%);
  opacity: 0;
}
// 
.helper-enter-active,
.helper-leave-active {
  transition: all 0.3s ease;
  opacity: 1;
}
.helper-enter,
.helper-leave-to {
  transform: translateX(100%);
  opacity: 0;
}

