<template>
  <div v-if="state" v-zinx>
    <div class="wrapper-mask"></div>
    <div class="comp_game_box">
      <div ref="gameBoxRef" class="game_box">
        <!--  -->
        <div v-if="isShowBackIcon" ref="gameBackRef" v-drag class="back_icon" :data-app="`${isApp}`" @click="hide">
          <!-- img -->
          <img :src="loadIcon(isActivity ? 'common_activity_back' : 'common_back', false)" />
        </div>
        <!--  -->
        <template v-if="isActivity">
          <div
            ref="gameRefreshRef"
            v-drag
            class="activity_refresh"
            :data-app="`${isApp}`"
            @mousedown="isShowIframeDiv = true"
            @mouseup="isShowIframeDiv = false"
            @click="onRefresh"
          >
            <img :src="loadIcon('common_activity_refresh', false)" />
          </div>
        </template>

        <div class="game_bg">
          <img class="load_img" :src="loadIcon('common_loading', false)" />
          <!-- iframeDiv -->
          <div v-show="isShowIframeDiv" class="iframeDiv"></div>

          <iframe
            id="gameIframeId"
            frameborder="0"
            :referrerpolicy="refer_policy"
            allowfullscreen
            webkitallowfullscreen
            mozallowfullscreen
            sandbox="allow-same-origin allow-scripts allow-popups"
          >
          </iframe>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { b64_to_str, getQueryObject } from '@/utils/helper'
import { sport_digitain_id, REFER_POLICY, POP_TIME } from '@/api/setting'
import loadIcon from '@/assets/icons'
import { hasShowGuideInstall } from '@/utils/hasShowGuideInstall'

export default {
  data() {
    return {
      state: false,
      isShowIframeDiv: false,
      isShowBackIcon: true
    }
  },
  computed: {
    ...mapState('base', [
      'isApp',
      'user',
      'systemConfig',
      'locationAuthType',
      'deviceid',
      'platform',
      'pkgName',
      'subVer',
      'mainVer'
    ]),
    ...mapState('game', ['gameItem', 'isActivity']),
    ...mapState('source', ['maintains']),
    // 
    refer_policy() {
      return REFER_POLICY ? 'no-referrer' : ''
    }
  },
  watch: {
    maintains: {
      handler(list = []) {
        const { gid = '' } = this.gameItem
        const item = list.find(f => f.gid == gid)
        if (!item) return

        // mtc_status:01
        const { mtc_status = 0, mtc_st_date = 0, mtc_end_date = 0, mtc_msg = '' } = item
        // 
        const beginTime = this.$u.getTimeStamp(new Date(), mtc_st_date)
        // 
        const endTime = this.$u.getTimeStamp(new Date(), mtc_end_date)

        // 
        if (mtc_status == 1 && beginTime && !endTime) {
          const start_time = this.$u.useTimestamp(mtc_st_date, 'yyyy-MM-dd HH:mm:ss') || ''
          const end_time = this.$u.useTimestamp(mtc_end_date, 'yyyy-MM-dd HH:mm:ss') || ''

          this.$pop
            .mainten({
              title: this.$t('UI.Mainten_Title'),
              content: this.$t('UI.Mainten_Text', { start_time, end_time }),
              submitText: this.$t('UI.Mainten_Submit'),
              success: () => {
                this.closeMiddle()
                this.$router.push({ name: 'menu' })
              }
            })
            .show()
        }
      },
      deep: true,
      immediate: true
    },
    // iframe
    state(bool = false) {
      if (bool) {
        //  iframe - message
        window.addEventListener('message', this.onIframeMessage)
      } else {
        //  iframe - message
        window.removeEventListener('message', this.onIframeMessage)
      }
    }
  },
  methods: {
    loadIcon: key => loadIcon(key, false),
    // sport_digitain link
    responseDigitainFack(url) {
      if (!url) return
      let ret = ''
      const query = getQueryObject(url)
      // base64
      ret = query.game ? b64_to_str(query.game) : url
      ret += `&user_id=${this.user.uid}`
      if (this.isApp) {
        ret += '&view=mobile'
      }
      return ret
    },
    // 
    async show(game = {}) {
      //  app
      let isDown = hasShowGuideInstall(0)
      if (isDown) {
        this.$pop.guideInstall().show()
        return
      }

      const resData = await this.$store.dispatch('game/getGemeUp', game).catch(e => e)
      if (resData == 'cancel') {
        if (this.remove) this.remove()
        return
      }

      const { html = '', base_url = '', gameGid = 0, open_mode = 0 } = resData

      /**
       * (wickets9)
       * 
       * safariwindow.open
       * open_mode  0:iframe 1:/9w/wickets9
       */
      if (open_mode == 1) {
        if (this.remove) this.remove()
        setTimeout(() => window.open(base_url || '', '_blank'))
        return
      }

      /**
       *  iframe 
       */
      this.state = true
      this.$nextTick(() => {
        this.unloadEvent()
        this.watchEvent()

        const gameIframeId = document.getElementById('gameIframeId')
        if (!gameIframeId) return

        if (html) {
          gameIframeId.setAttribute('srcdoc', html)
        } else {
          let link = base_url
          if (gameGid == sport_digitain_id) link = `./sport_iframe.html?${this.responseDigitainFack(link)}`

          gameIframeId.setAttribute('src', link)
        }

        const gameBoxRef = this.$refs['gameBoxRef']
        if (gameBoxRef) this.$anim.insert2(gameBoxRef)
      })
    },
    //  => 
    hide() {
      this.$pop
        .confirm({
          title: this.$t('General.General_13'),
          content: this.$t('UI_ExternGame'),
          confirmText: this.$t('General.General_1'),
          cancelText: this.$t('General.General_2'),
          success: this.closeMiddle
        })
        .show()
    },
    /**
     *  => 
     */
    async closeMiddle() {
      this.$nextTick(() => {
        const gameBoxRef = this.$refs['gameBoxRef']
        if (gameBoxRef) this.$anim.close2(gameBoxRef)

        this.unloadEvent()
        setTimeout(() => {
          this.state = false
          if (this.remove) this.remove()
          this.$store.dispatch('game/getGemeDown')
        }, POP_TIME)
      })
    },
    // 
    onRefresh() {
      this.$nextTick(() => {
        const gameIframeId = document.getElementById('gameIframeId')
        if (!gameIframeId) return

        const tmpUrl = gameIframeId.src
        gameIframeId.src = 'about:blank'

        const _t = setTimeout(() => {
          gameIframeId.src = tmpUrl
          clearTimeout(_t)
        }, 300)
      })
    },
    // 
    isFullscreen() {
      return (
        document.fullscreenElement ||
        document.msFullscreenElement ||
        document.mozFullScreenElement ||
        document.webkitFullscreenElement ||
        false
      )
    },
    // 
    enterFullScreen() {
      if (document.body.requestFullscreen) {
        // document.body.requestFullscreen();
      } else if (document.body.mozRequestFullScreen) {
        /* Firefox */
        document.body.mozRequestFullScreen()
        // } else if (document.body.webkitRequestFullscreen) {
        /* Chrome, Safari & Opera */
        // document.body.webkitRequestFullscreen();
      } else if (document.body.msRequestFullscreen) {
        /* IE/Edge */
        // document.body.msRequestFullscreen();
      }
    },
    // 
    exitFullScreen() {
      if (!this.isFullscreen()) return

      if (document.exitFullscreen) {
        document.exitFullscreen()
      } else if (document.mozCancelFullScreen) {
        /* Firefox */
        document.mozCancelFullScreen()
      } else if (document.webkitExitFullscreen) {
        /* Chrome, Safari and Opera */
        document.webkitExitFullscreen()
      } else if (document.msExitFullscreen) {
        /* IE/Edge */
        document.msExitFullscreen()
      }
    },
    //  iframe 
    onIframeMessage(e) {
      const data = e.data
      // 
      // if (data == 'pplive_enter_fs') {
      //     this.enterFullScreen();
      // }
      // // 
      // if (data == 'pplive_exit_fs') {
      //     this.exitFullScreen();
      // }
      //  - 
      if (typeof data == 'object' && data.event == 'APPLICATION_UNLOAD') {
        this.exitFullScreen()
      }
      if (typeof data == 'object' && data.type == 'show_activity_back') {
        this.isShowBackIcon = true
      }
      if (typeof data == 'object' && data.type == 'hide_activity_back') {
        this.isShowBackIcon = false
      }
    },
    // icon
    watchEvent() {
      this.$nextTick(() => {
        const gameBackRef = this.$refs['gameBackRef']
        if (!gameBackRef) return

        if (this.isApp) {
          gameBackRef.addEventListener('touchstart', this.onBackIconDown)
          gameBackRef.addEventListener('touchend', this.onBackIconUp)
        } else {
          gameBackRef.addEventListener('mousedown', this.onBackIconDown)
          gameBackRef.addEventListener('mouseup', this.onBackIconUp)
        }
      })
    },
    // icon
    unloadEvent() {
      this.$nextTick(() => {
        const gameBackRef = this.$refs['gameBackRef']
        if (!gameBackRef) return

        // 
        if (this.isApp) {
          gameBackRef.removeEventListener('touchstart', this.onBackIconDown)
          gameBackRef.removeEventListener('touchend', this.onBackIconUp)
        } else {
          gameBackRef.removeEventListener('mousedown', this.onBackIconDown)
          gameBackRef.removeEventListener('mouseup', this.onBackIconUp)
        }
      })
    },
    // icon
    onBackIconDown() {
      this.isShowIframeDiv = true
    },
    // icon
    onBackIconUp() {
      this.isShowIframeDiv = false
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/dialog.modules.scss';

.comp_game_box {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  bottom: constant(safe-area-inset-bottom);
  bottom: env(safe-area-inset-bottom);
  .game_box {
    width: $game_box_width;
    height: $game_box_height;
    position: relative;
    .back_icon {
      width: $game_back_width;
      position: absolute;
      top: $game_back_top;
      left: $game_back_left;
      padding: $game_back_padding;
      box-sizing: content-box;
      z-index: $game_back_zindex;
      img {
        width: $game_back_img_width;
      }
    }
    .activity_refresh {
      width: $game_refresh_width;
      position: absolute;
      top: $game_refresh_top;
      right: $game_refresh_right;
      padding: $game_refresh_padding;
      box-sizing: content-box;
      z-index: $game_refresh_zindex;
      img {
        width: $game_refresh_img_width;
      }
    }
    .game_bg {
      width: $game_width;
      height: $game_height;
      background: $game_bg;
      position: relative;
      .load_img {
        width: $game_load_img_app_width;
        position: absolute;
        left: $game_load_img_left;
        top: $game_load_img_top;
        transform: $game_load_img_transform;
        z-index: $game_load_img_zindex;
      }
      .iframeDiv {
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: 3;
      }
      #gameIframeId {
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: 2;
      }
    }
  }
}

@include breakpoint-only-up() {
  .comp_game_box {
    .game_box {
      .back_icon {
        width: $game_back_pc_width;
        padding: $game_back_pc_padding;
        cursor: pointer !important;
      }
      .activity_refresh {
        width: $game_refresh_pc_width;
        padding: $game_refresh_pc_padding;
        cursor: pointer !important;
      }
      .game_bg {
        .load_img {
          width: $game_load_img_pc_width;
        }
      }
    }
  }
}
</style>
