<template>
  <div v-zinx v-if="state" class="comp redeem">
    <div class="wrapper-mask"></div>
    <div class="redeem_box_comp" :style="{ top }">
      <div ref="redeemRef" class="redeem_box" :style="backgroundStyle">
        <div class="redeem_head">
          <div class="head_text">{{ $t('UI.Redeem_Code') }}</div>
          <icon-font
            class="head_icon"
            name="icon-Icon_Cloose"
            :size="commonStyle('redeem_dialog_icon_size')"
            @click="hide"
          />
        </div>
        <div class="redeem_content">
          <div class="redeem_content_text">{{ $t('UI.Redeem_Content') }}</div>
          <v-app style="background: transparent">
            <v-text-field
              v-model="field.value"
              class="input_radius mt-6"
              color="t_border_color"
              hide-details
              dense
              solo
              flat
              clearable
              height="42"
              :placeholder="$t('UI.Redeem_Placeholder')"
            />
            <v-btn block class="btn btn_radius mt-4" color="t_btn_host_color" height="42" @click="onSubmit">
              {{ $t('UI_Recommendar.Receber') }}
            </v-btn>
          </v-app>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { POP_TIME } from '@/api/setting'
import { getCacheIcon } from '@/assets/imgCache.js'
import { redeemOpen } from '@/api/modules/agent'
import PublishSubscribe from 'publish-subscribe-js'
import _common from '@style/components/dialog.modules.scss'

export default {
  props: {
    top: {
      type: String,
      default: '50%'
    },
    onCancel: {
      // 
      type: Function,
      default() {
        return () => {}
      }
    }
  },
  data() {
    return {
      state: false,
      field: {
        value: ''
      }
    }
  },
  computed: {
    ...mapState('base', ['user', 'languageType']),
    ...mapState('dialog', ['popupState']),
    // 
    backgroundStyle() {
      const result = getCacheIcon('redeem_bg')
      if (result) {
        const src = typeof result === 'string' ? result : result.value.src
        return {
          background: `url(${src})`,
          backgroundSize: '100% 100%',
          backgroundRepeat: 'no-repeat'
        }
      }

      return {}
    }
  },
  watch: {
    // 
    popupState(n) {
      if (!n) {
        this.state = false
        if (this.remove) this.remove()
      }
    }
  },
  methods: {
    commonStyle: (k = '') => _common[k],
    show() {
      this.state = !this.state
      this.$nextTick(() => {
        const redeemRef = this.$refs['redeemRef']
        if (redeemRef) this.$anim.insert(redeemRef)
      })
    },
    hide(callback) {
      this.$nextTick(() => {
        const redeemRef = this.$refs['redeemRef']
        if (redeemRef) this.$anim.close(redeemRef)

        setTimeout(() => {
          this.state = false
          if (this.remove) this.remove()
          if (typeof callback == 'function') callback()
        }, POP_TIME)
      })
    },
    onSubmit() {
      const value = this.field.value
      if (!value || !value.trim()) return this.$pop.message().show({ content: this.$t('UI.Redeem_Placeholder') })
      const params = {
        token: this.user.token,
        language: this.languageType,
        ckcode: value
      }
      redeemOpen(params).then(res => {
        const { code, msg, data } = res
        if (code == 0) {
          // 
          PublishSubscribe.publish(this.$store.state?.dialog?.DUALOG_MANAGER_KEY, {
            key: 'deposit',
            seat: 'first',
            params: {
              title: this.$t('UI_Code_Bonus'),
              value: data.gold
            }
          })

          this.hide()
          setTimeout(() => {
            this.$store.dispatch('base/refreshAccount')
          }, 600)
          return
        }

        this.$pop.message().show({ content: msg })
      })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/dialog.modules.scss';

.redeem_box_comp {
  position: fixed;
  left: 50%;
  transform: translate(-50%, -50%);
  .redeem_box {
    width: $redeem_dialog_width;
    height: $redeem_dialog_height;
    .redeem_head {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      height: $redeem_dialog_head_height;
      padding: $redeem_dialog_head_pa;
      .head_text {
        font-size: $redeem_dialog_title_font_size;
        font-weight: $redeem_dialog_title_font_weight;
        color: $redeem_dialog_head_color;
      }
      .head_icon {
        position: absolute;
        right: $redeem_dialog_head_icon_right;
        color: $redeem_dialog_icon_color;
        cursor: pointer;
      }
    }
    .redeem_content {
      flex: 1;
      padding: $redeem_dialog_body_pa;
      .redeem_content_text {
        font-size: $redeem_dialog_content_font_size;
        color: $redeem_dialog_content_color;
        line-height: $redeem_dialog_content_line_height;
      }
      .btn {
        font-size: $redeem_btn_size;
      }
      ::v-deep {
        $placeholder_font_size: 13px;
        .v-input__control > .v-input__slot {
          background: rgba($color: #000000, $alpha: 0.4);
        }
        .v-text-field.v-input--dense:not(.v-text-field--outlined) input {
          padding: 2px 0 4px;
        }
        input::-webkit-input-placeholder {
          font-size: $placeholder_font_size;
        }
        input::-moz-placeholder {
          /* Mozilla Firefox 19+ */
          font-size: $placeholder_font_size;
        }
        input:-moz-placeholder {
          /* Mozilla Firefox 4 to 18 */
          font-size: $placeholder_font_size;
        }

        input:-ms-input-placeholder {
          /* Internet Explorer 10-11 */
          font-size: $placeholder_font_size;
        }
      }
    }
  }
}
</style>
