<template>
  <div v-zinx v-if="state" class="comp deposit">
    <div class="wrapper-mask" @click="hide"></div>
    <div class="deposit_box_comp" :style="`top: ${top}`" @click="hide">
      <div ref="depositBoxRef" class="deposit_box">
        <div class="deposit_box_top">
          <div class="gold_title text_center line_3">{{ title }}</div>
          <icon-font name="icon-Icon_Cloose" @click="$emit('close', false)" />
        </div>
        <div class="deposit_box_body">
          <span class="gold_content" :class="isIcon ? 'icon_show' : 'icon_hide'">
            <icon-font
              v-if="isIcon"
              class="icon_close"
              name="icon-a-IconIcon_bonus"
              :dark="commonStyle('deposit_icon_color')"
              :size="commonStyle('deposit_icon_size')"
            />
            <div class="gold_content_text line_3">{{ value }}</div>
          </span>
          <div class="gold_desc">{{ $t('UI_Email_GoldReward') }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { POP_TIME } from '@/api/setting'
import _common from '@style/components/dialog.modules.scss'

export default {
  props: {
    title: {
      type: String,
      default: ''
    },
    value: {
      type: [Number, String],
      default: 0
    },
    isIcon: {
      //  icon
      type: Boolean,
      default: true
    },
    top: {
      type: [Number, String],
      default: '50%'
    },
    onCancel: {
      // 
      type: Function,
      default() {
        return () => {}
      }
    }
  },
  data() {
    return {
      state: false
    }
  },
  computed: {
    ...mapState('base', ['isApp']),
    ...mapState('dialog', ['popupState'])
  },
  watch: {
    // 
    popupState(n) {
      if (!n) {
        this.state = false
        if (this.remove) this.remove()
      }
    }
  },
  methods: {
    commonStyle: k => _common[k],
    show() {
      this.state = !this.state
      this.$nextTick(() => {
        const depositBoxRef = this.$refs['depositBoxRef']
        if (depositBoxRef) this.$anim.insert(depositBoxRef)
      })

      return Promise.resolve(this)
    },
    hide() {
      this.$nextTick(() => {
        const depositBoxRef = this.$refs['depositBoxRef']
        if (depositBoxRef) this.$anim.close(depositBoxRef)

        setTimeout(() => {
          this.state = false
          if (this.remove) this.remove()
          this.onCancel()
        }, POP_TIME)
      })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/dialog.modules.scss';

// 
.deposit_box_comp {
  width: $deposit_app_width;
  position: fixed;
  left: $deposit_app_fixed_left;
  transform: translateY(-50%);
  .deposit_box {
    background: $deposit_bg;
    padding: $deposit_padding;
    border-radius: $deposit_radius_app;
    .deposit_box_top {
      position: relative;
      padding: $deposit_top_pa;
      background: $comp_dialog_head_bg;
      .icon {
        position: absolute;
        right: $deposit_colse_right;
        top: 50%;
        color: $deposit_colse_color;
        transform: translate(0, -50%);
      }
    }
    .deposit_box_body {
      padding: $deposit_body_pa;
    }
    .gold_title {
      color: $deposit_title_color;
      font-size: $deposit_title_size;
      font-weight: $deposit_title_blod;
    }
    .gold_content {
      width: $deposit_content_width;
      margin: $deposit_content_margin;
      background: $deposit_content_bg;
      color: $deposit_content_color;
      border-radius: $deposit_radius_app;
      @include setBox(row, center, center);
      &.icon_show {
        padding: $deposit_content_icon_show_padding;
      }
      &.icon_hide {
        padding: $deposit_content_icon_hide_padding;
      }
      .icon_close {
        margin: $deposit_content_icon_margin;
      }
      .gold_content_text {
        word-break: break-all;
        font-size: $deposit_content_text_size;
        font-weight: $deposit_content_text_blod;
      }
    }
    .gold_desc {
      color: $deposit_desc_color;
      font-size: $deposit_desc_size;
      font-weight: $deposit_desc_blod;
      margin: $deposit_desc_margin;
      text-align: center;
    }
  }
}
// pc
@include breakpoint-only-up() {
  .deposit_box_comp {
    width: $deposit_pc_width;
    position: fixed;
    left: $deposit_pc_fixed_left;
    transform: translate(-50%, -50%);
    .deposit_box {
      border-radius: $deposit_radius_pc;
      .gold_content {
        border-radius: $deposit_radius_pc;
      }
    }
  }
}
</style>
