<template>
  <div v-zinx v-if="state">
    <div class="wrapper-mask" @click="hide"></div>
    <div class="limit_login_comp" :style="`top: ${top};`">
      <div ref="limitLoginRef" class="limit_login_box">
        <div class="limit_login_title">
          <div class="lt">
            <icon-font
              class="lt_icon"
              name="icon-icon_IP_ban"
              :size="commonStyle('limit_login_title_back_size')"
              :dark="commonStyle('limit_login_title_back_color')"
            />
            <span class="lt_title text_1">
              {{ $t('UI.Resp_Dialog_Limit_Title') }}
            </span>
          </div>
          <icon-font
            class="rt"
            name="icon-Icon_Cloose"
            :size="commonStyle('limit_login_title_close_size')"
            :dark="commonStyle('limit_login_title_close_color')"
            @click="hide"
          />
        </div>
        <div class="limit_login_text break_word">
          {{ $t('UI.Resp_Dialog_Limit_Content') }}
        </div>
        <div class="limit_login_body">
          <div class="item_limit">
            <span class="text">{{ $t('UI.Resp_Dialog_Limit_Start_Time') }}</span>
            <div class="time">
              <icon-font
                name="icon-icon_rili"
                :size="commonStyle('limit_login_body_item_icon_size')"
                :dark="commonStyle('limit_login_body_item_icon_color')"
              />
              <span class="time_text">{{ formatTime.start }}</span>
            </div>
          </div>
          <div class="item_limit">
            <span class="text">{{ $t('UI.Resp_Dialog_Limit_End_Time') }}</span>
            <div class="time">
              <icon-font
                name="icon-icon_rili"
                :size="commonStyle('limit_login_body_item_icon_size')"
                :dark="commonStyle('limit_login_body_item_icon_color')"
              />
              <span class="time_text">{{ formatTime.end }}</span>
            </div>
          </div>
        </div>
        <div class="limit_login_sign break_word">
          {{ $t('UI.Resp_Dialog_Limit_Sign') }}{{ $t('UI.Resp_Dialog_Limit_Sign_Two') }}
        </div>
        <div class="limit_login_btn">
          <v-app light style="background: transparent">
            <template v-if="isEdit">
              <v-btn class="btn_radius" :color="commonStyle('limit_login_btn_cancel_color')" @click="onCancel">
                {{ $t('UI.Resp_Dialog_Limit_Btn_Cancel') }}
              </v-btn>
            </template>
            <v-btn
              class="btn_radius"
              :class="{ all: !isEdit }"
              :color="commonStyle('limit_login_btn_close_color')"
              @click="onSubmit"
            >
              {{ $t('UI.Resp_Dialog_Limit_Btn_Close') }}
            </v-btn>
          </v-app>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { POP_TIME } from '@/api/setting'
import { datetime } from '@/utils/helper.js'
import loadIcon from '@/assets/icons'
import _common from '@style/components/dialog.modules.scss'

export default {
  props: {
    top: {
      type: String,
      default: '50%'
    },
    // token
    currentToken: {
      type: String,
      default: ''
    },
    // 
    startTime: {
      type: [Number, String],
      default: 0
    },
    // 
    endTime: {
      type: [Number, String],
      default: 0
    },
    // 
    editTime: {
      type: [Number, String],
      default: 0
    },
    // 
    submit: {
      type: Function,
      default: () => {}
    },
    // 
    error: {
      type: Function,
      default: () => {}
    }
  },
  data() {
    return {
      state: false
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'languageType']),
    ...mapState('dialog', ['popupState']),
    // 
    formatTime() {
      return {
        start: datetime.formatDateTime(new Date(this.startTime * 1000), 'MM/dd/yyyy HH:mm A'),
        end: datetime.formatDateTime(new Date(this.endTime * 1000), 'MM/dd/yyyy HH:mm A')
      }
    },
    // 
    isEdit() {
      return !Number(this.editTime)
    }
  },
  watch: {
    // 
    popupState(n) {
      if (!n) {
        this.state = false
        if (this.remove) this.remove()
      }
    }
  },
  methods: {
    commonStyle: k => _common[k],
    loadIcon: k => loadIcon(k, false),
    // 
    show() {
      this.state = !this.state
      this.$nextTick(() => {
        const limitLoginRef = this.$refs['limitLoginRef']
        if (limitLoginRef) this.$anim.insert(limitLoginRef)
      })
    },
    // 
    hide(callback) {
      this.$nextTick(() => {
        const limitLoginRef = this.$refs['limitLoginRef']
        if (limitLoginRef) this.$anim.close(limitLoginRef)

        setTimeout(() => {
          this.state = false
          if (this.remove) this.remove()
          if (typeof callback == 'function') callback()
        }, POP_TIME)
      })
    },
    // 
    async onCancel() {
      const params = {
        token: this.currentToken,
        language: this.languageType,
        start_time: 0,
        end_time: 0
      }
      const res = await this.$store.dispatch('responsible/setLimitLogin', params).catch(e => e)
      if (res.code != 0) return this.$pop.message().show({ content: res.msg })

      this.$pop.message().show({ content: res.msg })
      this.hide(() => {
        if (this.error) this.error()
      })
    },
    // 
    onSubmit() {
      this.hide(() => {
        if (this.submit) this.submit()
      })
    }
  }
}
</script>

