<template>
  <div v-zinx v-if="state">
    <div class="wrapper-mask"></div>
    <div class="confirm_body">
      <div ref="confirmBoxRef" class="confirm_box">
        <div class="confirm_box_title" :class="`text_${this.titleAlign}`">
          <div class="confirm_box_text text_1" :class="{ full: !closeIcon }">
            {{ title }}
          </div>
          <template v-if="closeIcon">
            <icon-font
              class="close_icon"
              name="icon-Icon_Cloose"
              :size="commonStyle(isApp ? 'confirm_close_app_size' : 'confirm_close_pc_size')"
              :dark="commonStyle(isApp ? 'confirm_close_app_color' : 'confirm_close_pc_color')"
              @click="hide"
            />
          </template>
        </div>
        <div class="confirm_box_content">
          <div class="confirm_content_text" v-html="contentText"></div>
        </div>
        <template v-if="cancelText || confirmText">
          <div class="confirm_btn" :class="{ interval: cancelText && confirmText }">
            <template v-if="cancelText">
              <v-app light class="cancet_btn" style="background: transparent">
                <v-btn
                  class="btn_radius"
                  :color="commonStyle(isApp ? 'confirm_cancel_btn_app_color' : 'confirm_cancel_btn_pc_color')"
                  @click="onCancel"
                >
                  {{ cancelText }}
                </v-btn>
              </v-app>
            </template>
            <template v-if="confirmText">
              <v-app light class="submit_btn" style="background: transparent">
                <v-btn
                  class="btn_radius"
                  :color="commonStyle(isApp ? 'confirm_submit_btn_app_color' : 'confirm_submit_btn_pc_color')"
                  @click="onSubmit"
                >
                  {{ confirmText }}
                </v-btn>
              </v-app>
            </template>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { getEeplace } from '@/utils/helper'
import { POP_TIME } from '@/api/setting'
import _common from '@style/components/dialog.modules.scss'

export default {
  props: {
    // 
    titleAlign: {
      type: String,
      default: _common.confirm_props_title_align
    },
    // 
    title: {
      type: String,
      default: ''
    },
    // icon
    closeIcon: {
      type: Boolean,
      default: true
    },
    // 
    content: {
      type: String,
      default: ''
    },
    // 
    confirmText: {
      type: String,
      default: ''
    },
    // 
    cancelText: {
      type: String,
      default: ''
    },
    isSuccessClose: {
      type: Boolean,
      default: true
    },
    // 
    success: {
      type: Function,
      default() {
        return () => {}
      }
    },
    // 
    error: {
      type: Function,
      default() {
        return () => {}
      }
    }
  },
  data() {
    return {
      state: false
    }
  },
  computed: {
    ...mapState('base', ['isApp']),
    ...mapState('dialog', ['popupState']),
    // 
    contentText() {
      return this.content ? getEeplace(this.content) : ''
    }
  },
  watch: {
    // 
    popupState(n) {
      if (!n) {
        this.state = false
        if (this.remove) this.remove()
      }
    }
  },
  methods: {
    commonStyle: k => _common[k],
    show() {
      this.state = !this.state
      this.$nextTick(() => {
        const confirmBoxRef = this.$refs['confirmBoxRef']
        if (confirmBoxRef) this.$anim.insert(confirmBoxRef)
      })

      return Promise.resolve(this)
    },
    hide(callback) {
      this.$nextTick(() => {
        const confirmBoxRef = this.$refs['confirmBoxRef']
        if (confirmBoxRef) this.$anim.close(confirmBoxRef)

        setTimeout(() => {
          this.state = false
          if (this.remove) this.remove()
          if (typeof callback == 'function') callback()
        }, POP_TIME)
      })
    },
    // 
    onSubmit() {
      if (this.success) this.success()
      if (this.isSuccessClose) this.hide()
    },
    // 
    onCancel() {
      this.hide(() => {
        this.error()
      })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/dialog.modules.scss';

// app
.confirm_body {
  width: $confirm_body_app_width;
  position: $confirm_body_app_position;
  left: $confirm_body_app_left;
  top: $confirm_body_app_top;
  transform: $confirm_body_app_transform;
  .confirm_box {
    width: $confirm_box_app_width;
    padding: $confirm_box_app_padding;
    border-radius: $confirm_box_app_radius;
    background: $confirm_box_app_bg;
    .confirm_box_title {
      width: $confirm_box_title_app_width;
      position: relative;
      @include setBox(row, center, center);
      .confirm_box_text {
        flex-shrink: 1;
        flex-grow: 1;
        padding: $confirm_box_title_text_app_padding;
        font-size: $confirm_box_title_text_app_size;
        font-weight: $confirm_box_title_text_app_weight;
        color: $confirm_box_title_text_app_color;
        line-height: $confirm_box_title_text_app_line;
        &.full {
          padding: $confirm_box_title_text_full_app_padding;
        }
      }
      .close_icon {
        flex-shrink: 0;
        flex-grow: 0;
      }
    }
    .confirm_box_content {
      width: $confirm_box_content_app_width;
      min-height: $confirm_box_content_app_min_height;
      margin: $confirm_box_content_app_margin;
      @include setBox(row, center, center);
      .confirm_content_text {
        width: $confirm_box_content_text_app_width;
        font-size: $confirm_box_content_text_app_size;
        font-weight: $confirm_box_content_text_app_weight;
        color: $confirm_box_content_text_app_color;
        line-height: $confirm_box_content_text_app_line;
        text-align: $confirm_box_content_text_app_align;
        @include setBox(column, center, center);
      }
    }
    .confirm_btn {
      width: $confirm_btn_all_app_width;
      @include setBox(row, center, center);
      &.interval {
        @include setBox(row, space-between, center);
        .cancet_btn,
        .submit_btn {
          width: $confirm_btn_app_width;
        }
      }
    }
  }
}
// pc
@include breakpoint-only-up() {
  .confirm_body {
    width: $confirm_body_pc_width;
    .confirm_box {
      padding: $confirm_box_pc_padding;
      border-radius: $confirm_box_pc_radius;
      .confirm_box_content {
        min-height: $confirm_box_content_pc_min_height;
        margin: $confirm_box_content_pc_margin;
      }
      .confirm_btn {
        &.interval {
          .cancet_btn,
          .submit_btn {
            width: $confirm_btn_pc_width;
          }
        }
      }
    }
  }
}
</style>
