<template>
  <div v-zinx v-if="state" class="comp kefu">
    <div class="wrapper-mask" @click="hide"></div>
    <template v-if="!kefuConfig || kefuConfig == 0">
      <div class="old_kefu">
        <!--  -->
        <problem-header class="wrapper_title" :isSuporte="isSuporte" @close="hide" />
        <!--  -->
        <main class="wrapper_box" :class="{ wrapper_box_isSuporte: isSuporte }">
          <!--  -->
          <template v-if="isHome">
            <problem-list :list="computedList" @touchstart="start" @touchmove="move" @click="showShowProblemDesc" />
          </template>
          <problem-desc v-if="isDesc" :list="questionList" @click="back"></problem-desc>
          <problem-suporte v-if="isSuporte" :list="kefuList"></problem-suporte>
        </main>

        <problem-footer
          v-if="isShowFooter"
          :externalShow="externalShow"
          :suporteShow="suporteShow"
          @click="showShowProblemSuporte"
          @external="externalClick"
        />
      </div>
    </template>
    <template v-else>
      <div class="new_kefu">
        <problem-iframe @close="hide" />
      </div>
    </template>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import ProblemList from './ui/problem-list'
import ProblemDesc from './ui/problem-desc'
import ProblemSuporte from './ui/problem-suporte'
import ProblemHeader from './ui/problem-header'
import ProblemFooter from './ui/problem-footer'
import problemIframe from './ui/problem-iframe.vue'
import { jump } from '@/utils/linkMap'
import { getRobotQuestionsAllType, getRobotQuestions, getKefuList } from '@/api/modules/kefu'

export default {
  components: {
    ProblemList,
    ProblemDesc,
    ProblemHeader,
    ProblemFooter,
    ProblemSuporte,
    problemIframe
  },
  data() {
    return {
      state: false,
      searchValue: '',
      list: [],
      questionList: [],
      queryParams: {
        page: 1,
        limit: 20
      },
      isEmpty: false,
      startMill: 0,
      endMill: 0,
      isHome: true,
      isDesc: false,
      isSuporte: false,
      kefuList: [],
      externalShow: false,
      suporteShow: false,
      link: null,
      isDeposit: false
    }
  },
  computed: {
    ...mapState('base', ['user', 'systemConfig', 'isApp']),
    ...mapState('dialog', ['popupState']),
    ...mapGetters('base', ['isTourist']),
    computedList() {
      return this.list.filter(e => e.name.toLocaleLowerCase().indexOf(this.searchValue) !== -1)
    },
    isShowFooter() {
      if (this.isHome) {
        return false
      } else if (this.isSuporte) {
        return false
      } else {
        return this.externalShow || this.suporteShow
      }
    },
    /**
     * 
     * ai_chat_robot_switch 0: 1: 2: 3: AI
     */
    kefuConfig() {
      const { ai_chat_robot_switch = 0 } = this.systemConfig
      return ai_chat_robot_switch
    }
  },
  watch: {
    // 
    popupState(n) {
      if (!n) {
        this.state = false
        if (this.remove) this.remove()
      }
    }
  },
  methods: {
    async getList() {
      const params = {
        token: this.$store.state.base.user.token,
        language: this.$store.state.base.languageType
      }
      let { code, data, msg } = await getRobotQuestionsAllType({
        ...params,
        ...this.queryParams
      })
      if (code == 0) {
        this.list = [...this.list, ...data]
        this.isEmpty = data.length < this.queryParams.limit
        if (this.list.length === 0) {
          this.isSuporte = true
          this.showShowProblemSuporte()
        }
      }
      if (code != 0) this.$pop.message().show({ content: msg })
    },
    // 
    show(isDeposit) {
      if (isDeposit) {
        this.isDeposit = isDeposit
      }
      // 
      if (!this.kefuConfig || this.kefuConfig == 0) {
        this.state = !this.state
        this.isHome = true
        this.getList()
        return Promise.resolve(this)
      } else {
        // 
        // 
        // if (this.isTourist) return this.$store.dispatch('modal/setLoginRegistShow')

        // 
        const { chat_robot_entry_host = '', chat_feedback_entry_host = '' } = this.systemConfig
        const chat_host = chat_robot_entry_host.trim()
        const work_order_src = chat_feedback_entry_host.trim()
        let isState = true

        if (this.kefuConfig == 3) {
          if (!chat_host) isState = false
        } else {
          if (!work_order_src) isState = false
        }

        if (!isState) return this.$pop.message().show({ content: this.$t('UI.Parameter_Error_Text') })

        this.state = !this.state
        return Promise.resolve(this)
      }
    },
    // 
    hide() {
      this.state = false
      if (this.remove) this.remove()
    },
    back() {
      this.isHome = true
      this.isDesc = false
    },
    showShowProblemDesc(type) {
      this.isHome = false
      this.getRobotQuestions(type)
    },
    showShowProblemSuporte() {
      this.isDesc = this.isHome = false
      this.getKefuList()
    },
    async getKefuList() {
      const params = {
        token: this.$store.state.base.user.token,
        language: this.$store.state.base.languageType
      }
      let { code, data, msg } = await getKefuList({
        ...params
      })
      if (code == 0) {
        this.kefuList = data
        this.isSuporte = true
      }
      if (code != 0) this.$pop.message().show({ content: msg })
    },
    async getRobotQuestions(type) {
      const params = {
        token: this.$store.state.base.user.token,
        language: this.$store.state.base.languageType,
        type,
        ver: 2
      }
      let { code, data, msg } = await getRobotQuestions({
        ...params
      })
      if (code == 0) {
        this.questionList = data.list
        this.suporteShow = data.show_service_button
        this.externalShow = data.inside_link > 0 || data.outside_link != ''
        this.link = data
        this.isDesc = true
      }
      if (code != 0) this.$pop.message().show({ content: msg })
    },
    start(e) {
      this.startMill = e.timeStamp
    },
    move(e) {
      if (e.timeStamp - this.startMill > 500) {
        this.startMill = e.startMill
        this.loadMore()
      }
    },
    loadMore() {
      if (this.isEmpty) return
      this.queryParams.page += 1
      this.getList()
    },
    filterList(v) {
      this.searchValue = v
    },
    externalClick() {
      this.$pop.kefu().hide()
      // 
      this.$store.dispatch('modal/setDepositWithdrawShow', { show: false })

      jump(this.link, this)
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/dialog.kefu.modules.scss';

// 
.old_kefu {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  box-sizing: border-box;
  background: $kefu_bg;
  border: thin solid $kefu_pc_border_color;
  @include setBox(column, flex-start, center);
  .wrapper_title {
    flex-shrink: 0;
    flex-grow: 0;
  }
  .wrapper_box {
    width: 100%;
    height: 100%;
    overflow-y: auto;
    margin: $kefu_main_margin;
    padding: $kefu_main_padding;
    & .wrapper_box_isSuporte {
      padding: 0 16px 0px 12px !important;
    }
  }
}
.new_kefu {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  box-sizing: border-box;
  @include setBox(column, flex-start, center);
}

// pc
@include breakpoint-only-up() {
  .old_kefu {
    width: 376px;
    height: 682px;
    top: unset;
    left: unset;
    right: 17px;
    bottom: 12px;
    border-radius: 8px;
    .wrapper_box {
      overflow-y: auto;
      height: 100%;
    }
  }
  .new_kefu {
    width: 376px;
    height: 682px;
    top: unset;
    left: unset;
    right: 17px;
    bottom: 12px;
    border-radius: 8px;
    border: thin solid $kefu_pc_border_color;
  }
}
</style>
