<template>
  <div v-zinx v-if="state">
    <div class="wrapper-mask" @click="hide"></div>
    <div class="limit_resp_comp" :style="`top: ${top};`">
      <div ref="limitRespRef" class="limit_resp_box">
        <!--  -->
        <template v-if="!activeTime.length">
          <div class="limit_one_title">
            <div class="lt text_1">{{ $t('UI.Resp_Limit_Title') }}</div>
            <icon-font
              class="rt"
              name="icon-Icon_Cloose"
              :size="commonStyle('limit_one_title_icon_size')"
              :dark="commonStyle('limit_one_title_icon_color')"
              @click="hide"
            />
          </div>
          <div class="limit_one_text break_word">
            {{ $t('UI.Resp_Limit_Content') }}
            <br />
            {{ $t('UI.Resp_Limit_Content_1') }}
          </div>
          <div class="limit_one_list">
            <div
              class="limit_item"
              :class="{ active: activeIndex == i }"
              v-for="(item, i) in limitTimeArr"
              :key="i"
              @click="onChangeDate({ sign: 'regular', index: i })"
            >
              <span class="text">{{ limitTimeFilt(item) }}</span>
              <icon-font
                v-if="activeIndex == i"
                class="icon"
                name="icon-check_rightup"
                :size="commonStyle('limit_item_active_icon_size')"
              />
            </div>
            <div class="limit_item_indefinite" :class="{ active: activeIndex == -2 }" @click="activeIndex = -2">
              <icon-font
                name="icon-wuxian"
                dark="t_text_color_2"
                class="icon_offset"
                :size="commonStyle('limit_date_picker_icon_size')"
              />
              <span class="text">{{ $t('UI.Responsible_Indefinite') }}</span>
              <icon-font
                v-if="activeIndex == -2"
                class="flag"
                name="icon-check_rightup"
                :size="commonStyle('limit_item_active_icon_size')"
              />
            </div>
            <div class="limit_item_picker" :class="{ active: activeIndex == -1 }">
              <date-picker
                size="small"
                :radius="4"
                nudge-bottom="16"
                :allowedDates="allowedDates"
                :time="defTime"
                @change="(time = []) => onChangeDate({ sign: 'change', index: -1, time })"
              >
                <div class="d-flex align-center">
                  <icon-font
                    name="icon-icon_rili"
                    dark="t_text_color_2"
                    :size="commonStyle('limit_date_picker_icon_size')"
                  />
                </div>
              </date-picker>
              <icon-font
                v-if="activeIndex == -1"
                class="icon"
                name="icon-check_rightup"
                :size="commonStyle('limit_item_active_icon_size')"
              />
            </div>
          </div>
          <div class="limit_one_btn">
            <v-app light style="background: transparent">
              <v-btn
                class="btn_radius"
                :color="commonStyle('limit_one_btn_color')"
                :class="{ disabled_btn: activeIndex == null }"
                @click="onSubmitTime"
              >
                {{ $t('UI.Resp_Limit_Continue') }}
              </v-btn>
            </v-app>
          </div>
        </template>
        <!--  -->
        <template v-if="activeTime.length">
          <div class="limit_two_title">
            <div class="lt">
              <div class="lt_back" @click="onTwoBack">
                <icon-font
                  name="icon-Icon_left"
                  :size="commonStyle('limit_two_title_back_icon_size')"
                  :dark="commonStyle('limit_two_title_back_icon_color')"
                />
              </div>
              <div class="text text_1">
                {{ $t('UI.Resp_Limit_Two_Confirmation') }}
              </div>
            </div>
            <icon-font
              class="rt"
              name="icon-Icon_Cloose"
              :size="commonStyle('limit_two_title_close_icon_size')"
              :dark="commonStyle('limit_two_title_close_icon_color')"
              @click="hide"
            />
          </div>
          <template>
            <div class="limit_two_content break_word">{{ $t('UI.Resp_Limit_Two_Content') }}</div>
            <div class="limit_two_list">
              <div class="limit_two_item">
                <span class="time_sign">{{ $t('UI.Resp_Limit_Two_Self') }}</span>
                <div class="time_icon">
                  <icon-font
                    name="icon-prohibit"
                    :size="commonStyle('limit_two_item_icon_size')"
                    :dark="commonStyle('limit_two_item_icon_color')"
                  />
                  <div v-if="activeIndex == -2" class="time_text">
                    <icon-font
                      name="icon-wuxian"
                      :size="commonStyle('limit_two_item_icon_indefinite_size')"
                      :dark="commonStyle('limit_two_list_item_icon_text_color')"
                    />
                    <span>{{ $t('UI.Responsible_Indefinite') }}</span>
                  </div>
                  <span v-else class="time_text">{{ differTime }}</span>
                </div>
              </div>
              <div class="limit_two_item">
                <span class="time_sign">{{ $t('UI.Resp_Limit_Two_Start_Time') }}</span>
                <div class="time_icon">
                  <icon-font
                    name="icon-icon_rili"
                    :size="commonStyle('limit_two_item_icon_size')"
                    :dark="commonStyle('limit_two_item_icon_color')"
                  />
                  <span class="time_text">{{ formatTime.start }}</span>
                </div>
              </div>
              <div class="limit_two_item">
                <span class="time_sign">{{ $t('UI.Resp_Limit_Two_End_Time') }}</span>
                <div class="time_icon">
                  <icon-font
                    name="icon-icon_rili"
                    :size="commonStyle('limit_two_item_icon_size')"
                    :dark="commonStyle('limit_two_item_icon_color')"
                  />
                  <div v-if="activeIndex == -2" class="time_text">
                    <icon-font
                      name="icon-wuxian"
                      :size="commonStyle('limit_two_item_icon_indefinite_size')"
                      :dark="commonStyle('limit_two_list_item_icon_text_color')"
                    />
                    <span>{{ $t('UI.Responsible_Indefinite') }}</span>
                  </div>
                  <span v-else class="time_text">{{ formatTime.end }}</span>
                </div>
              </div>
            </div>
            <div class="limit_two_sign break_word">
              {{ $t('UI.Resp_Limit_Two_Sign') }}
            </div>
            <!-- <div class="limit_two_text break_word">
							{{ $t("UI.Resp_Limit_Two_Detile", { time: limitTimeFilt(limitItem.edit_time) }) }}
						</div> -->
          </template>
          <div class="limit_two_btn">
            <!--  -->
            <v-app light style="background: transparent">
              <v-btn class="btn_radius" :color="commonStyle('limit_two_btn_cancel_color')" @click="onCancel">
                {{ $t('UI.Resp_Limit_Two_Btn_Cancel') }}
              </v-btn>
              <v-btn class="btn_radius" :color="commonStyle('limit_two_btn_confirm_color')" @click="onSubmit">
                {{ $t('UI.Resp_Limit_Two_Btn_Confirm') }}
              </v-btn>
            </v-app>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { POP_TIME } from '@/api/setting'
import { datetime } from '@/utils/helper.js'
import { loadWebsocket } from '@/server/ws.js'
import DatePicker from '@/components/ui/datepicker/date-picker'
import _common from '@style/components/dialog.modules.scss'
import { applyDisabled } from '@/api/modules/responsible.js'
import { REQUEST_ENUMS } from '@/api/interface/enums.js'

export default {
  components: {
    DatePicker
  },
  props: {
    top: {
      type: String,
      default: '50%'
    }
  },
  data() {
    return {
      state: false,
      activeIndex: null, // , -1: -2>=0: 
      waitTime: [], //  
      activeTime: [], //  
      isLastConfirm: false, // 
      defTime: []
    }
  },
  created() {
    this.getDefaultDayRange()
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'languageType']),
    ...mapState('responsible', ['limitItem']),
    ...mapState('dialog', ['popupState']),
    // 
    limitTimeArr() {
      const { config = [] } = this.limitItem
      return config
    },
    // 
    differTime() {
      const [startTime = 0, endTime = 0] = this.activeTime
      const time = datetime.getDifferTime(startTime, endTime)
      const { day = '', hh = '', mm = '' } = time

      let text = ''
      if (Number(day)) text += `${day} ${this.$t('UI.Responsible_Day')} `
      if (Number(hh)) text += `${hh} ${this.$t('UI.Responsible_Hours')} `
      if (Number(mm)) text += `${mm} ${this.$t('UI.Responsible_Min')}`

      return text
    },
    // 
    formatTime() {
      const [startTime = 0, endTime = 0] = this.activeTime

      return {
        start: datetime.formatDateTime(new Date(startTime), 'MM/dd/yyyy HH:mm A'),
        end: datetime.formatDateTime(new Date(endTime), 'MM/dd/yyyy HH:mm A')
      }
    }
  },
  watch: {
    // 
    popupState(n) {
      if (!n) {
        this.state = false
        if (this.remove) this.remove()
      }
    }
  },
  methods: {
    getDefaultDayRange() {
      const start = this.$u.getFormatDate({ date: this.getNextDayMidnight(1).getTime(), pattern: 1 })
      const end = this.$u.getFormatDate({ date: this.getNextDayMidnight(7).getTime(), pattern: 1 })
      this.defTime = [start, end]
    },
    getNextDayMidnight(day) {
      let date = new Date()
      date.setDate(date.getDate() + day)
      date.setHours(0, 0, 0, 0)
      return date
    },
    allowedDates(value) {
      const str = value.replace(/-/g, '/')
      const d = new Date(str).getTime()
      const start = this.getNextDayMidnight(0)
      const end = this.getNextDayMidnight(8)
      return start < d && d < end
    },
    commonStyle: k => _common[k],
    // 
    limitTimeFilt(seco = 0) {
      return datetime.getFormatTime(seco)
    },
    // 
    show() {
      this.state = !this.state
      this.$nextTick(() => {
        const limitRespRef = this.$refs['limitRespRef']
        if (limitRespRef) this.$anim.insert(limitRespRef)
      })
    },
    // 
    hide(callback) {
      this.$nextTick(() => {
        const limitRespRef = this.$refs['limitRespRef']
        if (limitRespRef) this.$anim.close(limitRespRef)

        setTimeout(() => {
          this.state = false
          if (this.remove) this.remove()
          if (typeof callback == 'function') callback()
        }, POP_TIME)
      })
    },
    // 
    onChangeDate({ sign = '', index = null, time = [] }) {
      switch (sign) {
        case 'regular':
          this.activeIndex = index
          const start_time = new Date().getTime()
          const end_time = start_time + this.limitTimeArr[index] * 1000

          this.waitTime = [start_time, end_time]
          break
        case 'change':
          this.activeIndex = index
          let [start = '', end = ''] = time
          start = `${start} 00:00:00`
          start = start.replace(/\-/g, '\/')
          end = `${end} 23:59:59`
          end = end.replace(/\-/g, '\/')

          this.waitTime = [new Date(start).getTime(), new Date(end).getTime()]
          this.defTime = time
          break
      }
      return
    },
    // 
    onSubmitTime() {
      if (this.activeIndex == null) return
      if (this.activeIndex !== -2) {
        const [start, end] = this.waitTime
        this.activeTime = [new Date(start).getTime(), new Date(end).getTime()]
      } else {
        this.activeTime = [new Date().getTime(), 'indefinite']
      }
    },
    // 
    onTwoBack() {
      this.activeIndex = null
      this.waitTime = []
      this.activeTime = []
    },
    // 
    onCancel() {
      this.hide()
    },
    // 
    async onSubmit() {
      if (this.activeIndex == -2) {
        // 
        const params = {
          token: this.user.token,
          language: this.languageType,
          disabled_desc: ''
        }
        const res = await applyDisabled(params).catch(e => e)
        if (!res || res.code != 0) return
        let data = JSON.parse(JSON.stringify(this.limitItem))
        data.apply_disabled = res.data?.apply_disabled || {}
        this.$store.commit('responsible/SET_LIMIT_ITEM', data)
        res.msg && this.$pop.message().show({ content: res.msg })
        res.code == 0 && this.onSubimtAfter()
      } else {
        const [start_time = 0, end_time = 0] = this.activeTime
        const params = {
          token: this.user.token,
          language: this.languageType,
          start_time: parseInt(start_time / 1000),
          end_time: parseInt(end_time / 1000)
        }
        const res = await this.$store.dispatch('responsible/setLimitLogin', params).catch(e => e)
        res.msg && this.$pop.message().show({ content: res.msg })
        res.code == 0 && this.onSubimtAfter()
      }
    },
    // 
    async onSubimtAfter() {
      this.hide(async () => {
        // activeIndex -1 
        if (this.activeIndex != -1) {
          this.$pop.loading().show()
          const res = await this.$store.dispatch('base/logout').catch(e => e)
          this.$pop.loading().hide()
          if (res.code == 0) {
            loadWebsocket(this)
            this.$router.push({ name: 'menu' })
            this.$store.dispatch('modal/setLoginRegistShow')
          } else if (res.code != REQUEST_ENUMS.TOKENINVALID) {
            location.reload()
          }
        }
      })
    }
  }
}
</script>

