<template>
  <div v-zinx v-if="state">
    <div class="wrapper-mask"></div>
    <div class="kick_box_comp" :style="`top: ${top};`">
      <div ref="kickBoxRef" class="kick_box">
        <div class="kick_box_title" :class="`text_${this.titleAlign}`">
          <span class="kick_box_text line_3">
            {{ title }}
          </span>
          <icon-font
            class="close_icon cursor_point"
            name="icon-Icon_Cloose"
            :dark="commonStyle('kick_close_color')"
            :size="commonStyle('kick_close_size')"
            @click="hide"
          >
          </icon-font>
        </div>
        <div class="kick_box_content">
          <div class="confirm_box_text text_center line_3" v-html="contentText"></div>
          <div v-if="submitText" class="submit_btn">
            <v-app light style="background: transparent">
              <v-btn class="btn btn_radius" color="t_btn_host_color" @click="onSubmit">
                {{ submitText }}
              </v-btn>
            </v-app>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { getEeplace } from '@/utils/helper'
import { POP_TIME } from '@/api/setting'
import _common from '@style/components/dialog.modules.scss'

export default {
  props: {
    top: {
      type: [Number, String],
      default: '50%'
    },
    titleAlign: {
      type: String,
      default: _common.kick_props_title_align
    },
    title: {
      type: String,
      default: ''
    },
    content: {
      type: String,
      default: ''
    },
    submitText: {
      type: String,
      default: ''
    },
    success: {
      type: Function,
      default() {
        return () => {}
      }
    }
  },
  data() {
    return {
      state: false
    }
  },
  computed: {
    ...mapState('base', ['isApp']), // icon
    ...mapState('dialog', ['popupState']),
    // 
    contentText() {
      return this.content ? getEeplace(this.content) : ''
    }
  },
  watch: {
    // 
    popupState(n) {
      if (!n) {
        this.state = false
        if (this.remove) this.remove()
      }
    }
  },
  methods: {
    commonStyle: k => _common[k],
    show() {
      this.state = !this.state
      this.$nextTick(() => {
        const kickBoxRef = this.$refs['kickBoxRef']
        if (kickBoxRef) this.$anim.insert(kickBoxRef)
      })

      return Promise.resolve(this)
    },

    hide(callback) {
      this.$nextTick(() => {
        const kickBoxRef = this.$refs['kickBoxRef']
        if (kickBoxRef) this.$anim.close(kickBoxRef)

        setTimeout(() => {
          this.state = false
          if (this.remove) this.remove()
          if (typeof callback == 'function') callback()
        }, POP_TIME)
      })
    },
    // 
    onSubmit() {
      this.hide(() => {
        this.success()
      })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/dialog.modules.scss';

// 
.kick_box_comp {
  width: $kick_app_width;
  position: fixed;
  left: $kick_app_fixed_left;
  transform: translateY(-50%);
  .kick_box {
    background: $kick_bg;
    padding: $kick_box_padding;
    border-radius: $kick_radius_app;
    @include setBox(column, flex-start, center);
    .kick_box_title {
      width: $kick_box_title_width;
      min-height: $kick_box_title_height;
      position: relative;
      padding: $kick_box_title_padding;
      background: $comp_dialog_head_bg;
      .kick_box_text {
        color: $kick_title_color;
        font-size: $kick_box_title_size;
        font-weight: $kick_box_title_bold;
        text-transform: uppercase;
      }
      .close_icon {
        position: absolute;
        right: $kick_box_title_icon_right;
        top: $kick_box_title_icon_top;
        transform: translateY(-50%);
      }
    }
    .kick_box_content {
      width: $kick_content_width;
      padding: $kick_box_content_padding;
      .confirm_box_text {
        font-size: $kick_content_size;
        font-weight: $kick_content_bold;
        margin: $kick_content_margin;
        color: $kick_content_color;
      }
      .submit_btn {
        width: $kick_btn_width;
        margin: $kick_btn_margin;
        .btn {
          width: 100%;
          height: $kick_btn_height;
          font-size: $kick_btn_size;
          font-weight: $kick_btn_bold;
        }
      }
    }
  }
}

// pc
@include breakpoint-only-up() {
  .kick_box_comp {
    width: $kick_pc_width;
    left: $kick_pc_fixed_left;
    transform: translate(-50%, -50%);
    .kick_box {
      border-radius: $kick_radius_pc;
    }
  }
}
</style>
