<template>
  <div v-zinx v-if="state">
    <div class="wrapper-mask" @click="onCancel"></div>
    <div class="daily_play_comp" :style="`top: ${top};`">
      <div ref="dailyPlayRef" class="daily_play_box">
        <!--  -->
        <template v-if="!isConfirm">
          <div class="daily_play_one_title">
            <div class="lt text_1">{{ $t('UI.Resp_Daily_Playtime') }}</div>
            <icon-font
              class="rt"
              name="icon-Icon_Cloose"
              :size="commonStyle('daily_play_title_icon_size')"
              :dark="commonStyle('daily_play_title_icon_color')"
              @click="onCancel"
            />
          </div>
          <div class="daily_play_one_text break_word">
            {{ $t('UI.Resp_Daily_Playtime_Text_1') }}
          </div>
          <div class="check_time_box">
            <time-picker
              :value="startTime"
              class="picker_one"
              :disabledHour="disabledStartHour"
              :disabledMinute="disabledStartMinute"
              @change="onChangeTime($event, 'startTime')"
            />
            <span class="connect">~</span>
            <time-picker
              :value="endTime"
              class="picker_tow"
              :disabledHour="disabledEndHour"
              :disabledMinute="disabledEndMinute"
              @change="onChangeTime($event, 'endTime')"
            />
          </div>
          <div class="daily_play_one_btn">
            <v-app light style="background: transparent">
              <v-btn
                class="btn_radius"
                :class="{ disabled_btn: !(startTime.length > 1 && endTime.length > 1) }"
                :color="commonStyle('daily_play_btn_color')"
                @click="onContinue"
              >
                {{ $t('UI.Resp_Remind_Continue') }}
              </v-btn>
            </v-app>
          </div>
        </template>
        <!--  -->
        <template v-else>
          <div class="daily_play_two_title">
            <div class="lt">
              <div class="lt_back" @click="onTwoBack">
                <icon-font
                  name="icon-Icon_left"
                  :size="commonStyle('daily_play_two_title_back_icon_size')"
                  :dark="commonStyle('daily_play_two_title_back_icon_color')"
                />
              </div>
              <div class="text text_1">{{ $t('UI.Resp_Remind_Confirmation') }}</div>
            </div>
            <icon-font
              class="rt"
              name="icon-Icon_Cloose"
              :size="commonStyle('daily_play_title_icon_size')"
              :dark="commonStyle('daily_play_title_icon_color')"
              @click="onCancel"
            />
          </div>
          <div class="daily_play_two_content">{{ $t('UI.Resp_Daily_Playtime_Text_2') }}</div>
          <div class="daily_play_two_set">
            <div class="set_row">
              <div class="label">{{ $t('UI.Resp_Daily_Playtime') }}</div>
              <div class="val">{{ rangeTime }}</div>
            </div>
          </div>
          <div class="daily_play_two_btn">
            <v-app light style="background: transparent">
              <v-btn class="btn_radius" :color="commonStyle('daily_play_two_btn_cancel_color')" @click="onCancel">
                {{ $t('UI.Resp_Remind_Cancel') }}
              </v-btn>
              <v-btn class="btn_radius" :color="commonStyle('daily_play_two_btn_submit_color')" @click="onSubmit">
                {{ $t('UI.Resp_Remind_Confirm') }}
              </v-btn>
            </v-app>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { POP_TIME } from '@/api/setting'
import _common from '@style/components/dialog.modules.scss'
import TimePicker from '@/components/ui/time-picker/index.vue'

export default {
  components: { TimePicker },
  props: {
    top: {
      type: String,
      default: '50%'
    },
    // 
    cancel: {
      type: Function,
      default: () => {}
    },
    // 
    submit: {
      type: Function,
      default: () => {}
    }
  },
  data() {
    return {
      state: false,
      isConfirm: false, // 
      startTime: ['00', '00'],
      endTime: ['23', '59']
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'languageType']),
    ...mapState('responsible', ['dayPlayItem']),
    ...mapState('dialog', ['popupState']),
    rangeTime() {
      if (this.startTime.length > 1 && this.endTime.length > 1) {
        return `${this.startTime[0]}:${this.startTime[1]}~${this.endTime[0]}:${this.endTime[1]}`
      }
      return ''
    }
  },
  watch: {
    // 
    popupState(n) {
      if (!n) {
        this.state = false
        if (this.remove) this.remove()
      }
    }
  },
  methods: {
    commonStyle: k => _common[k],
    // 
    disabledStartHour(hour, minu) {
      const h = hour
      if (this.endTime.length) {
        const endH = parseInt(this.endTime[0])
        if (this.endTime.length > 1) {
          const endM = parseInt(this.endTime[1])
          return endM != 0 ? h > endH : h >= endH
        }
        return endH < h
      }
      return false
    },
    // 
    disabledStartMinute(hour, minu) {
      const m = minu
      if (hour < this.endTime[0]) {
        return false
      }
      if (this.endTime.length > 1) {
        const endM = parseInt(this.endTime[1])
        return m >= endM
      }
      return false
    },
    // 
    disabledEndHour(hour, minu) {
      const h = hour
      if (this.startTime.length) {
        const startH = parseInt(this.startTime[0])
        if (this.startTime.length > 1) {
          const startM = parseInt(this.startTime[1])
          return startM != 59 ? h < startH : h > startH
        }
        return startH > h
      }
      return false
    },
    // 
    disabledEndMinute(hour, minu) {
      const m = minu
      if (hour > this.startTime[0]) {
        return false
      }
      if (this.startTime.length > 1) {
        const startM = parseInt(this.startTime[1])
        return m <= startM
      }
      return false
    },
    // 
    show(isEdit = false) {
      this.state = !this.state
      if (this.state && isEdit) {
        const d = this.dayPlayItem?.day_play_time || {}
        this.startTime = [d.start_hour || '', d.start_min || '']
        this.endTime = [d.end_hour || '', d.end_min || '']
      }
      this.$nextTick(() => {
        const dailyPlayRef = this.$refs['dailyPlayRef']
        if (dailyPlayRef) this.$anim.insert(dailyPlayRef)
      })
    },
    // 
    hide(callback) {
      this.$nextTick(() => {
        const dailyPlayRef = this.$refs['dailyPlayRef']
        if (dailyPlayRef) this.$anim.close(dailyPlayRef)

        setTimeout(() => {
          this.state = false
          if (this.remove) this.remove()
          if (typeof callback == 'function') callback()
        }, POP_TIME)
      })
    },
    // 
    onContinue() {
      // 
      this.isConfirm = true
    },
    // 
    onTwoBack() {
      this.isConfirm = false
    },
    // 
    onCancel() {
      this.hide(() => {
        if (this.cancel) this.cancel()
      })
    },
    // 
    async onSubmit() {
      const params = {
        token: this.user.token,
        language: this.languageType,
        start_hour: this.startTime[0],
        start_min: this.startTime[1],
        end_hour: this.endTime[0],
        end_min: this.endTime[1]
      }
      const res = await this.$store.dispatch('responsible/setDayPlay', params).catch(e => e)
      if (res.code != 0) return this.$pop.message().show({ content: res.msg })

      this.hide(() => {
        if (this.submit) this.submit()
      })
      this.$pop.message().show({ content: res.msg })
    },
    onChangeTime(val, key) {
      this[key] = val
    }
  }
}
</script>

