<template>
  <div v-zinx v-if="state" class="comp invite_gift">
    <div class="wrapper-mask" @click="hide"></div>
    <div ref="inviteGiftRef" class="invite_gift_box">
      <movable-turntable
        :size="turntableSize"
        :goldAnimationClass="goldAnimationClass"
        :isGoldState="isGoldState"
        :turntableBg="turntableBg"
        @stopLucky="stopLucky"
        ref="turntableRef"
      >
      </movable-turntable>
      <div class="invite_gift_box_view">
        <div class="invite_gift_box_title">{{ $t('UIWheelFreeSpins') }}</div>
        <icon-font
          class="invite_gift_box_close"
          name="icon-icon_pop_close"
          :size="variables.invite_gift_box_close_size"
          :dark="variables.invite_gift_box_close_color"
          @click="hide"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import variables from '@style/components/dialog.modules.scss'
import movableTurntable from '@/components/ui/refer-activity/movable-turntable'

export default {
  props: {
    onCancel: {
      // 
      type: Function,
      default() {
        return () => {}
      }
    },
    onSubmit: {
      //
      type: Function,
      default() {
        return () => {}
      }
    }
  },
  components: {
    movableTurntable
  },
  data() {
    return {
      state: false,
      variables,
      isGoldState: false, // 
      goldAnimationClass: '', // 
      turntableSize: 0
    }
  },
  computed: {
    ...mapState('dialog', ['popupState']),
    ...mapState('base', ['isApp', 'user']),
    ...mapState('source', ['activityInvite']),
    turntableBg() {
      return [this.variables.movable_turntable_item1, this.variables.movable_turntable_item2]
    },
    // 
    throttledScrollHandler() {
      return this.throttle(this.handleScroll, 200) // 200
    }
  },
  watch: {
    // 
    popupState(n) {
      if (!n) {
        this.state = false
        if (this.remove) this.remove()
      }
    }
  },
  created() {
    // const { offsetWidth = 0 } = document.body;
    // this.turntableSize = this.isApp ? offsetWidth  : offsetWidth / 3;
    this.$nextTick(() => {
      const inviteGiftRef = this.$refs['inviteGiftRef']
      if (inviteGiftRef) {
        const { width = 0 } = inviteGiftRef.getBoundingClientRect() || {}
        this.turntableSize = width
      }
    })
    window.addEventListener('resize', this.throttledScrollHandler)
  },
  mounted() {},
  methods: {
    /**
     * 
     * @param {*} money 
     * @param {*} cancel 
     */
    show() {
      this.state = true
    },
    // 
    hide() {
      this.state = false
      if (this.remove) this.remove()
      this.onCancel()
    },
    // 
    stopLucky(item) {
      // 
      if (!item) return this.hide()
      // data.type   0: 1: 2:
      const { type = 2 } = item
      if (type == 1) {
        this.isGoldState = true
        this.goldAnimationClass = 'bank_account_btn_text'
      }
      this.onSubmit()
      setTimeout(() => {
        if (type == 2) {
          let activityInvite = JSON.parse(JSON.stringify(this.activityInvite))
          activityInvite.activity_user_task = { ...item }
          this.$store.commit('source/SET_ACTIVITY_INVITE', activityInvite)

          this.hide()
          this.$pop.inviteQuest().show()
        }
        if (type == 1) {
          // user
          this.$store.commit('base/SET_IS_REFRESH_USER', true)
          // 
          this.$store.commit('source/SET_INVITE_GIFT_GOLD', item?.value || 0)
          setTimeout(() => {
            this.user.gold = Number(this.user?.gold) + Number(item?.value || 0)
            // user
            this.$store.commit('base/SET_USER', this.user)
            // 
            this.$store.commit('source/SET_INVITE_GIFT_GOLD', 0)
            this.isGoldState = false
          }, 2000)
        }
      }, 1000)
    },
    // throttledScrollHandler
    throttle(func, wait) {
      let timeout
      return function () {
        const context = this,
          args = arguments
        clearTimeout(timeout)
        timeout = setTimeout(() => {
          func.apply(context, args)
        }, wait)
      }
    },
    handleScroll() {
      // const { offsetWidth = 0 } = document.body;
      // this.turntableSize = this.isApp ? offsetWidth  : offsetWidth / 3;
      this.$nextTick(() => {
        const inviteGiftRef = this.$refs['inviteGiftRef']
        if (inviteGiftRef) {
          const { width = 0 } = inviteGiftRef.getBoundingClientRect() || {}
          this.turntableSize = width
        }
      })
    }
  },
  destroyed() {
    // 
    window.removeEventListener('resize', this.throttledScrollHandler)
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/dialog.modules.scss';

// app 
.invite_gift_box {
  width: $invite_gift_width_app;
  position: fixed;
  top: $invite_gift_top_app;
