  transform: $invite_gift_transform_app;
  .invite_box {
    width: $invite_box_width;
    font-size: $invite_box_font_size;
    @include setBox(row, center, center);
  }
  .invite_gift_box_view {
    width: $invite_gift_invite_box_width;
    height: $invite_gift_invite_box_height;
    position: relative;
    z-index: 2;
    @include setBox(column, center, center);
    .invite_gift_box_title {
      margin: $invite_gift_box_title_margin;
      font-weight: $invite_gift_box_title_font_weight;
      font-size: $invite_gift_box_title_font_size;
      color: $invite_gift_box_title_color;
    }
    .invite_gift_box_close {
      margin: $invite_gift_box_close_margin;
      cursor: pointer;
    }
  }
}

// pc 
@include breakpoint-only-up($px: 1080px) {
  .invite_gift_box {
    width: $invite_gift_width_pc;
    position: fixed;
    top: $invite_gift_top_pc;
