<template>
  <div v-zinx v-if="state">
    <div class="wrapper-mask" @click="onBtnCancel"></div>
    <div class="regression_dialog_box">
      <div ref="regressionRef" class="regression_comp">
        <div class="regression_body">
          <div class="lt">
            <img :src="loadIcon(activityIconName)" alt="" />
          </div>
          <div class="rt">
            <div class="rt_text">
              {{ actionItem.window_content || '' }}
            </div>
            <div class="rt_btn" @click="onBtnPrimay" v-zoom>
              {{ $t('UI_Regression_Dialig_Btn_Text') }}
            </div>
          </div>
        </div>
        <icon-font
          class="icon_close"
          name="icon-icon_pop_close"
          :size="
            commonStyle(isApp ? 'regression_comp_icon_close_icon_app_size' : 'regression_comp_icon_close_icon_pc_size')
          "
          :dark="
            commonStyle(
              isApp ? 'regression_comp_icon_close_icon_app_color' : 'regression_comp_icon_close_icon_pc_color'
            )
          "
          @click="onBtnCancel"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { POP_TIME } from '@/api/setting'
import { jump } from '@/utils/linkMap'
import loadIcon from '@/assets/icons'
import _common from '@style/components/dialog.modules.scss'

export default {
  props: {
    // 
    actionItem: {
      type: Object,
      default() {
        return {}
      }
    },
    // 
    onSubmit: {
      type: Function,
      default() {
        return () => {}
      }
    },
    // 
    onCancel: {
      type: Function,
      default() {
        return () => {}
      }
    }
  },
  data() {
    return {
      state: false
    }
  },
  computed: {
    ...mapState('base', ['isApp']),
    ...mapState('dialog', ['popupState']),
    // icon
    activityIconName() {
      //  1A 2B 3C 4D
      const { action_type = 1 } = this.actionItem

      if (action_type == 1) return 'regression_dialog_a'
      if (action_type == 2) return 'regression_dialog_b'
      if (action_type == 3) return 'regression_dialog_c'
      if (action_type == 4) return 'regression_dialog_d'

      return ''
    }
  },
  watch: {
    // 
    popupState(n) {
      if (!n) {
        this.state = false
        if (this.remove) this.remove()
      }
    }
  },
  methods: {
    loadIcon: k => loadIcon(k, false),
    commonStyle: k => _common[k],
    // 
    show() {
      this.state = !this.state
      this.$nextTick(() => {
        const regressionRef = this.$refs['regressionRef']
        if (regressionRef) this.$anim.insert(regressionRef)
      })

      return Promise.resolve(this)
    },
    // /
    hide(callback) {
      this.$nextTick(() => {
        const regressionRef = this.$refs['regressionRef']
        if (regressionRef) this.$anim.close(regressionRef)

        setTimeout(() => {
          this.state = false
          if (this.remove) this.remove()
          if (typeof callback == 'function') callback()
        }, POP_TIME)
      })
    },
    // 
    onBtnPrimay() {
      jump(this.actionItem, this)

      this.hide(() => {
        this.onSubmit()
      })
    },
    // 
    onBtnCancel() {
      this.hide(() => {
        this.onCancel()
      })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/dialog.modules.scss';

// 
.regression_dialog_box {
  width: $regression_box_app_width;
  position: fixed;
  top: $regression_box_app_top;
  left: $regression_box_app_left;
  transform: $regression_box_app_transform;
  .regression_comp {
    @include setBox(column, center, center);
    .regression_body {
      width: $regression_comp_body_app_width;
      padding: $regression_comp_body_app_padding;
      background: $regression_comp_body_app_bg;
      border-radius: $regression_comp_body_app_radius;
      overflow: hidden;
      @include setBox(row, center, center);
      .lt {
        flex-shrink: 0;
        flex-grow: 0;
        width: $regression_comp_body_lt_app_width;
        min-height: $regression_comp_body_lt_app_min_height;
        img {
          width: $regression_comp_body_lt_img_app_width;
        }
      }
      .rt {
        flex-shrink: 1;
        flex-grow: 1;
        min-height: $regression_comp_body_rt_app_min_height;
        padding: $regression_comp_body_rt_app_padding;
        @include setBox(column, space-between, center);
        .rt_text {
          width: $regression_comp_body_rt_text_app_width;
          font-size: $regression_comp_body_rt_text_app_size;
          font-weight: $regression_comp_body_rt_text_app_weight;
          color: $regression_comp_body_rt_text_app_color;
          line-height: $regression_comp_body_rt_text_app_line;
          text-align: $regression_comp_body_rt_text_app_lign;
          white-space: pre-wrap;
          word-break: break-all;
        }
        .rt_btn {
          min-width: $regression_comp_body_rt_btn_app_min_width;
          height: $regression_comp_body_rt_btn_app_height;
          padding: $regression_comp_body_rt_btn_app_padding;
          margin: $regression_comp_body_rt_btn_app_margin;
          border-radius: $regression_comp_body_rt_btn_app_radius;
          background: $regression_comp_body_rt_btn_app_bg;
          font-size: $regression_comp_body_rt_btn_app_size;
          font-weight: $regression_comp_body_rt_btn_app_weight;
          color: $regression_comp_body_rt_btn_app_color;
          cursor: pointer;
          @include setBox(row, center, center);
        }
      }
    }
    .icon_close {
      margin: $regression_comp_icon_close_app_margin;
    }
  }
}
// pc
@include breakpoint-only-up() {
  .regression_dialog_box {
    width: $regression_box_pc_width;
    .regression_comp {
      .regression_body {
        padding: $regression_comp_body_pc_padding;
        border-radius: $regression_comp_body_pc_radius;
        .lt {
          width: $regression_comp_body_lt_pc_width;
          min-height: $regression_comp_body_lt_pc_min_height;
        }
        .rt {
          min-height: $regression_comp_body_rt_pc_min_height;
          padding: $regression_comp_body_rt_pc_padding;
          .rt_text {
            font-size: $regression_comp_body_rt_text_pc_size;
          }
          .rt_btn {
            min-width: $regression_comp_body_rt_btn_pc_min_width;
            height: $regression_comp_body_rt_btn_pc_height;
            margin: $regression_comp_body_rt_btn_pc_margin;
            border-radius: $regression_comp_body_rt_btn_pc_radius;
            font-size: $regression_comp_body_rt_btn_pc_size;
          }
        }
      }
      .icon_close {
        margin: $regression_comp_icon_close_pc_margin;
      }
    }
  }
}
</style>
