<template>
  <div v-zinx v-if="state">
    <div class="wrapper-mask"></div>
    <div class="guide_install_comp" :style="`top: ${top};`">
      <div ref="guideInstallRef" class="guide_install_box">
        <div class="guide_install_wrap" :style="getBg">
          <img :src="getImgUrl('guide-install-download')" />
          <div class="guide_install_wrap_content">
            <div class="des">{{ $t('UI_Download_App') }}</div>
            <v-btn color="t_btn_host_color" class="down_btn btn_radius" @click="onBtnPrimay">
              <icon-font
                class="down_btn_icon"
                name="icon-a-DownloadAPP"
                :dark="commonStyle('guide_instal_app_btn_color')"
                :size="commonStyle('guide_instal_app_btn_icon_size')"
              ></icon-font>
              {{ $t('UI_Invite_Download') }}
            </v-btn>
          </div>
        </div>
        <icon-font
          class="guide_install_close cursor_point"
          name="icon-icon_pop_close"
          :dark="commonStyle('guide_instal_app_btn_color')"
          :size="commonStyle('guide_instal_close_app_icon_size')"
          @click="hide"
        ></icon-font>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { POP_TIME } from '@/api/setting'
import { getCacheIcon } from '@/assets/imgCache.js'
import _common from '@style/components/dialog.modules.scss'

export default {
  props: {
    top: {
      type: [Number, String],
      default: '50%'
    },
    onSubmit: {
      // 
      type: Function,
      default() {
        return () => {}
      }
    },
    onCancel: {
      // 
      type: Function,
      default() {
        return () => {}
      }
    }
  },
  data() {
    return {
      state: false
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'systemConfig', 'platform']),
    ...mapState('dialog', ['popupState']),
    // 
    getBg() {
      const src = this.getImgUrl('guide-install')
      if (src) {
        return {
          background: `url(${src})`,
          backgroundSize: '100% 100%',
          backgroundRepeat: 'no-repeat'
        }
      }

      return {}
    }
  },
  watch: {
    // 
    popupState(n) {
      if (!n) {
        this.state = false
        if (this.remove) this.remove()
      }
    }
  },
  methods: {
    getImgUrl(key) {
      const result = getCacheIcon(key)
      let src = ''
      if (result) {
        src = typeof result === 'string' ? result : result.value.src
      }
      return src
    },
    commonStyle: k => _common[k],
    // 
    show() {
      this.state = !this.state
      this.$nextTick(() => {
        const guideInstallRef = this.$refs['guideInstallRef']
        if (guideInstallRef) this.$anim.insert(guideInstallRef)
      })

      return Promise.resolve(this)
    },
    // /
    hide(callback) {
      this.$nextTick(() => {
        const guideInstallRef = this.$refs['guideInstallRef']
        if (guideInstallRef) this.$anim.close(guideInstallRef)

        setTimeout(() => {
          this.state = false
          if (this.remove) this.remove()
          if (typeof callback == 'function') callback()
        }, POP_TIME)
      })
    },
    // 
    onBtnPrimay() {
      this.hide(() => {
        const { down_urls = {} } = this.systemConfig
        const { android_download_url = '' } = down_urls
        if (this.platform === 'ios') {
          this.$pop.downloadDialog().show()
          this.hide()
        } else {
          if (!android_download_url) return
          setTimeout(() => window.open(android_download_url))
        }
      })
    },
    // 
    onBtnCancel() {
      this.hide(() => {
        this.onCancel()
      })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/dialog.modules.scss';

// 
.guide_install_comp {
  width: $guide_install_app_width;
  position: fixed;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  .guide_install_box {
    .guide_install_wrap {
      width: $guide_install_wrap_app_width;
      aspect-ratio: $guide_install_wrap_app_ratio;
      border-radius: $guide_install_wrap_app_radius;
      position: relative;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      align-items: center;
      img {
        width: $guide_install_wrap_img_app_width;
        height: $guide_install_wrap_img_app_height;
        position: absolute;
        top: $guide_install_wrap_img_app_top;
        left: $guide_install_wrap_img_app_left;
        transform: $guide_install_wrap_img_app_transform;
      }
      .guide_install_wrap_content {
        margin: $guide_install_wrap_content_margin;
        .des {
          font-weight: $guide_install_wrap_des_app_weight;
          font-size: $guide_install_wrap_des_app_size;
          color: $guide_install_wrap_des_app_color;
          line-height: $guide_install_wrap_des_app_line;
          padding: $guide_install_wrap_des_app_padding;
          margin: $guide_install_wrap_des_app_margin;
        }
        .down_btn {
          width: $guide_install_wrap_down_btn_app_width;
          height: $guide_install_wrap_down_btn_app_height;
          color: $guide_instal_app_btn_color;
          .down_btn_icon {
            margin: $guide_install_wrap_down_btn_app_margin;
          }
        }
      }
    }
  }
  .guide_install_close {
    margin: $guide_instal_close_app_icon_margin;
  }
}

// pc
@include breakpoint-only-up() {
  .guide_install_comp {
    width: $guide_install_pc_width;
  }
}
</style>
