<template>
  <div v-zinx v-if="state">
    <div class="wrapper-mask"></div>
    <div class="img_drag_comp">
      <div ref="imgDragBoxRef" class="img_drag_box">
        <div class="drag_title">
          <div class="text break_word">{{ $t('UI_Dialog_Img_Drag_Title') }}</div>
          <div class="drag_close" @click="onCancel">
            <icon-font
              name="icon-close"
              :size="commonStyle(isApp ? 'img_drag_close_size_app' : 'img_drag_close_size_pc')"
              :dark="commonStyle(isApp ? 'img_drag_close_color_app' : 'img_drag_close_color_pc')"
            />
          </div>
        </div>
        <!-- loading -->
        <div v-if="imgLoading" class="drag_load">
          <v-progress-circular
            :width="commonStyle(isApp ? 'img_drag_circular_width_app' : 'img_drag_circular_width_pc')"
            :size="commonStyle(isApp ? 'img_drag_circular_size_app' : 'img_drag_circular_size_pc')"
            :color="commonStyle(isApp ? 'img_drag_circular_color_app' : 'img_drag_circular_color_pc')"
            :indeterminate="true"
          />
        </div>
        <div class="drag_content_box">
          <div id="imgBgId">
            <div id="imgIconId"></div>
            <div v-if="showErrbox" class="img_load_err" @click="checkDrag({}, false)">
              <div class="img_load_err_title">{{ $t('UI_Dialog_Img_Drag_Load_Err') }}</div>
              <icon-font
                name="icon-shuaxin"
                :size="commonStyle(isApp ? 'img_drag_refresh_icon_app_size' : 'img_drag_refresh_icon_pc_size')"
                :dark="commonStyle(isApp ? 'img_drag_refresh_icon_app_color' : 'img_drag_refresh_icon_pc_color')"
              />
            </div>
          </div>
          <div v-show="!imgLoading" id="drag_btn_box">
            <div class="drag_btn_fu">
              <div class="drag_btn_too"></div>
              <div class="drag_btn">
                <icon-font
                  :name="iconName"
                  :size="commonStyle(isApp ? 'img_drag_slide_size_app' : 'img_drag_slide_size_pc')"
                  :dark="commonStyle(isApp ? 'img_drag_slide_color_app' : 'img_drag_slide_color_pc')"
                />
              </div>
            </div>
          </div>
          <!-- lodding -->
          <div v-if="interLoad" class="inter_load">
            <img :src="loadIcon('common_loading')" alt="" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { POP_TIME, VUE_APP_SELF_VERIFICATION_KEY } from '@/api/setting'
import handleImg from './handleImg.js'
import Encryption from '@/utils/encryption.js'
import loadIcon from '@/assets/icons'
import _common from '@style/components/dialog.modules.scss'
import { getCheckVerifyImageCode } from '@/api/modules/config.js'
const encryption = new Encryption()

export default {
  props: {
    // url
    checkSrc: {
      type: String,
      default: ''
    },
    // bese64
    baseSrc: {
      type: String,
      default: ''
    },
    // code
    checkCode: {
      type: [String, Number],
      default: ''
    }
  },
  data() {
    return {
      state: false,
      imgSrc: '', // bese64
      imgCode: '', // code
      imgLoading: false, // loading
      interLoad: false, // loadding
      isDragging: false, // 
      num: 6, // icon
      container_w: 0, // 
      container_h: 0, // 
      start_x: 0, // 
      icon_x: 0, // icon x 
      icon_y: 0, // icon y 
      icon_w: 0, // icon 
      icon_h: 0, // icon 
      start_time: 0, // 
      end_time: 0, // 
      move_list: [], // icon
      icon_status: 'initial', // icon initial: success: error:
      showErrbox: false // 
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'languageType', 'user', 'systemConfig']),
    ...mapState('dialog', ['popupState']),
    // icon
    iconName() {
      if (this.icon_status == 'initial' || this.icon_status == 'success') return 'icon-Slide'
      // if (this.icon_status == 'success') return "icon-select";
      return 'icon-close'
    },
    // 
    pictureHost() {
      const { picture_host = '' } = this.systemConfig
      return picture_host
    }
  },
  watch: {
    // 
    popupState(n) {
      if (!n) {
        this.state = false
        if (this.remove) this.remove()
      }
    }
  },
  methods: {
    commonStyle: k => _common[k],
    loadIcon: k => loadIcon(k, false),
    // 
    show() {
      return new Promise((resolve, reject) => {
        this.dragResolve = resolve
        this.dragReject = reject

        this.state = !this.state
        this.imgLoading = true
        this.imgSrc = this.checkSrc ? `${this.pictureHost}${this.checkSrc}` : this.baseSrc
        this.imgCode = this.checkCode
        this.initImg(() => {
          this.imgLoading = false
        })
        this.unloadEvent()
        this.watchEvent()

        this.$nextTick(() => {
          const imgDragBoxRef = this.$refs['imgDragBoxRef']
          if (imgDragBoxRef) this.$anim.insert(imgDragBoxRef)
        })
      })
    },
    // 
    hide(callback) {
      this.$nextTick(() => {
        const imgDragBoxRef = this.$refs['imgDragBoxRef']
        if (imgDragBoxRef) this.$anim.close(imgDragBoxRef)

        this.unloadEvent()
        setTimeout(() => {
          this.state = false
          if (this.remove) this.remove()
          if (typeof callback == 'function') callback()
        }, POP_TIME)
      })
    },
    // 
    initImg(callback) {
      this.$nextTick(async () => {
        const imgBgId = document.getElementById('imgBgId')
        if (!imgBgId) {
          if (typeof callback == 'function') callback()
          return
        }

        // 
        const imgBgId_w = imgBgId.getBoundingClientRect()?.width

        // 
        const clsImg = new handleImg(this.imgSrc)
        const { bg = {}, icon = {} } = await clsImg.init()
        if (JSON.stringify(bg) === '{}' || !bg.width || !bg.height) {
          this.showErrbox = true
          if (typeof callback == 'function') callback()
          this.unloadEvent()
          return
        }
        const { canvas: bg_dom, width: bg_w, height: bg_h } = bg
        const { canvas: icon_dom, width: icon_w, height: icon_h, x, y } = icon

        const rtion = bg_w / bg_h
        const imgBgId_h = imgBgId_w / rtion
        if (bg_dom) {
          // 
          const dragImgCanvasId = document.getElementById('dragImgCanvasId')
          if (dragImgCanvasId) dragImgCanvasId.remove()

          bg_dom.style.width = `${imgBgId_w}px`
          bg_dom.style.height = `${imgBgId_h}px`
          bg_dom.id = 'dragImgCanvasId'
          imgBgId.appendChild(bg_dom)
          this.container_w = imgBgId_w
          this.container_h = imgBgId_h
        }
        if (icon_dom) {
          const new_icon_w = (imgBgId_w * icon_w) / bg_w
          const new_icon_h = (new_icon_w * icon_h) / icon_w
          icon_dom.style.width = `${new_icon_w}px`
          icon_dom.style.height = `${new_icon_h}px`
          icon_dom.id = 'dragIconCanvasId'
          this.icon_w = new_icon_w
          this.icon_h = new_icon_h

          // x / bg_w = new_icon_x / imgBgId_w
          const new_icon_x = (x * imgBgId_w) / bg_w
          // y / bg_h = new_icon_y / imgBgId_h;
          const new_icon_y = (y * imgBgId_h) / bg_h

          this.icon_x = new_icon_x
          this.icon_y = new_icon_y
          const imgIconId = document.getElementById('imgIconId')
          if (imgIconId) {
            // 
            const dragIconCanvasId = document.getElementById('dragIconCanvasId')
            if (dragIconCanvasId) dragIconCanvasId.remove()

            imgIconId.style.left = `${new_icon_x}px`
            imgIconId.style.top = `${new_icon_y}px`
            imgIconId.appendChild(icon_dom)
          }
        }

        if (typeof callback == 'function') callback()
      })
    },
    // 
    onDragDown(e) {
      this.$nextTick(() => {
        if (this.icon_status != 'initial') return
        e.preventDefault()
        this.isDragging = true

        // btn 
        const dragBtnBox = document.getElementById('drag_btn_box')
        if (dragBtnBox) {
          if (this.isApp) {
            const pageX = e.touches && e.touches[0] ? e.touches[0].pageX : 0
            this.start_x = pageX - dragBtnBox.getBoundingClientRect().left
          } else {
            this.start_x = e.clientX - dragBtnBox.getBoundingClientRect().left
          }
        }
        // 
        this.start_time = new Date().getTime()
      })
    },
    // 
    onDragMove(e) {
      this.$nextTick(() => {
        if (this.icon_status != 'initial') return
        e.preventDefault()
        if (!this.isDragging) return

        const dragBtnBox = document.getElementById('drag_btn_box')
        if (dragBtnBox) {
          // 
          const dragBtnBox_w = dragBtnBox.getBoundingClientRect().width

          // x
          let mouse_x = 0
          if (this.isApp) {
            const pageX = e.touches && e.touches[0] ? e.touches[0].pageX : 0
            mouse_x = pageX - dragBtnBox.getBoundingClientRect().left
          } else {
            mouse_x = e.clientX - dragBtnBox.getBoundingClientRect().left
          }
          // x = x - x
          const move_x = mouse_x - this.start_x

          const dragBtnToo = dragBtnBox.querySelectorAll('.drag_btn_too')[0]
          const dragBtn = dragBtnBox.querySelectorAll('.drag_btn')[0]
          const imgIconId = document.getElementById('imgIconId')
          if (dragBtn) {
            const dragBtn_w = dragBtn.getBoundingClientRect().width
            /**
             * 
             */
            // 
            const btnTance = dragBtnBox_w - dragBtn_w
            if (move_x > 0 && move_x < btnTance) {
              dragBtnToo.style.width = `${move_x + dragBtn_w / 2}px`
              dragBtn.style.left = `${move_x}px`
            }
            /**
             * icon
             */
            if (imgIconId) {
              const dragIcon_w = imgIconId.getBoundingClientRect().width
              const iconTance = dragBtnBox_w - (dragIcon_w + this.icon_x)
              // icon move_x / btnTance = icon_move_x / iconTance
              const icon_move_x = (move_x * iconTance) / btnTance
              // icon
              if (move_x > 0 && icon_move_x < iconTance) {
                imgIconId.style.left = `${this.icon_x + icon_move_x}px`

                // icon --> iconicon1/2
                const imgBgId = document.getElementById('imgBgId')
                const move_y = imgIconId.getBoundingClientRect().top - imgBgId.getBoundingClientRect().top
                this.move_list.push({
                  //  +  + icon1/2
                  x: this.icon_x + icon_move_x + this.icon_w / 2,
                  //  + icon1/2
                  y: move_y + this.icon_h / 2,
                  time: new Date().getTime()
                })
              }
            }
          }
        }
      })
    },
    // 
    onDragUp(e) {
      if (this.icon_status != 'initial') return
      if (!this.isDragging) return

      this.$nextTick(() => {
        e.preventDefault()
        this.isDragging = false

        // 
        this.end_time = new Date().getTime()

        // icon 
        let new_icon_x = 0
        let new_icon_y = 0
        const imgBgId = document.getElementById('imgBgId')
        const imgIconId = document.getElementById('imgIconId')
        if (imgIconId) {
          new_icon_x = imgIconId.getBoundingClientRect().left - imgBgId.getBoundingClientRect().left
          new_icon_y = imgIconId.getBoundingClientRect().top - imgBgId.getBoundingClientRect().top
        }

        const data = {
          start: {
            x: this.icon_x + this.icon_w / 2,
            y: this.icon_y + this.icon_h / 2,
            time: this.start_time
          },
          end: {
            x: new_icon_x + this.icon_w / 2,
            y: new_icon_y + this.icon_h / 2,
            time: this.end_time
          },
          container_w: this.container_w,
          container_h: this.container_h,
          code: this.imgCode,
          list: this.moveArr()
        }
        /**
         * 
         * ,(1/3),,.
         */
        const finish_x = new_icon_x + this.icon_w / 2
        const imgBgId_w = (imgBgId ? imgBgId.getBoundingClientRect().width : 0) / 3
        if (this.moveArr().length > 0 && finish_x > imgBgId_w) {
          this.checkDrag(data)
        } else {
          const img_src = this.imgSrc
          const img_code = this.imgCode
          this.reset(() => {
            this.imgSrc = img_src
            this.imgCode = img_code
          })
        }
      })
    },
    // iconnum
    moveArr() {
      const list = JSON.parse(JSON.stringify(this.move_list))
      const len = list.length

      if (len <= this.num) return list

      const remain = len - this.num
      const start_num = parseInt(remain / 2)
      const end_num = start_num + this.num
      return list.slice(start_num, end_num)
    },
    // 
    reset(callback) {
      this.imgSrc = ''
      this.imgCode = ''
      this.imgLoading = false
      this.isDragging = false
      this.start_time = 0
      this.end_time = 0
      this.move_list = []
      this.icon_status = 'initial'

      // 
      const dragBtnBox = document.getElementById('drag_btn_box')
      if (dragBtnBox) {
        this.start_x = 0
        const dragBtnToo = dragBtnBox.querySelectorAll('.drag_btn_too')[0]
        const dragBtn = dragBtnBox.querySelectorAll('.drag_btn')[0]
        if (dragBtn) {
          dragBtnToo.style.width = 0
          dragBtn.style.left = 0
        }
      }
      // icon
      const imgIconId = document.getElementById('imgIconId')
      if (imgIconId) imgIconId.style.left = `${this.icon_x}px`

      if (typeof callback == 'function') callback()
    },
    // 
    onCancel() {
      this.hide(() => {
        this.dragReject('cancel')
        this.reset()
      })
    },
    // 
    async checkDrag(data = {}, showErrorAnim = true) {
      const strData = JSON.stringify(data)
      const key = VUE_APP_SELF_VERIFICATION_KEY || ''
      // 
      const encrypted = encryption.encrypt(strData, key)
      // 
      const decrypted = encryption.decrypt(encrypted, key)

      // 
      const params = {
        token: this.user?.token,
        language: this.languageType,
        verify_img_params: encrypted
      }
      this.interLoad = true
      const res = await getCheckVerifyImageCode(params).catch(e => e)
      this.interLoad = false
      if (!res || res.code != 0) {
        const { code = '', img = '', url = '' } = res && res.data && this.$u.lodash.isObject(res.data) ? res.data : {}
        showErrorAnim && this.checkError({ code, img, url })
        return
      }

      // this.$pop.message().show({ content: res && res.msg });
      this.checkSuccess({ encrypted, decrypted, key })
    },
    // 
    checkError({ code = '', img = '', url = '' }) {
      this.$nextTick(() => {
        this.icon_status = 'error'
        const imgDragBoxRef = this.$refs['imgDragBoxRef']
        if (imgDragBoxRef) {
          this.$anim.trembling(imgDragBoxRef)

          setTimeout(() => {
            imgDragBoxRef.style.animationName = 'none'
            this.reset(() => {
              this.imgSrc = url ? `${this.pictureHost}${url}` : img
              this.imgCode = code
              this.interLoad = true
              this.initImg(() => {
                this.interLoad = false
              })
            })
          }, POP_TIME + 100)
        }
      })
    },
    // 
    checkSuccess(data = {}) {
      this.icon_status = 'success'
      this.hide(() => {
        /**
         * encrypted 
         * decrypted 
         * key  key
         */
        this.dragResolve(data)
        this.reset()
      })
    },
    // /
    watchEvent() {
      this.$nextTick(() => {
        const dragBtnBox = document.getElementById('drag_btn_box')
        if (!dragBtnBox) return

        // /
        if (this.isApp) {
          dragBtnBox.addEventListener('touchstart', this.onDragDown)
          dragBtnBox.addEventListener('touchmove', this.onDragMove)
          document.addEventListener('touchend', this.onDragUp)
        } else {
          dragBtnBox.addEventListener('mousedown', this.onDragDown)
          window.addEventListener('mousemove', this.onDragMove)
          document.addEventListener('mouseup', this.onDragUp)
        }
      })
    },
    // /
    unloadEvent() {
      this.$nextTick(() => {
        const dragBtnBox = document.getElementById('drag_btn_box')
        if (!dragBtnBox) return

        // 
        if (this.isApp) {
          dragBtnBox.removeEventListener('touchstart', this.onDragDown)
          dragBtnBox.removeEventListener('touchmove', this.onDragMove)
          document.removeEventListener('touchend', this.onDragUp)
        } else {
          dragBtnBox.removeEventListener('mousedown', this.onDragDown)
          window.removeEventListener('mousemove', this.onDragMove)
          document.removeEventListener('mouseup', this.onDragUp)
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/dialog.modules.scss';

// app
.img_drag_comp {
  width: $img_drag_app_width;
  position: $img_drag_app_position;
  top: $img_drag_app_top;
  left: $img_drag_app_left;
  transform: $img_drag_app_transform;
  .img_drag_box {
    width: $img_drag_box_app_width;
    padding: $img_drag_box_app_padding;
    border-radius: $img_drag_box_app_radius;
    font-size: $img_drag_box_app_size;
    overflow: hidden;
    user-select: none;
    background: $img_drag_box_app_bg;
    .drag_title {
      width: $img_drag_box_title_app_width;
      margin: $img_drag_box_title_app_margin;
      @include setBox(row, center, flex-start);
      .text {
        flex-shrink: 1;
        flex-grow: 1;
        padding: $img_drag_box_title_text_app_padding;
        font-size: $img_drag_box_title_text_app_size;
        font-weight: $img_drag_box_title_text_app_weight;
        color: $img_drag_box_title_text_app_color;
        line-height: $img_drag_box_title_text_app_line;
      }
      .drag_close {
        flex-shrink: 0;
        flex-grow: 0;
      }
    }
    .drag_load {
      width: $img_drag_load_app_width;
      margin: $img_drag_load_app_margin;
      @include setBox(row, center, center);
    }
    .drag_content_box {
      width: $img_drag_content_box_app_width;
      border-radius: $img_drag_content_box_app_radius;
      overflow: hidden;
      position: relative;
      #imgBgId {
        width: $img_drag_img_bg_app_width;
        overflow: hidden;
        position: relative;
        #imgIconId {
          position: absolute;
        }
      }
      #drag_btn_box {
        width: $img_drag_btn_box_app_width;
        padding: $img_drag_btn_box_app_padding;
        background: $img_drag_btn_box_app_bg;
        @include setBox(row, flex-start, center);
        .drag_btn_fu {
          width: $img_drag_btn_fu_app_width;
          background: $img_drag_btn_fu_app_bg;
          position: relative;
          .drag_btn_too {
            position: absolute;
            left: $img_drag_btn_too_app_left;
            top: $img_drag_btn_too_app_top;
            bottom: $img_drag_btn_too_app_bottom;
            background: $img_drag_btn_too_app_bg;
          }
          .drag_btn {
            width: $img_drag_btn_box_btn_app_width;
            padding: $img_drag_btn_box_btn_app_padding;
            border-radius: $img_drag_btn_box_btn_app_radius;
            background: $img_drag_btn_box_btn_app_bg;
            position: relative;
            left: 0;
            @include setBox(row, center, center);
          }
        }
      }
      .inter_load {
        position: absolute;
        left: $img_drag_inter_load_app_left;
        top: $img_drag_inter_load_app_top;
        right: $img_drag_inter_load_app_right;
        bottom: $img_drag_inter_load_app_bottom;
        z-index: 1;
        background: $img_drag_inter_load_app_bg;
        @include setBox(row, center, center);
        img {
          width: $img_drag_inter_load_img_app_width;
        }
      }
      .img_load_err {
        @include setBox(column, center, center);
        gap: $img_drag_inter_load_img_err_app_gap;
        width: $img_drag_img_bg_app_width;
        aspect-ratio: $img_drag_inter_load_img_err_app_ratio;
        padding: $img_drag_inter_load_img_err_app_padding;
        background-color: $img_drag_inter_load_img_err_app_bg;
        cursor: pointer;
        .img_load_err_title {
          font-size: $img_drag_inter_load_img_err_title_app_size;
          line-height: $img_drag_inter_load_img_err_title_app_line;
          color: $img_drag_inter_load_img_err_title_app_color;
          text-align: $img_drag_inter_load_img_err_title_app_align;
        }
      }
    }
  }
}
// pc
@include breakpoint-only-up() {
  .img_drag_comp {
    width: $img_drag_pc_width;
    .img_drag_box {
      padding: $img_drag_box_pc_padding;
      border-radius: $img_drag_box_pc_radius;
      .drag_title {
        margin: $img_drag_box_title_pc_margin;
        .text {
          padding: $img_drag_box_title_text_pc_padding;
          font-size: $img_drag_box_title_text_pc_size;
        }
      }
      .drag_load {
        margin: $img_drag_load_pc_margin;
      }
      .drag_content_box {
        border-radius: $img_drag_content_box_pc_radius;
        #drag_btn_box {
          padding: $img_drag_btn_box_pc_padding;
          .drag_btn_fu {
            .drag_btn {
              width: $img_drag_btn_box_btn_pc_width;
              padding: $img_drag_btn_box_btn_pc_padding;
              border-radius: $img_drag_btn_box_btn_pc_radius;
            }
          }
        }
        .img_load_err {
          gap: $img_drag_inter_load_img_err_pc_gap;
          .img_load_err_title {
            font-size: $img_drag_inter_load_img_err_title_pc_size;
            line-height: $img_drag_inter_load_img_err_title_pc_line;
          }
        }
      }
    }
  }
}
</style>
