<template>
  <div class="comp problem_iframe">
    <iframe v-show="iframeShow" ref="myIframe" @load="onLoad" :src="iframeUrl" allowfullscreen></iframe>
    <!-- iframe -->
    <div v-show="!iframeShow" class="iframe_div"></div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import PublishSubscribe from 'publish-subscribe-js'
import kefuType from '@/events/kefu'

export default {
  data() {
    return {
      iframeShow: false
    }
  },
  computed: {
    ...mapState('base', ['user', 'systemConfig', 'isApp', 'languageType', 'KfNoticeData']),
    //  iframe 
    iframeUrl() {
      const { token = '', uid = '' } = this.user
      // ai_chat_robot_switch 1: 2: 3:AI
      // chat_robot_entry_host AI
      // chat_feedback_entry_host /
      const {
        ai_chat_robot_switch = 1,
        chat_robot_entry_host,
        chat_robot_appid,
        chat_feedback_entry_host,
        customer_service_entry_switch
      } = this.systemConfig
      if (ai_chat_robot_switch == 3) {
        return `${chat_robot_entry_host}#/ai_chat?appid=${chat_robot_appid}&token=${token}&language=${this.languageType}&user_id=${uid}&version=${VUE_APP_VERSION}&customer_service=0`
      }
      const entryMap = {
        1: 'chat', // 
        2: 'frequently' // 
      }
      const enteyPath = entryMap[ai_chat_robot_switch]
      return `${chat_feedback_entry_host}#/${enteyPath}?token=${token}&language=${this.languageType}&user_id=${uid}&version=${VUE_APP_VERSION}&customer_service=${customer_service_entry_switch}`
    }
  },
  mounted() {
    //  iframe 
    window.addEventListener('message', this.iframeCallback)

    // iframe
    setTimeout(() => {
      this.propIframe('chat_notice_renew', this.KfNoticeData)
    }, 1000)

    // 
    PublishSubscribe.subscribe(kefuType.KEFU_WORK_ORDER_RED, data => {
      this.propIframe('chat_notice_renew', data)
    })
  },
  methods: {
    // iframe
    onLoad() {
      this.iframeShow = true
      setTimeout(() => {
        this.initClientConfig()
      }, 300)
    },
    initClientConfig() {
      // 
      const initData = {
        logo: this.$loadIcon('logo', false)
      }
      this.propIframe('init_client_config', initData)
    },
    //  iframe 
    propIframe(type, data = {}) {
      const newData = { iframeType: type, iframeData: data }
      this.$nextTick(() => {
        let myIframe = this.$refs['myIframe']
        if (myIframe) {
          myIframe = myIframe.contentWindow
          // postMessage (json++)
          myIframe.postMessage(newData, '*')
        }
      })
    },
    // iframe 
    iframeCallback(event) {
      const infoData = event.data || {}
      const { iframeType = '', iframeData = {} } = infoData
      // 
      if (iframeType == 'close_page') {
        this.iframeShow = false
        this.$emit('close')
      }
      // //  message 
      // if (iframeType == "tip_message") {
      //     const { content = '' } = iframeData;
      //     this.$pop.message().show({ content });
      // };
      // 
      if (iframeType == 'jump_page') {
        this.iframeShow = false
        this.$emit('close')
        this.$pop.guide({ item: iframeData }).show()
      }
    }
  },
  beforeDestroy() {
    window.removeEventListener('message', this.iframeCallback)
    // 
    PublishSubscribe.unsubscribe(kefuType.KEFU_WORK_ORDER_RED)
  }
}
</script>

<style lang="scss" scoped>
.comp.problem_iframe {
  width: 100%;
  height: 100%;

  .iframe_div {
    width: 100%;
    height: 100%;
    background: $kefu_bg;
  }

  iframe {
    width: 100%;
    height: 100%;
    background: $kefu_bg;
  }
}
</style>
