<template>
  <div v-zinx v-if="state">
    <div class="wrapper-mask"></div>
    <div class="second_recharge_comp" :style="`top: ${top};`">
      <div ref="secondRechargeRef" class="second_recharge_box">
        <div class="second_recharge_wrap">
          <div class="second_recharge_wrap_content">
            <div class="second_recharge_scale">{{ activityInfo?.reward_ratio }}%</div>
            <div class="second_recharge_content_box">
              <div class="des_tips">
                {{ $t('UI_Congratulations') }}
              </div>
              <div class="des_content" v-html="$t('UI_Receive_content', { per: activityInfo?.reward_ratio })"></div>
              <div class="des_time">
                <span>{{ timeData }}</span>
              </div>
              <v-btn color="t_btn_host_color" class="des_btn btn_radius" @click="onBtnGo">
                {{ $t('UI.RegressionTask_Go') }}
              </v-btn>
            </div>
          </div>
        </div>
        <icon-font
          class="second_recharge_app_btn_icon_size_close cursor_point"
          name="icon-icon_pop_close"
          :dark="commonStyle('second_recharge_app_btn_color')"
          :size="commonStyle('second_recharge_close_app_icon_size')"
          @click="onBtnCancel"
        ></icon-font>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { POP_TIME } from '@/api/setting'
import { getCacheIcon } from '@/assets/imgCache.js'
import _common from '@style/components/dialog.modules.scss'

export default {
  props: {
    top: {
      type: [Number, String],
      default: '50%'
    },
    onSubmit: {
      // 
      type: Function,
      default() {
        return () => {}
      }
    },
    onCancel: {
      // 
      type: Function,
      default() {
        return () => {}
      }
    }
  },
  data() {
    return {
      state: false,
      activityInfo: null,
      timeData: null,
      timer: null,
      count: 0,
      dayData: ''
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'systemConfig', 'platform', 'user', 'languageType', 'systemConfig']),
    ...mapState('dialog', ['popupState'])
  },
  watch: {
    // 
    popupState(n) {
      if (!n) {
        this.state = false
        if (this.remove) this.remove()
      }
    }
  },
  methods: {
    // 
    async init() {
      const params = {
        token: this.user.token,
        language: this.languageType
      }
      const res = await this.$store.dispatch('source/getSecondRechargeExtActFn', params).catch(e => e)
      if (res.code != 0) return
      if (res.code == 0) {
        this.activityInfo = res?.data?.activity_info
        if (this.$u.lodash.isObject(this.user?.secord_recharge_ext_gift)) {
          this.countDown()
        }
      }
    },
    dayFn() {
      const timeCount = this.$u.datetime.getFormatTimeThree(this.count)
      this.dayData = timeCount.days
      let hours = this.$u.datetime.strpad(timeCount.hours)
      let minutes = this.$u.datetime.strpad(timeCount.minutes)
      let seconds = this.$u.datetime.strpad(timeCount.seconds)
      let time = `${hours}:${minutes}:${seconds}`
      this.timeData = time
    },
    // 
    countDown() {
      if (this.timer) {
        clearTimeout(this.timer)
        this.timer = null
      }
      const currentTime = parseInt(new Date().getTime() / 1000)
      const { end_time = 0 } = this.user.secord_recharge_ext_gift
      const surplusTime = end_time - currentTime
      this.count = surplusTime <= 0 ? 0 : surplusTime
      this.dayFn()
      // 
      if (!this.count) {
        this.hide()
        return
      }

      this.timer = setTimeout(() => {
        this.countDown()
      }, 1 * 1000)
    },
    commonStyle: k => _common[k],
    // 
    show() {
      this.state = !this.state
      this.init()
      this.$nextTick(() => {
        const secondRechargeRef = this.$refs['secondRechargeRef']
        if (secondRechargeRef) this.$anim.insert(secondRechargeRef)
      })

      return Promise.resolve(this)
    },
    // /
    hide(callback) {
      this.$nextTick(() => {
        const secondRechargeRef = this.$refs['secondRechargeRef']
        if (secondRechargeRef) this.$anim.close(secondRechargeRef)

        setTimeout(() => {
          this.state = false
          if (this.remove) this.remove()
          if (typeof callback == 'function') callback()
        }, POP_TIME)
      })
    },
    // 
    onBtnGo() {
      this.hide(() => {
        this.$store.dispatch('modal/setDepositWithdrawShow')
      })
    },
    // 
    onBtnCancel() {
      this.hide(() => {
        this.onCancel()
      })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/dialog.modules.scss';
$second_recharge_bg: url('@image/dialog/second-recharge.png') no-repeat top / 100% auto;
// 
.second_recharge_comp {
  width: $second_recharge_app_width;
  position: fixed;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  .second_recharge_box {
    .second_recharge_wrap {
      width: $second_recharge_wrap_app_width;
      aspect-ratio: $second_recharge_wrap_app_ratio;
      border-radius: $second_recharge_wrap_app_radius;
      position: relative;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      align-items: center;
      background: $second_recharge_bg;
      img {
        width: $second_recharge_wrap_img_app_width;
        height: $second_recharge_wrap_img_app_height;
        position: absolute;
        top: $second_recharge_wrap_img_app_top;
        left: $second_recharge_wrap_img_app_left;
        transform: $second_recharge_wrap_img_app_transform;
      }
      .second_recharge_wrap_content {
        margin: $second_recharge_wrap_content_margin;
        width: $second_recharge_wrap_content_width;
        padding: $second_recharge_wrap_content_padding;
        .second_recharge_scale {
          background: $second_recharge_scale_background;
          border-radius: $second_recharge_scale_border_radius;
          height: $second_recharge_scale_height;
          line-height: $second_recharge_scale_height;
          padding: $second_recharge_scale_padding;
          font-weight: bold;
          font-size: $second_recharge_scale_font_size;
          color: $second_recharge_scale_color;
          display: inline-block;
          min-width: $second_recharge_scale_min_width;
        }
        .second_recharge_content_box {
          background: $second_recharge_content_box_background;
          border-radius: $second_recharge_content_box_border_radius;
          padding: $second_recharge_content_box_padding;
          overflow: hidden;
          margin: $second_recharge_content_box_margin;
          .des_tips {
            font-weight: $second_recharge_des_tips_font_weight;
            font-size: $second_recharge_des_tips_font_size;
            color: $second_recharge_des_tips_color;
            margin-top: $second_recharge_des_tips_margin_top;
            padding: $second_recharge_des_tips_padding;
          }
          .des_content {
            font-weight: $des_content_font_weight;
            font-size: $des_content_font_size;
            color: $des_content_color;
            margin-top: $des_content_margin_top;
            padding: $des_content_padding;
          }
          .des_time {
            background: $des_time_background;
            border-radius: $des_time_border_radius;
            padding: $des_time_padding;
            text-align: center;
            margin-top: $des_time_margin_top;
            font-weight: $des_time_font_weight;
            font-size: $des_time_font_size;
            color: $des_time_font_color;
          }
          .des_btn {
            width: $des_btn_width;
            margin-top: $des_btn_margin_top;
            font-weight: $des_btn_font_weight;
            font-size: $des_btn_font_size;
            color: $des_btn_color;
            height: $des_btn_height;
          }
        }
      }
    }
  }
  .second_recharge_close {
    margin: $guide_instal_close_app_icon_margin;
  }
}

// pc
@include breakpoint-only-up() {
  .second_recharge_comp {
    width: $second_recharge_pc_width;
  }
}
</style>
