class handleImg {
  /**
   * @param {*} imageSrc url / base64 
   */
  constructor(imageSrc) {
    this.imageSrc = imageSrc
  }
  /**
   * 
   * @param {*} width
   * @param {*} height
   * @returns { canvas, ctx }
   */
  createCanvas(width, height) {
    let canvas = document.createElement('canvas')
    let ctx = canvas.getContext('2d')
    canvas.width = width
    canvas.height = height

    return { canvas, ctx }
  }
  /**
   * icon
   * @param {*} ctx
   * @param {*} width
   * @param {*} height
   * @returns { x: x, y: y, width: , height: , data };
   */
  getElementCoordinates(ctx, width, height) {
    let imageData = ctx.getImageData(0, height / 2, width, height)
    let pixels = imageData.data
    let list = []
    let xMin = ctx.canvas.width
    let xMax = 0
    let yMin = ctx.canvas.height
    let yMax = 0

    for (let i = 0; i < pixels.length; i += 4) {
      let r = pixels[i]
      let g = pixels[i + 1]
      let b = pixels[i + 2]

      // RGB: 0, 0, 0
      if (!(r === 0 && g === 0 && b === 0)) {
        let x = (i / 4) % ctx.canvas.width
        let y = Math.floor(i / (4 * width))
        let t = [r, g, b]
        list = list.concat(t)
        xMin = Math.min(xMin, x)
        xMax = Math.max(xMax, x)
        yMin = Math.min(yMin, y)
        yMax = Math.max(yMax, y)
      }
    }

    let result = new Uint8Array(list.length)
    let w = xMax - xMin
    let h = yMax - yMin
    result.set(list)

    return {
      x: xMin,
      y: yMin,
      width: w,
      height: h,
      data: result
    }
  }
  // 
  getIconArray(list) {
    for (let i = 0; i < list.length; i += 4) {
      const r = list[i]
      const g = list[i + 1]
      const b = list[i + 2]
      // const max = Math.max(r, g, b);
      // list[i + 3] = max;
      // RGB: 0, 0, 0
      if (r === 0 && g === 0 && b === 0) list[i + 3] = 0
    }

    return list
  }
  // 
  init() {
    return new Promise((resolve, reject) => {
      if (!this.imageSrc) return resolve({})

      let image = new Image()
      // canvas
      image.crossOrigin = 'Anonymous'
      image.src = this.imageSrc

      image.onload = () => {
        let { canvas: allCanvas, ctx: allCtx } = this.createCanvas(image.width, image.height)
        // 
        allCtx.drawImage(image, 0, 0)

        // 
        let { canvas: bgCanvas, ctx: bgCtx } = this.createCanvas(image.width, image.height / 2)
        let bgData = allCtx.getImageData(0, 0, image.width, image.height / 2)
        bgCtx.putImageData(bgData, 0, 0)

        // icon
        const box = this.getElementCoordinates(allCtx, image.width, image.height)
        let { canvas: iconCanvas, ctx: iconCtx } = this.createCanvas(box.width, box.height)
        let iconData = allCtx.getImageData(box.x, image.height / 2 + box.y, box.width, box.height)
        // 
        // iconData.data = this.getIconArray(iconData.data);
        // iconData.data.set(this.getIconArray(iconData.data));
        iconCtx.putImageData(iconData, 0, 0)
        // 
        iconCanvas.imageSmoothingEnabled = true
        iconCanvas.imageSmoothingQuality = 'high'

        return resolve({
          bg: {
            canvas: bgCanvas,
            width: image.width,
            height: image.height / 2
          },
          icon: {
            canvas: iconCanvas,
            width: box.width,
            height: box.height,
            x: box.x,
            y: box.y
          }
        })
      }
    })
  }
}

export default handleImg
