<template>
  <div v-zinx v-if="state">
    <div class="wrapper-mask"></div>
    <div class="version_box_comp" :style="`top: ${top};`">
      <div ref="versionBoxRef" class="version_box">
        <div class="version_box_title" :class="`text_${this.align}`">
          <div class="version_box_text line_3 text_1">
            {{ title }}
          </div>
          <template v-if="!modeBool">
            <icon-font
              class="close_icon cursor_point"
              name="icon-Icon_Cloose"
              :dark="commonStyle(isApp ? 'version_app_close_color' : 'version_pc_close_color')"
              :size="commonStyle(isApp ? 'version_app_close_size' : 'version_pc_close_size')"
              @click="onCancel"
            >
            </icon-font>
          </template>
        </div>
        <div class="version_box_content w_100">
          <div
            class="version_content_text text_center line_3 c_box"
            :style="!title ? 'margin-top: 16px;' : ''"
            v-html="contentText"
          ></div>
          <div class="version_btn">
            <v-app v-if="!modeBool" light style="background: transparent">
              <v-btn
                class="cancel_btn btn_radius"
                :color="commonStyle(isApp ? 'version_app_btn_cancel_color' : 'version_pc_btn_cancel_color')"
                @click="onCancel"
              >
                {{ cancelText || $t('General.General_2') }}
              </v-btn>
            </v-app>
            <v-app light style="background: transparent">
              <v-btn
                class="submit_btn btn_radius"
                :class="{ interval: !modeBool }"
                :color="commonStyle(isApp ? 'version_app_btn_ok_color' : 'version_pc_btn_ok_color')"
                @click="onSubmit"
              >
                {{ submitText || $t('General.General_1') }}
              </v-btn>
            </v-app>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { getEeplace } from '@/utils/helper'
import { POP_TIME } from '@/api/setting'
import _common from '@style/components/dialog.modules.scss'

export default {
  props: {
    modeBool: {
      // 
      type: Boolean,
      default: false
    },
    top: {
      // 
      type: [Number, String],
      default: '50%'
    },
    title: {
      // 
      type: String,
      default: ''
    },
    align: {
      // 
      type: String,
      default() {
        return this.isApp ? _common.version_app_title_align : _common.version_pc_title_align
      }
    },
    content: {
      // 
      type: String,
      default: ''
    },
    submitText: {
      // 
      type: String,
      default: ''
    },
    cancelText: {
      // 
      type: String,
      default: ''
    },
    submit: {
      // 
      type: Function,
      default() {
        return () => {}
      }
    },
    cancel: {
      // 
      type: Function,
      default() {
        return () => {}
      }
    }
  },
  data() {
    return {
      state: false
    }
  },
  computed: {
    ...mapState('base', ['isApp']),
    ...mapState('dialog', ['popupState']),
    // 
    contentText() {
      return this.content ? getEeplace(this.content) : ''
    }
  },
  watch: {
    // 
    popupState(n) {
      if (!n) {
        this.state = false
        if (this.remove) this.remove()
      }
    }
  },
  methods: {
    commonStyle: k => _common[k],
    // 
    show() {
      this.state = !this.state
      this.$nextTick(() => {
        const versionBoxRef = this.$refs['versionBoxRef']
        if (versionBoxRef) this.$anim.insert(versionBoxRef)
      })

      return Promise.resolve(this)
    },
    // 
    hide(callback) {
      this.$nextTick(() => {
        const versionBoxRef = this.$refs['versionBoxRef']
        if (versionBoxRef) this.$anim.close(versionBoxRef)

        setTimeout(() => {
          this.state = false
          if (this.remove) this.remove()
          if (typeof callback == 'function') callback()
        }, POP_TIME)
      })
    },
    // 
    onSubmit() {
      if (this.submit) this.submit()
    },
    // 
    onCancel() {
      if (this.cancel) this.cancel()
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/dialog.modules.scss';

// 
.version_box_comp {
  width: $version_app_width;
  position: fixed;
  left: $version_app_fixed_left;
  transform: translateY(-50%);
  .version_box {
    padding: $version_app_box_padding;
    background: $version_app_bg;
    border-radius: $version_app_radius;
    @include setBox(column, flex-start, center);
    .version_box_title {
      width: $version_app_box_title_width;
      position: relative;
      .version_box_text {
        min-height: $version_app_box_title_height;
        color: $version_app_title_color;
        font-size: $version_app_box_title_size;
        font-weight: $version_app_box_title_bold;
      }
      .close_icon {
        position: absolute;
        right: $version_app_box_title_icon_right;
        top: $version_app_box_title_icon_top;
        transform: translateY(-50%);
      }
    }
    .version_content_text {
      color: $version_app_content_color;
      line-height: $version_app_content_line;
      font-size: $version_app_content_size;
      font-weight: $version_app_content_bold;
      margin: $version_app_content_margin;
    }
  }
  .version_btn {
    @include setBox(row, space-between, center);
    width: $version_app_btn_width;
    margin: $version_app_btn_margin;
    .cancel_btn,
    .submit_btn {
      height: $version_app_btn_height;
      font-size: $version_app_btn_size;
      font-weight: $version_app_btn_bold;
      &:hover {
        @include setFilter(contrast(120%) drop-shadow(0px 0px 4px #ffffff), $btn_hover_filter_type);
      }
      &.interval {
        margin: $version_app_btn_submit_btn_margin;
      }
    }
  }
}
// pc
@include breakpoint-only-up() {
  .version_box_comp {
    width: $version_pc_width;
    position: fixed;
    left: $version_pc_fixed_left;
    transform: translate(-50%, -50%);
    .version_box {
      padding: $version_pc_box_padding;
      background: $version_pc_bg;
      border-radius: $version_pc_radius;
      @include setBox(column, flex-start, center);
      .version_box_title {
        width: $version_pc_box_title_width;
        min-height: $version_pc_box_title_height;
        position: relative;
        .version_box_text {
          color: $version_pc_title_color;
          font-size: $version_pc_box_title_size;
          font-weight: $version_pc_box_title_bold;
        }
        .close_icon {
          position: absolute;
          right: $version_pc_box_title_icon_right;
          top: $version_pc_box_title_icon_top;
          transform: translateY(-50%);
        }
      }
      .version_content_text {
        color: $version_pc_content_color;
        line-height: $version_pc_content_line;
        font-size: $version_pc_content_size;
        font-weight: $version_pc_content_bold;
        margin: $version_pc_content_margin;
      }
    }
    .version_btn {
      @include setBox(row, space-between, center);
      width: $version_pc_btn_width;
      margin: $version_pc_btn_margin;
      .cancel_btn,
      .submit_btn {
        height: $version_pc_btn_height;
        font-size: $version_pc_btn_size;
        font-weight: $version_pc_btn_bold;
        &.interval {
          margin: $version_pc_btn_submit_btn_margin;
        }
      }
    }
  }
}
</style>
