<template>
  <div class="list_wrap">
    <div class="tips_message_box c_box">
      <div class="avatar">
        <img :src="loadIcon('kefu_avatar', false)" />
      </div>
      <div class="message">
        {{ $t('UI_KEFU_doubt') }}
      </div>
    </div>
    <div class="column_flex content">
      <div
        class="content__main w_100"
        @touchstart="$emit('touchstart', $event)"
        @touchmove="$emit('touchmove', $event)"
      >
        <div
          v-for="{ id, name } in list"
          :key="id"
          class="content__main__btn text_center c_box cursor_point"
          @click="$emit('click', id)"
        >
          {{ name }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import loadIcon from '@/assets/icons'
export default {
  props: {
    list: {
      type: Array,
      default: () => []
    }
  },
  computed: {
    ...mapState('base', ['isApp'])
  },
  methods: {
    loadIcon: key => loadIcon(key, false)
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/dialog.kefu.modules.scss';
.list_wrap {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.tips_message_box {
  padding: $kefu_message_padding;
  .avatar {
    margin: $kefu_message_avatar_margin;
    img {
      width: $kefu_message_avatar_width;
    }
  }
  .message {
    font-size: $kefu_message_font_size;
    font-weight: $kefu_message_font_weight;
    line-height: $kefu_message_line_height;
    color: #fff;
  }
}
.content {
  overflow: auto;
  flex-grow: 1;
  &__main {
    @include setBox(row, flex-start, center, wrap);
    &__btn {
      height: $problem_list_btn_height;
      width: $problem_list_btn_width;
      margin: $problem_list_btn_margin;
      background: $problem_list_btn_bg;
      border-radius: $problem_list_btn_radius;
      color: $problem_list_btn_color;
      font-size: $problem_list_btn_font_size;
      padding: $problem_list_btn_padding;
      font-weight: $problem_list_btn_font_weight;
      &:nth-child(-n + 2) {
        margin-top: 0 !important;
      }
      &:nth-child(even) {
        margin-right: 0 !important;
      }
    }
  }
}
</style>
