<template>
  <div v-zinx class="comp birthday" v-if="state">
    <div class="wrapper-mask" @click="onCancel"></div>
    <div class="birthday_box_comp" :style="`top: ${top};`">
      <div ref="birthdayBoxRef" class="birthday_box">
        <div class="birthday_box_title">
          <div class="birthday_box_text line_3 text_1">
            {{ title || $t('UI_Edit_my_birthday') }}
          </div>
          <icon-font
            class="close_icon cursor_point"
            name="icon-Icon_Cloose"
            :dark="modulesStyle('birthday_icon_color')"
            :size="modulesStyle('birthday_icon_size')"
            @click="onCancel"
          >
          </icon-font>
        </div>
        <div class="birthday_box_content">
          <div class="year" @click="onSelece('year')" v-zoom>
            <span class="text">{{ year }}</span>
            <span class="down" :class="{ active: dateSign == 'year' }"></span>
          </div>
          <div class="month" @click="onSelece('month')" v-zoom>
            <span class="text">{{ month }}</span>
            <span class="down" :class="{ active: dateSign == 'month' }"></span>
          </div>
          <div class="sun" @click="onSelece('sun')" v-zoom>
            <span class="text">{{ sun }}</span>
            <span class="down" :class="{ active: dateSign == 'sun' }"></span>
          </div>

          <ul v-show="dateState" class="date_ul">
            <li
              :id="`birthdayLi${item}`"
              class="date_li"
              :class="{
                active:
                  (dateSign == 'year' && item == year) ||
                  (dateSign == 'month' && item == month) ||
                  (dateSign == 'sun' && item == sun)
              }"
              v-for="item in dateList"
              :key="item"
              @click="onChange(item)"
            >
              {{ item }}
            </li>
          </ul>
        </div>
        <div class="birthday_box_btn btn_radius" v-zoom @click="onSubmit">
          {{ $t('UI_Plinko_Enter') }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { POP_TIME } from '@/api/setting'
import _common from '@style/components/dialog.modules.scss'

export default {
  props: {
    top: {
      type: [Number, String],
      default: '50%'
    },
    title: {
      type: String,
      default: ''
    },
    success: {
      // 
      type: Function,
      default() {
        return () => {}
      }
    },
    error: {
      // 
      type: Function,
      default() {
        return () => {}
      }
    }
  },
  data() {
    return {
      state: false,
      dateState: false, // 
      dateSign: '', // 
      year: '2000', // 
      month: '06', // 
      sun: '15' // 
    }
  },
  computed: {
    ...mapState('base', ['isApp']),
    ...mapState('dialog', ['popupState']),
    // 
    dateList() {
      // 100 
      if (this.dateSign == 'year') {
        let start = new Date().setFullYear(new Date().getFullYear() - 100)
        start = new Date(start).getFullYear()
        const arr = new Array(100)
          .toString()
          .split(',')
          .map((item, index) => start + index + 1)
        return [start, ...arr].sort((a, b) => b - a)
      }
      // 
      if (this.dateSign == 'month') {
        return ['01', '02', '03', '04', '05', '06', '07', '08', '09', '10', '11', '12']
      }
      // 
      if (this.dateSign == 'sun') {
        const day = new Date(this.year, this.month, 0)
        const count = day.getDate()
        const arr = new Array(Number(count))
          .toString()
          .split(',')
          .map((item, index) => {
            if (index + 1 < 10) return `0${index + 1}`
            return index + 1
          })

        return arr
      }

      return []
    }
  },
  watch: {
    // 
    popupState(n) {
      if (!n) {
        this.state = false
        if (this.remove) this.remove()
      }
    }
  },
  methods: {
    modulesStyle: k => _common[k],
    show() {
      this.state = !this.state
      this.$nextTick(() => {
        const birthdayBoxRef = this.$refs['birthdayBoxRef']
        if (birthdayBoxRef) this.$anim.insert(birthdayBoxRef)
      })

      return Promise.resolve(this)
    },
    hide(callback) {
      this.$nextTick(() => {
        const birthdayBoxRef = this.$refs['birthdayBoxRef']
        if (birthdayBoxRef) this.$anim.close(birthdayBoxRef)

        setTimeout(() => {
          this.state = false
          if (this.remove) this.remove()
          if (typeof callback == 'function') callback()
        }, POP_TIME)
      })
    },
    // 
    onSelece(sign = '') {
      if (!sign) return

      this.dateSign = sign
      this.dateState = !this.dateState

      this.$nextTick(() => {
        const birthdayLiId = `birthdayLi${sign == 'year' ? this.year : sign == 'month' ? this.month : sign == 'sun' ? this.sun : ''}`
        const dom = document.getElementById(birthdayLiId)
        // 
        dom.scrollIntoView({ block: 'center', inline: 'center' })
      })
    },
    // 
    onChange(num = '') {
      if (!num) return

      if (this.dateSign == 'year') this.year = num
      if (this.dateSign == 'month') {
        this.month = num
        this.sun = '01'
      }
      if (this.dateSign == 'sun') this.sun = num
      this.dateState = !this.dateState
    },
    // 
    onSubmit() {
      let time = `${this.year}/${this.month}/${this.sun}`
      time = new Date(time).getTime()

      this.hide(() => {
        this.success(time)
      })
    },
    // 
    onCancel() {
      let time = `${this.year}/${this.month}/${this.sun}`
      time = new Date(time).getTime()

      this.hide(() => {
        this.error(time)
      })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/dialog.modules.scss';

// 
.birthday_box_comp {
  position: fixed;
  width: $birthday_width_app;
  left: $birthday_left_app;
  transform: $birthday_transform_app;
  .birthday_box {
    padding: $birthday_box_padding_app;
    background-color: $birthday_box_bg_app;
    border-radius: $birthday_box_radius_app;
    user-select: none;
    .birthday_box_title {
      width: $birthday_box_title_width_app;
      @include setBox(row, space-between, center);
      .birthday_box_text {
        flex-grow: 1;
        flex-shrink: 1;
        font-size: $birthday_box_title_size_app;
        font-weight: $birthday_box_title_weight_app;
        color: $birthday_box_title_color_app;
      }
      .close_icon {
        flex-grow: 0;
        flex-shrink: 0;
      }
    }
    .birthday_box_content {
      margin: $birthday_box_content_padding_app;
      @include setBox(row, space-between, center);
      position: relative;
      .year,
      .month,
      .sun {
        height: $birthday_box_content_zi_height_app;
        border-radius: $birthday_box_content_zi_radius_app;
        padding: $birthday_box_content_zi_padding_app;
        background: $birthday_box_content_zi_bg_app;
        @include setBox(row, center, center);
        cursor: pointer;
        .text {
          font-size: $birthday_box_content_zi_text_size_app;
          font-weight: $birthday_box_content_zi_text_weight_app;
          color: $birthday_box_content_zi_text_color_app;
        }
        .down {
          border-top: $birthday_box_content_zi_down_bt_app;
          border-right: $birthday_box_content_zi_down_br_app;
          border-left: $birthday_box_content_zi_down_bl_app;
          border-bottom: $birthday_box_content_zi_down_bb_app;
          margin: $birthday_box_content_zi_down_margin_app;
          position: relative;
          top: $birthday_box_content_zi_down_top_app;
        }
      }
      .date_ul {
        max-height: $birthday_box_content_zi_ul_min_height_app;
        overflow-y: auto;
        position: absolute;
        left: $birthday_box_content_zi_ul_left_app;
        right: $birthday_box_content_zi_ul_right_app;
        top: $birthday_box_content_zi_ul_top_app;
        z-index: 9;
        border-radius: $birthday_box_content_zi_ul_radius_app;
        background: $birthday_box_content_zi_ul_bg_app;
        .date_li {
          padding: $birthday_box_content_zi_li_padding_app;
          font-size: $birthday_box_content_zi_li_size_app;
          font-weight: $birthday_box_content_zi_li_weight_app;
          color: $birthday_box_content_zi_li_color_app;
          &.active {
            background: $birthday_box_content_zi_li_active_bg_app;
            color: $birthday_box_content_zi_li_active_color_app;
          }
        }
      }
    }
    .birthday_box_btn {
      height: $birthday_box_btn_height_app;
      margin: $birthday_box_btn_margin_app;
      background: $birthday_box_btn_bg_app;
      @include setBox(row, center, center);
      font-size: $birthday_box_btn_size_app;
      font-weight: $birthday_box_btn_weight_app;
      color: $birthday_box_btn_color_app;
    }
  }
}

// pc
@include breakpoint-only-up() {
  .birthday_box_comp {
    width: $birthday_width_pc;
    left: $birthday_left_pc;
    transform: $birthday_transform_pc;
    .birthday_box {
      padding: $birthday_box_padding_pc;
      background-color: $birthday_box_bg_pc;
      border-radius: $birthday_box_radius_pc;
      .birthday_box_title {
        width: $birthday_box_title_width_pc;
        .birthday_box_text {
          flex-grow: 1;
          flex-shrink: 1;
          font-size: $birthday_box_title_size_pc;
          font-weight: $birthday_box_title_weight_pc;
          color: $birthday_box_title_color_pc;
        }
        .close_icon {
          flex-grow: 0;
          flex-shrink: 0;
        }
      }
      .birthday_box_content {
        margin: $birthday_box_content_padding_pc;
        .year,
        .month,
        .sun {
          height: $birthday_box_content_zi_height_pc;
          border-radius: $birthday_box_content_zi_radius_pc;
          padding: $birthday_box_content_zi_padding_pc;
          background: $birthday_box_content_zi_bg_pc;
          .text {
            font-size: $birthday_box_content_zi_text_size_pc;
            font-weight: $birthday_box_content_zi_text_weight_pc;
            color: $birthday_box_content_zi_text_color_pc;
          }
          .down {
            border-top: $birthday_box_content_zi_down_bt_pc;
            border-right: $birthday_box_content_zi_down_br_pc;
            border-left: $birthday_box_content_zi_down_bl_pc;
            border-bottom: $birthday_box_content_zi_down_bb_pc;
            margin: $birthday_box_content_zi_down_margin_pc;
            top: $birthday_box_content_zi_down_top_pc;
          }
        }
        .date_ul {
          max-height: $birthday_box_content_zi_ul_min_height_pc;
          left: $birthday_box_content_zi_ul_left_pc;
          right: $birthday_box_content_zi_ul_right_pc;
          top: $birthday_box_content_zi_ul_top_pc;
          z-index: 9;
          border-radius: $birthday_box_content_zi_ul_radius_pc;
          background: $birthday_box_content_zi_ul_bg_pc;
          .date_li {
            padding: $birthday_box_content_zi_li_padding_pc;
            font-size: $birthday_box_content_zi_li_size_pc;
            font-weight: $birthday_box_content_zi_li_weight_pc;
            color: $birthday_box_content_zi_li_color_pc;
            &.active {
              background: $birthday_box_content_zi_li_active_bg_pc;
              color: $birthday_box_content_zi_li_active_color_pc;
            }
          }
        }
      }
      .birthday_box_btn {
        height: $birthday_box_btn_height_pc;
        margin: $birthday_box_btn_margin_pc;
        background: $birthday_box_btn_bg_pc;
        font-size: $birthday_box_btn_size_pc;
        font-weight: $birthday_box_btn_weight_pc;
        color: $birthday_box_btn_color_pc;
      }
    }
  }
}
</style>
