<template>
  <div v-zinx v-if="visible" class="comp lucky_reward">
    <div class="lucky_reward_mask"></div>
    <div id="luckyResultId" class="lucky_reward_comp">
      <div
        class="lucky_reward_box"
        :style="loadIcon(isApp ? 'game_lucky_reward_bg_app' : 'game_lucky_reward_bg_pc', true)"
      >
        <img
          class="ani_img"
          :class="animation ? 'show' : 'close'"
          :src="loadIcon(isApp ? 'game_lucky_reward_bg_ani_app' : 'game_lucky_reward_bg_ani_pc')"
          alt=""
        />
        <div id="rewardBoxId" class="reward_box" :class="animation ? 'show' : 'close'">
          <img
            class="win_img"
            :src="loadIcon(isApp ? 'game_lucky_reward_text_app' : 'game_lucky_reward_text_pc')"
            alt=""
          />
          <div class="reward_body">
            <div
              class="reward_money"
              :style="loadIcon(isApp ? 'game_lucky_money_bg_app' : 'game_lucky_money_bg_pc', true)"
            >
              <img
                class="img"
                :src="loadIcon(isApp ? 'game_lucky_reward_species_app' : 'game_lucky_reward_species_pc')"
                alt=""
              />
              <span class="text">{{ info.gold }}</span>
            </div>
            <div class="reward_text">
              {{ $t('UI.Lucky_Reward_Text') }}
            </div>
            <div class="reward_btn btn_radius line_1" v-zoom @click="onClose">
              {{ $t('UI.Lucky_Reward_Btn_Text') }}
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import loadIcon from '@/assets/icons'
import _common from '@style/components/dialog.lucky.modules.scss'

export default {
  data() {
    return {
      visible: false,
      animation: false, // /
      info: {} // 
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'systemConfig', 'languageType'])
  },
  methods: {
    commonStyle: k => _common[k],
    loadIcon: (key, bool = false) => loadIcon(key, bool),
    // 
    init() {
      this.$nextTick(() => {
        const luckyResultId = document.getElementById('luckyResultId')
        if (!luckyResultId) return

        const pare = this.commonStyle(this.isApp ? 'lucky_reward_comp_app_rtion' : 'lucky_reward_comp_pc_rtion')
        const luckyResultId_w = luckyResultId.clientWidth || luckyResultId.offsetWidth
        luckyResultId.style.height = `${luckyResultId_w / pare}px`

        const winImg = luckyResultId.querySelectorAll('.win_img')[0]
        const pare_win = this.commonStyle(
          this.isApp ? 'lucky_reward_comp_win_img_app_rtion' : 'lucky_reward_comp_win_img_pc_rtion'
        )
        const winImg_w = winImg.clientWidth || winImg.offsetWidth
        const winImg_h = winImg_w / pare_win
        winImg.style.height = `${winImg_h}px`

        const rewardMoney = luckyResultId.querySelectorAll('.reward_money')[0]
        const pare_money = this.commonStyle(
          this.isApp ? 'lucky_reward_comp_pare_money_app_rtion' : 'lucky_reward_comp_pare_money_pc_rtion'
        )
        const rewardMoney_w = rewardMoney.clientWidth || rewardMoney.offsetWidth
        const rewardMoney_h = rewardMoney_w / pare_money
        rewardMoney.style.height = `${rewardMoney_h}px`
      })
    },
    // 
    open(data = {}) {
      // 
      this.animation = true

      this.info = data
      this.visible = true

      this.$nextTick(() => {
        const rewardBoxId = document.getElementById('rewardBoxId')
        if (rewardBoxId) {
          rewardBoxId.removeEventListener('animationend', this.luckyRewardFun ? this.luckyRewardFun : () => {})
        }

        this.init()
      })
    },
    // 
    onClose() {
      // 
      this.animation = false

      // 
      this.$nextTick(() => {
        const rewardBoxId = document.getElementById('rewardBoxId')
        if (!rewardBoxId) return (this.visible = false)

        rewardBoxId.removeEventListener('animationend', this.luckyRewardFun ? this.luckyRewardFun : () => {})

        this.luckyRewardFun = () => {
          this.visible = false
        }
        rewardBoxId.addEventListener('animationend', this.luckyRewardFun)
      })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/dialog.lucky.modules.scss';

.comp.lucky_reward {
  .lucky_reward_mask {
    position: $lucky_reward_mask_position;
    top: $lucky_reward_mask_top;
    right: $lucky_reward_mask_right;
    left: $lucky_reward_mask_left;
    bottom: $lucky_reward_mask_bottom;
    background-color: $lucky_reward_mask_bg;
  }
  // 
  .lucky_reward_comp {
    width: $lucky_reward_app_width;
    position: fixed;
    top: $lucky_reward_app_top;
    left: $lucky_reward_app_left;
    right: $lucky_reward_app_right;
    transform: $lucky_reward_app_transform;
    .lucky_reward_box {
      width: $lucky_reward_box_app_width;
      height: $lucky_reward_box_app_height;
      background-size: $lucky_reward_box_app_bg_size;
      background-repeat: $lucky_reward_box_app_bg_repeat;
      @include setBox(column, center, center);
      position: relative;
      .ani_img {
        width: $lucky_reward_box_img_app_width;
        height: $lucky_reward_box_img_app_height;
        position: absolute;
        top: $lucky_reward_box_img_app_top;
        left: $lucky_reward_box_img_app_left;
        right: $lucky_reward_box_img_app_right;
        bottom: $lucky_reward_box_img_app_bottom;
        &.show {
          animation: lucky-reward-ani-show linear 1.5s 1 both;
        }

        &.close {
          animation: lucky-reward-ani-close linear 1.5s 1 both;
        }
      }
      .reward_box {
        width: $lucky_reward_box_body_app_width;
        &.show {
          animation: lucky-reward-box-show linear 1.5s 1 both;
        }
        &.close {
          animation: lucky-reward-box-close linear 1.5s 1 both;
        }
        .win_img {
          width: $lucky_reward_box_body_win_app_width;
        }
        .reward_body {
          width: $lucky_reward_box_body_content_app_width;
          position: relative;
          top: $lucky_reward_box_body_content_app_top;
          z-index: 1;
          @include setBox(column, center, center);
          .reward_money {
            width: $lucky_reward_box_body_content_money_app_width;
            background-size: $lucky_reward_box_body_content_money_app_bg_size;
            background-repeat: $lucky_reward_box_body_content_money_app_bg_repeat;
            @include setBox(row, center, center);
            .img {
              width: $lucky_reward_box_body_content_money_img_app_width;
            }
            .text {
              margin: $lucky_reward_box_body_content_money_text_app_margin;
              font-size: $lucky_reward_box_body_content_money_text_app_size;
              font-weight: $lucky_reward_box_body_content_money_text_app_weight;
              color: $lucky_reward_box_body_content_money_text_app_color;
            }
          }
          .reward_text {
            width: $lucky_reward_box_body_content_reward_text_app_width;
            font-size: $lucky_reward_box_body_content_reward_text_app_size;
            font-weight: $lucky_reward_box_body_content_reward_text_app_weight;
            color: $lucky_reward_box_body_content_reward_text_app_color;
            line-height: $lucky_reward_box_body_content_reward_text_app_line;
          }
          .reward_btn {
            margin: $lucky_reward_box_body_content_reward_btn_app_margin;
            padding: $lucky_reward_box_body_content_reward_btn_app_padding;
            background: $lucky_reward_box_body_content_reward_btn_app_bg;
            box-shadow: $lucky_reward_box_body_content_reward_btn_app_shadow;
            font-size: $lucky_reward_box_body_content_reward_btn_app_size;
            font-weight: $lucky_reward_box_body_content_reward_btn_app_weight;
            color: $lucky_reward_box_body_content_reward_btn_app_color;
            &:hover {
              @include setFilter(
                $lucky_reward_box_body_content_reward_btn_hover_app_filter,
                $lucky_reward_box_body_content_reward_btn_hover_app_show
              );
            }
          }
        }
      }
    }
  }
  // pc
  @include breakpoint-only-up() {
    .lucky_reward_comp {
      width: $lucky_reward_pc_width;
      min-width: $lucky_reward_pc_min_width;
      position: fixed;
      top: $lucky_reward_pc_top;
      left: $lucky_reward_pc_left;
      right: $lucky_reward_pc_right;
      transform: $lucky_reward_pc_transform;
      user-select: none;
      .lucky_reward_box {
        width: $lucky_reward_box_pc_width;
        height: $lucky_reward_box_pc_height;
        background-size: $lucky_reward_box_pc_bg_size;
        background-repeat: $lucky_reward_box_pc_bg_repeat;
        @include setBox(column, center, center);
        position: relative;
        .ani_img {
          width: $lucky_reward_box_img_pc_width;
          height: $lucky_reward_box_img_pc_height;
          position: absolute;
          top: $lucky_reward_box_img_pc_top;
          left: $lucky_reward_box_img_pc_left;
          right: $lucky_reward_box_img_pc_right;
          bottom: $lucky_reward_box_img_pc_bottom;
          &.show {
            animation: lucky-reward-ani-show linear 1.5s 1 both;
          }
          &.close {
            animation: lucky-reward-ani-close linear 1.5s 1 both;
          }
        }
        .reward_box {
          width: $lucky_reward_box_body_pc_width;
          &.show {
            animation: lucky-reward-box-show linear 1.5s 1 both;
          }
          &.close {
            animation: lucky-reward-box-close linear 1.5s 1 both;
          }
          .win_img {
            width: $lucky_reward_box_body_win_pc_width;
          }
          .reward_body {
            width: $lucky_reward_box_body_content_pc_width;
            position: relative;
            top: $lucky_reward_box_body_content_pc_top;
            z-index: 1;
            @include setBox(column, center, center);
            .reward_money {
              width: $lucky_reward_box_body_content_money_pc_width;
              background-size: $lucky_reward_box_body_content_money_pc_bg_size;
              background-repeat: $lucky_reward_box_body_content_money_pc_bg_repeat;
              @include setBox(row, center, center);
              .img {
                width: $lucky_reward_box_body_content_money_img_pc_width;
              }
              .text {
                margin: $lucky_reward_box_body_content_money_text_pc_margin;
                font-size: $lucky_reward_box_body_content_money_text_pc_size;
                font-weight: $lucky_reward_box_body_content_money_text_pc_weight;
                color: $lucky_reward_box_body_content_money_text_pc_color;
              }
            }
            .reward_text {
              width: $lucky_reward_box_body_content_reward_text_pc_width;
              font-size: $lucky_reward_box_body_content_reward_text_pc_size;
              font-weight: $lucky_reward_box_body_content_reward_text_pc_weight;
              color: $lucky_reward_box_body_content_reward_text_pc_color;
              line-height: $lucky_reward_box_body_content_reward_text_pc_line;
            }
            .reward_btn {
              margin: $lucky_reward_box_body_content_reward_btn_pc_margin;
              padding: $lucky_reward_box_body_content_reward_btn_pc_padding;
              background: $lucky_reward_box_body_content_reward_btn_pc_bg;
              box-shadow: $lucky_reward_box_body_content_reward_btn_pc_shadow;
              font-size: $lucky_reward_box_body_content_reward_btn_pc_size;
              font-weight: $lucky_reward_box_body_content_reward_btn_pc_weight;
              color: $lucky_reward_box_body_content_reward_btn_pc_color;
              cursor: pointer;
              &:hover {
                @include setFilter(
                  $lucky_reward_box_body_content_reward_btn_hover_pc_filter,
                  $lucky_reward_box_body_content_reward_btn_hover_pc_show
                );
              }
            }
          }
        }
      }
    }
  }

  @keyframes lucky-reward-ani-show {
    0% {
      transform: scale(0);
      opacity: 0;
    }

    25% {
      transform: scale(1);
      opacity: 1;
    }

    70% {
      transform: scale(0.8);
      opacity: 0.5;
    }

    100% {
      transform: scale(1);
      opacity: 1;
    }
  }
  @keyframes lucky-reward-ani-close {
    0% {
      transform: scale(1);
      opacity: 1;
    }

    25% {
      transform: scale(0.8);
      opacity: 0.5;
    }

    70% {
      transform: scale(1);
      opacity: 1;
    }

    100% {
      transform: scale(0);
      opacity: 0;
    }
  }
  @keyframes lucky-reward-box-show {
    from {
      opacity: 0;
    }

    to {
      opacity: 1;
    }
  }
  @keyframes lucky-reward-box-close {
    from {
      opacity: 1;
    }

    to {
      opacity: 0;
    }
  }
}
</style>
