<template>
  <div v-zinx v-if="state" class="comp invite_quest">
    <div class="wrapper-mask" @click="hide"></div>
    <div class="invite_quest_box">
      <div class="quest_top">
        <div class="quest_top_left">
          <div class="quest_top_img"></div>
          <div class="quest_top_text">
            {{
              $t('UI_Receive_Tit', {
                Currency: currencySymbol,
                amount: activityInvite?.activity_user_task?.total_amount
              })
            }}
          </div>
        </div>
        <icon-font
          class="share_btn_icon"
          name="icon-Icon_Cloose"
          :size="variables.invite_quest_share_btn_icon_size"
          :dark="variables.invite_quest_share_btn_icon_color"
          @click="hide"
        />
      </div>
      <div class="quest_view">
        <div class="quest_content">
          <div class="quest_content_gold">
            <div class="quest_content_gold_left">
              <img class="quest_content_gold_left_img" :src="loadIcon('refer_gift_money', false)" alt="" />
            </div>
            <div class="quest_content_gold_rigth">
              {{ currencySymbol }}
              <CountTo :startVal="startVal" :endVal="endVal" :duration="duration" :decimals="decimals" />
            </div>
          </div>
          <div class="gold_text_box">
            <div class="quest_content_img"></div>
            <div class="quest_content_acquire">{{ $t('UI_QUICK_WITHDRAWAL') }}</div>
          </div>

          <div class="quest_content_text">{{ $t('UI_good_luck') }}</div>
          <div class="quest_content_progress">
            <referProgress :progress="progressValue" :strokeHeight="12" :showInfo="false"></referProgress>
            <div class="quest_content_progress_text" v-html="$t('UI_Refer_Withdrawal_Text', { x: remainValue })"></div>
          </div>
          <v-btn class="quest_content_btn" @click="onWithdrawal">
            {{ $t('UI_Claim_more_for_withdrawal') }}
          </v-btn>
        </div>
        <div class="quest_table">
          <refer-table :amount="20" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import variables from '@style/components/dialog.modules.scss'
import referProgress from '@/components/ui/refer-activity/refer-progress'
import referTable from '@/components/ui/refer-activity/refer-table'
import { getToFixed } from '@/utils/helper'
import CountTo from 'vue-count-to'
import loadIcon from '@/assets/icons'

export default {
  props: {
    onCancel: {
      // 
      type: Function,
      default() {
        return () => {}
      }
    },
    onSubmit: {
      //
      type: Function,
      default() {
        return () => {}
      }
    }
  },
  components: {
    referProgress,
    referTable,
    CountTo
  },
  data() {
    return {
      variables,
      state: false,
      startVal: 1, // 
      duration: 1500, // 
      progressValue: 0 //
    }
  },
  computed: {
    ...mapState('dialog', ['popupState']),
    ...mapState('base', ['isApp', 'systemConfig']),
    ...mapState('source', ['activityInvite']),
    ...mapState('switchState', ['currencyShow']),
    currencySymbol() {
      return this.currencyShow ? this.systemConfig.currency_symbol : ''
    },
    // 
    remainValue() {
      let value = this.getToFixed(
        parseFloat(this.activityInvite?.activity_user_task?.total_amount) -
          parseFloat(this.activityInvite?.activity_user_task?.progress_amount)
      )
      return `<span style=\'color: ${variables.invite_quest_quest_content_progress_text_tisp_color};\'>${value}</span>`
    },
    endVal() {
      // 
      setTimeout(() => {
        this.startVal = Number(this.activityInvite?.activity_user_task?.progress_amount) || 0
      }, this.duration)
      return Number(this.activityInvite?.activity_user_task?.progress_amount) || 0
    },
    decimals() {
      const progress_amount = this.activityInvite?.activity_user_task?.progress_amount.toString()
      return progress_amount?.split('.')?.[1]?.length || 0
    }
  },
  watch: {
    // 
    popupState(n) {
      if (!n) {
        this.state = false
        if (this.remove) this.remove()
      }
    },
    activityInvite: {
      immediate: true, //
      deep: true, //
      handler: function (newV, oldV) {
        const { activity_user_task = {} } = newV
        if (activity_user_task) {
          const { progress_amount = 0, total_amount = 0 } = activity_user_task
          setTimeout(() => {
            this.progressValue = this.getToFixed(parseFloat(progress_amount / total_amount) * 100)
          }, 200)
        }
      }
    }
  },
  methods: {
    loadIcon: key => loadIcon(key, false),
    /**
     * 
     * @param {*} money 
     * @param {*} cancel 
     */
    show() {
      this.state = true
    },
    // 
    hide() {
      this.state = false
      if (this.remove) this.remove()
      this.onCancel()
    },
    getToFixed(str, nub = 2) {
      return getToFixed(str, nub)
    },
    // referGift
    onWithdrawal() {
      this.state = false
      this.onSubmit()
      this.$router.push({ name: 'referGift' })
    }
  }
}
</script>

