<template>
  <div v-zinx v-if="state">
    <div class="wrapper-mask" @click="onClose"></div>
    <div class="remind_comp" :style="`top: ${top};`">
      <div ref="remindBoxRef" class="remind_box">
        <div class="remind_title">
          <icon-font
            class="remind_title_close cursor_point"
            name="icon-Icon_Cloose"
            :size="commonStyle(isApp ? 'remind_title_close_app_size' : 'remind_title_close_pc_size')"
            :dark="commonStyle(isApp ? 'remind_title_close_app_color' : 'remind_title_close_pc_color')"
            @click="onClose"
          >
          </icon-font>
        </div>
        <div class="remind_content break_word" v-html="content || $t('UI.Remind_Content_Text')"></div>
        <div class="remind_img">
          <img :src="getCacheIcon('remind')" alt="" />
        </div>
        <div class="remind_btn">
          <div class="btn btn_Revision btn_radius" @click="onRemindSubmit" v-zoom>
            {{ $t('UI.Remind_Btn_Text') }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { POP_TIME } from '@/api/setting'
import { getCacheIcon } from '@/assets/imgCache.js'
import _common from '@style/components/dialog.modules.scss'

export default {
  props: {
    // 
    top: {
      type: [Number, String],
      default: '50%'
    },
    // 
    content: {
      type: String,
      default: ''
    },
    // 
    onSubmit: {
      type: Function,
      default: null
    },
    // 
    onCancel: {
      type: Function,
      default: null
    }
  },
  data() {
    return {
      state: false
    }
  },
  computed: {
    ...mapState('base', ['isApp']),
    ...mapState('dialog', ['popupState'])
  },
  watch: {
    // 
    popupState(n) {
      if (!n) {
        this.state = false
        if (this.remove) this.remove()
      }
    }
  },
  methods: {
    commonStyle: (k = '') => _common[k],
    getCacheIcon(name = '') {
      const result = getCacheIcon(name)
      return typeof result === 'string' ? result : result.value.src
    },
    // 
    show() {
      this.state = !this.state
      this.$nextTick(() => {
        const remindBoxRef = this.$refs['remindBoxRef']
        if (remindBoxRef) this.$anim.insert(remindBoxRef)
      })
    },
    // 
    hide(callback) {
      this.$nextTick(() => {
        const remindBoxRef = this.$refs['remindBoxRef']
        if (remindBoxRef) this.$anim.close(remindBoxRef)

        setTimeout(() => {
          this.state = false
          if (this.remove) this.remove()
          if (typeof callback == 'function') callback()
        }, POP_TIME)
      })
    },
    // 
    onRemindSubmit() {
      this.hide(() => {
        if (this.onSubmit) this.onSubmit()
      })
    },
    // 
    onClose() {
      this.hide(() => {
        if (this.onCancel) this.onCancel()
      })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/dialog.modules.scss';

// 
.remind_comp {
  width: $remind_app_width;
  position: fixed;
  left: $remind_app_left;
  transform: $remind_app_transform;
  .remind_box {
    width: $remind_box_app_width;
    background: $remind_box_app_bg;
    border-radius: $remind_box_app_radius;
    padding: $remind_box_app_padding;
    user-select: none;
    .remind_title {
      width: $remind_box_title_app_width;
      @include setBox(row, flex-end, center);
    }
    .remind_content {
      width: $remind_box_content_app_width;
      margin: $remind_box_content_app_margin;
      font-size: $remind_box_content_app_size;
      font-weight: $remind_box_content_app_weight;
      color: $remind_box_content_app_color;
      line-height: $remind_box_content_app_line;
      text-align: $remind_box_content_app_align;
    }
    .remind_img {
      width: $remind_box_img_app_width;
      margin: $remind_box_img_app_margin;
      @include setBox(row, center, center);
      img {
        width: $remind_box_img_tu_app_width;
      }
    }
    .remind_btn {
      width: $remind_box_btn_app_width;
      margin: $remind_box_btn_app_margin;
      @include setBox(row, center, center);
      .btn {
        min-width: $remind_box_btn_app_min_width;
        height: $remind_box_btn_app_height;
        padding: $remind_box_btn_app_padding;
        background: $remind_box_btn_app_bg;
        font-size: $remind_box_btn_app_size;
        font-weight: $remind_box_btn_app_weight;
        color: $remind_box_btn_app_color;
        cursor: pointer;
        @include setBox(row, center, center);
      }
    }
  }
}
// pc
@include breakpoint-only-up() {
  .remind_comp {
    width: $remind_pc_width;
    position: fixed;
    left: $remind_pc_left;
    transform: $remind_pc_transform;
    .remind_box {
      width: $remind_box_pc_width;
      background: $remind_box_pc_bg;
      border-radius: $remind_box_pc_radius;
      padding: $remind_box_pc_padding;
      user-select: none;
      .remind_title {
        width: $remind_box_title_pc_width;
        @include setBox(row, flex-end, center);
      }
      .remind_content {
        width: $remind_box_content_pc_width;
        margin: $remind_box_content_pc_margin;
        font-size: $remind_box_content_pc_size;
        font-weight: $remind_box_content_pc_weight;
        color: $remind_box_content_pc_color;
        line-height: $remind_box_content_pc_line;
        text-align: $remind_box_content_pc_align;
      }
      .remind_img {
        width: $remind_box_img_pc_width;
        margin: $remind_box_img_pc_margin;
        @include setBox(row, center, center);
        img {
          width: $remind_box_img_tu_pc_width;
        }
      }
      .remind_btn {
        width: $remind_box_btn_pc_width;
        margin: $remind_box_btn_pc_margin;
        @include setBox(row, center, center);
        .btn {
          min-width: $remind_box_btn_pc_min_width;
          height: $remind_box_btn_pc_height;
          padding: $remind_box_btn_pc_padding;
          background: $remind_box_btn_pc_bg;
          font-size: $remind_box_btn_pc_size;
          font-weight: $remind_box_btn_pc_weight;
          color: $remind_box_btn_pc_color;
          cursor: pointer;
          @include setBox(row, center, center);
        }
      }
    }
  }
}
</style>
