<template>
  <div v-zinx v-if="state">
    <div class="wrapper-mask" :style="maskStyle"></div>
    <img class="loading_box_img" :style="loadingStyle" :src="loadIcon('common_loading')" alt="" />
  </div>
</template>

<script>
import { mapState } from 'vuex'
import loadIcon from '@/assets/icons'

export default {
  props: {
    // 
    zIndex: {
      type: [String, Number],
      default: 0
    }
  },
  data() {
    return {
      state: false
    }
  },
  computed: {
    ...mapState('base', ['isApp']),
    ...mapState('dialog', ['popupState']),
    // 
    maskStyle() {
      if (!Number(this.zIndex)) return {}
      return { 'z-index': this.zIndex }
    },
    // loading
    loadingStyle() {
      if (!Number(this.zIndex)) return {}
      return { 'z-index': Number(this.zIndex) + 1 }
    }
  },
  watch: {
    // 
    popupState(n) {
      this.state = false
      if (this.remove) this.remove()
    }
  },
  methods: {
    loadIcon: key => loadIcon(key, false),
    show() {
      this.state = !this.state
    },
    hide() {
      this.state = false
      if (this.remove) this.remove()
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/dialog.modules.scss';

.loading_box_img {
  width: 70px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
// pc
@include breakpoint-only-up() {
  .loading_box_img {
    width: 100px;
  }
}
</style>
