<template>
  <div class="comp lucky_help">
    <div class="lucky_help_dialog">
      <!--  -->
      <div class="lucky_help_back">
        <span class="help_title">{{ $t('UI.Lucky_Help_Title') }}</span>
        <icon-font
          class="help_close"
          name="icon-Icon_Right"
          :size="colorStyle(isApp ? 'lucky_help_close_app_size' : 'lucky_help_close_pc_size')"
          :dark="colorStyle(isApp ? 'lucky_help_close_app_color' : 'lucky_help_close_pc_color')"
          @click="onClose"
        />
      </div>
      <!--  -->
      <div class="lucky_help_box">
        <!--  -->
        <div class="lucky_help_sign" v-html="desc"></div>
        <!--  -->
        <div v-if="allList.length > 0" class="lucky_help_li">
          <!-- <span class="lucky_help_li_title">
            {{ helpTitle?.allStr }}
          </span> -->
          <div class="lucky_help_list">
            <div class="head">
              <span class="amount">{{ $t('UI.Lucky_Help_Amount') }}</span>
              <span class="num">{{ $t('UI.Lucky_Help_Number_Spins') }}</span>
            </div>
            <div class="list">
              <div v-for="(item, i) in allList" :key="i" class="item">
                <!-- <span class="amount">{{ item.min_recharge }} - {{ item.max_recharge }}</span> -->
                <span class="amount">{{ item.min_recharge }}</span>
                <span class="num">{{ item.reward_num }}</span>
              </div>
            </div>
          </div>
        </div>
        <!--  -->
        <template v-if="todayList.length > 0">
          <div class="lucky_help_li">
            <span class="lucky_help_li_title">
              {{ helpTitle?.todayStr }}
            </span>
            <div class="lucky_help_list">
              <div class="head">
                <span class="amount">{{ $t('UI.Lucky_Help_Recharge_Amount') }}</span>
                <span class="num">{{ $t('UI.Lucky_Help_Number_Spins') }}</span>
              </div>
              <div class="list">
                <div class="item" v-for="(item, i) in todayList" :key="i">
                  <span class="amount">{{ item.recharge }}</span>
                  <span class="num">{{ item.reward_num }}</span>
                </div>
              </div>
            </div>
          </div>
        </template>
        <!--  -->
        <template v-if="yesterdayList.length > 0">
          <div class="lucky_help_li">
            <span class="lucky_help_li_title">
              {{ helpTitle?.yesterdayStr }}
            </span>
            <div class="lucky_help_list">
              <div class="head">
                <span class="amount">{{ $t('UI.Lucky_Help_Recharge_Amount') }}</span>
                <span class="num">{{ $t('UI.Lucky_Help_Number_Spins') }}</span>
              </div>
              <div class="list">
                <div class="item" v-for="(item, i) in yesterdayList" :key="i">
                  <span class="amount">{{ item.recharge }}</span>
                  <span class="num">{{ item.reward_num }}</span>
                </div>
              </div>
            </div>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { getLuckyHelp } from '@/api/modules/game.js'
import _color from '@style/components/dialog.lucky.modules.scss'

export default {
  props: {
    // 
    desc: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      info: {} // 
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'languageType']),
    // 
    helpTitle() {
      const { title = {} } = this.info
      return title
    },
    // 
    allList() {
      const { all = [] } = this.info
      return all
    },
    // 
    todayList() {
      const { today = [] } = this.info
      return today
    },
    // 
    yesterdayList() {
      const { yesterday = [] } = this.info
      return yesterday
    }
  },
  created() {
    this.getHelp()
  },
  methods: {
    colorStyle: k => _color[k],
    // 
    async getHelp() {
      const params = {
        token: this.user.token,
        language: this.languageType
      }
      const res = await getLuckyHelp(params).catch(e => e)
      if (!res || res.code != 0) return

      this.info = res.data && this.$u.lodash.isObject(res.data) ? res.data : {}
      console.log('this.info', this.info)
    },
    // 
    onClose() {
      this.info = {}
      this.$emit('helperClose')
    }
  }
}
</script>

