<template>
  <div v-if="state" v-zinx="{ isScroll: false }">
    <div class="message_box">
      <div v-for="item in list" :key="item.id" class="message_item">
        <div v-if="item.show" :ref="`messageRef_${item.id}`" class="message_content">
          {{ item.text }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { DELAY_TIME, MESSAGE_POP_TIME } from '@/api/setting'

export default {
  data() {
    return {
      state: false,
      // message
      list: [
        // {text: 'msg1', show: true, time: '/s'},
        // {text: 'msg2', show: true, time: '/s'},
      ]
    }
  },
  computed: {
    ...mapState('base', ['isApp']),
    ...mapState('dialog', ['popupState'])
  },
  watch: {
    // 
    popupState(n) {
      this.state = false
      if (this.remove) this.remove()
    }
  },
  methods: {
    // 
    updateMsg(text = '', time = 0) {
      const id = this.list.length + 1
      this.list.unshift({ id, text, show: true, time })

      this.$nextTick(() => {
        const msgItem = this.list.find(f => f.id == id)
        if (msgItem) {
          const messageRef = this.$refs[`messageRef_${msgItem.id}`][0]
          if (messageRef) this.$anim.insertMes(messageRef)

          setTimeout(() => {
            if (messageRef) this.$anim.closeMes(messageRef)
            setTimeout(() => {
              msgItem.show = false

              // 
              const showList = this.list.filter(f => f.show)
              if (!showList.length) this.hide()
            }, MESSAGE_POP_TIME)
          }, msgItem.time)
        }
      })
    },
    // 
    show({ content = '', timeout = DELAY_TIME }) {
      if (!this.state) this.state = !this.state

      this.updateMsg(content, timeout)
    },
    // 
    hide() {
      this.state = false
      this.list = []
      if (this.remove) this.remove()
    },
    info(mes) {
      this.mes = mes
    },
    error(mes) {
      this.mes = mes
    },
    warning(mes) {
      this.mes = mes
    },
    success(mes) {
      this.mes = mes
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/dialog.modules.scss';

// 
.message_box {
  width: $message_box_width_app;
  position: fixed;
  left: $message_box_left_app;
  top: $message_box_top_app;
  transform: $message_box_transform_app;
  @include setBox(column, center, center);
  gap: 4px 0;
  .message_item {
    width: 100%;
    .message_content {
      width: $message_box_content_width_app;
      padding: $message_box_content_padding_app;
      border-radius: $message_box_content_radius_app;
      color: $message_box_content_color_app;
      font-size: $message_box_content_size_app;
      font-weight: $message_box_content_weight_app;
      line-height: $message_box_content_line_app;
      background: $message_box_content_bg_app;
      text-align: $message_box_content_align_app;
      word-break: break-all;
      white-space: pre-wrap;
    }
  }
}
// pc
@include breakpoint-only-up() {
  .message_box {
    width: $message_box_width_pc;
    top: $message_box_top_pc;
    gap: 8px 0;
    .message_content {
      width: $message_box_content_width_pc;
      padding: $message_box_content_padding_pc;
      border-radius: $message_box_content_radius_pc;
      color: $message_box_content_color_pc;
      font-size: $message_box_content_size_pc;
      font-weight: $message_box_content_weight_pc;
      line-height: $message_box_content_line_pc;
      background: $message_box_content_bg_pc;
      word-break: break-all;
    }
  }
}
</style>
