<template>
  <div class="page problem_suporte">
    <h1 class="main_title">
      <span>{{ $t('BemVindal') }} </span>
      <icon-font name="icon-icon_service_hi" :size="variables.problem_suporte_service_hi_size" class="icon"></icon-font>
    </h1>
    <div class="sub_title">{{ $t('Voce') }}</div>
    <section class="cursor_point card_box radius" v-for="item in list" :key="item.nickname" @click="goLink(item)">
      <div class="header">
        <div class="title">{{ $t('manual_title') }}</div>
        <div class="center">
          <img :src="item.headimg" class="avatar" />
          <div class="right">
            <div class="top">{{ item.nickname }}</div>
            <div class="bottom">
              <img :src="item.icon" />
              <span>{{ item.whatsapp }}</span>
            </div>
          </div>
        </div>
      </div>
      <div class="footer_wrap">
        <v-btn class="footer_wrap-btn w_100 btn_radius" color="t_btn_host_color">
          <icon-font name="icon-Icon_Service2" :size="variables.problem_suporte_service2_size" class="icon"></icon-font>
          <span class="text">{{ $t('kefu_Send') }} </span>
        </v-btn>
      </div>
    </section>
    <div class="iframe_box" v-show="kf_chat_url" :style="getPosition">
      <v-app-bar color="#fff" dense>
        <icon-font
          class="close_icon cursor_point"
          name="icon-Icon_Cloose"
          dark="t_text_color_2"
          :size="variables.problem_header_close_size"
          @click="close"
        >
        </icon-font>
      </v-app-bar>
      <iframe :src="kf_chat_url" style="width: 100%; height: calc(100% - 48px); background: #fff" frameborder="0" />
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import loadIcon from '@/assets/icons'
import variables from '@style/components/dialog.kefu.modules.scss'
export default {
  props: {
    list: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      variables,
      kf_chat_url: ''
    }
  },
  computed: {
    ...mapState('base', ['isApp']),
    getPosition() {
      if (!this.isApp) {
        return {
          width: '376px',
          height: '682px',
          borderRadius: '8px'
        }
      }
    }
  },
  methods: {
    loadIcon: key => loadIcon(key, false),
    goLink(item) {
      // 
      const { is_small = 0 } = item
      if (is_small) {
        this.kf_chat_url = item.kf_chat_url
      } else {
        setTimeout(() => window.open(item.kf_chat_url, '_blank'))
      }
    },
    getIconSizeFun(icon) {
      return _scssVariables.kefu.problemSuporte[icon]
    },
    close() {
      this.kf_chat_url = ''
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/dialog.kefu.modules.scss';

.page.problem_suporte {
  .main_title {
    display: flex;
    align-items: center;
    margin: $problem_suporte_main_title_margin;
    span {
      font-size: $problem_suporte_main_title_font_size;
      font-weight: $problem_suporte_main_title_font_weight;
      color: #fff;
    }
    .icon {
      margin: $problem_suporte_main_title_icon_margin;
    }
  }
  .sub_title {
    margin: $problem_suporte_sub_title_margin;
    line-height: $problem_suporte_sub_title_line_height;
    font-weight: $problem_suporte_sub_title_font_weight;
    font-size: $problem_suporte_sub_title_font_size;
    color: #fff;
  }
  .card_box {
    & + .card_box {
      margin: $problem_suporte_card_box_margin;
    }
    background: $problem_suporte_card_box_bg;
    font-weight: $problem_suporte_card_box_font_weight;
    &.radius {
      border-radius: $problem_suporte_card_box_app_radius;
    }
    .header {
      padding: $problem_suporte_card_box_content_padding;
      border-bottom: thin solid $problem_suporte_card_box_content_border_bottom_color;
      .title {
        font-size: $problem_suporte_card_box_content_title_font_size;
        color: #fff;
      }
      .center {
        display: flex;
        margin: $problem_suporte_card_box_content_center_margin;
        .right {
          display: flex;
          flex-direction: column;
          justify-content: space-around;
          color: $problem_suporte_card_box_content_right_color;
          margin: $problem_suporte_card_box_content_right_margin;
          font-size: $problem_suporte_card_box_content_right_font_size;
          .bottom {
            display: flex;
            align-items: center;
            img {
              width: $problem_suporte_card_box_content_right_icon_size;
              height: $problem_suporte_card_box_content_right_icon_size;
              margin: $problem_suporte_card_box_content_right_icon_margin;
            }
          }
        }
        .avatar {
          height: $problem_suporte_card_box_content_center_avatar_height;
        }
      }
    }
    .footer_wrap {
      padding: $problem_suporte_card_box_footer_wrap_padding;
      &-btn {
        .icon {
          margin: $problem_suporte_card_box_footer_btn_icon_margin;
          color: #fff;
        }
        .text {
          font-size: $problem_suporte_card_box_footer_btn_font_size;
          color: #fff;
        }
      }
    }
  }
  .iframe_box {
    width: 100vw;
    height: 100vh;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 99;
    overflow: hidden;
    .close_icon {
      margin-left: auto;
    }
  }
}

@include breakpoint-only-up() {
  .page.problem_suporte {
    .card_box {
      &.radius {
        border-radius: $problem_suporte_card_box_pc_radius;
      }
    }
  }
}
</style>
