<template>
  <div v-zinx v-if="state">
    <div class="wrapper-mask"></div>
    <div class="cpf_state_info_comp" :style="`top: ${top};`">
      <div ref="cpfStateInfoRef" class="cpf_state_info_box">
        <icon-font
          name="icon-icon_dizhi"
          :dark="commonStyle('cpf_state_info_icon_color')"
          :size="commonStyle(isApp ? 'cpf_state_info_icon_app_size' : 'cpf_state_info_icon_pc_size')"
        />
        <div class="text">{{ $t('UI_CPF_STATE_INFO') }}</div>
        <vue-select :value="checked" v-bind="selectStyle" @change="onChange"></vue-select>
        <v-btn :disabled="!checked" class="btn_submit" color="t_primary_dark_color" @click="onBtnPrimay">{{
          $t('UI_CPF_STATE_SUBMIT')
        }}</v-btn>
        <div class="tips">{{ $t('UI_CPF_STATE_TIPS') }}</div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import vueSelect from '@/components/ui/vue-select'
import { POP_TIME } from '@/api/setting'
import { saveUserStateName } from '@/api/modules/login.js'
import _common from '@style/components/dialog.modules.scss'

export default {
  components: {
    vueSelect
  },
  props: {
    top: {
      type: String,
      default: '50%'
    },
    onSubmit: {
      // 
      type: Function,
      default() {
        return () => {}
      }
    }
  },
  data() {
    return {
      state: false,
      checked: ''
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'systemConfig', 'user', 'languageType']),
    ...mapState('dialog', ['popupState']),
    selectStyle() {
      const { state_select_names = [] } = this.systemConfig
      const options = state_select_names.map((e, index) => {
        return { label: e, value: index + 1 }
      })
      return {
        placeholder: this.$t('UI_CPF_STATE_PLACEHOLDER'),
        inputHeight: this.isApp
          ? _common.cpf_state_info_select_app_inputHeight
          : _common.cpf_state_info_select_pc_inputHeight,
        inputWeight: this.isApp
          ? _common.cpf_state_info_select_app_inputWeight
          : _common.cpf_state_info_select_pc_inputWeight,
        fontSize: this.isApp ? _common.cpf_state_info_select_app_fontSize : _common.cpf_state_info_select_pc_fontSize,
        padding: this.isApp ? _common.cpf_state_info_select_app_padding : _common.cpf_state_info_select_app_padding,
        color: _common.cpf_state_info_select_color,
        iconColor: _common.cpf_state_info_select_iconColor,
        inputColor: _common.cpf_state_info_select_inputColor,
        borderRadius: this.isApp
          ? _common.cpf_state_info_select_app_borderRadius
          : _common.cpf_state_info_select_pc_borderRadius,
        border: _common.cpf_state_info_select_border,
        menuPadding: this.isApp
          ? _common.cpf_state_info_select_app_menuPadding
          : _common.cpf_state_info_select_pc_menuPadding,
        activeItemPadding: this.isApp
          ? _common.cpf_state_info_select_app_activeItemPadding
          : _common.cpf_state_info_select_pc_activeItemPadding,
        backgroundColor: _common.cpf_state_info_select_backgroundColor,
        offsetY: this.isApp ? _common.cpf_state_info_select_app_offsetY : _common.cpf_state_info_select_pc_offsetY,
        menuBorderRadius: this.isApp
          ? _common.cpf_state_info_select_app_menuBorderRadius
          : _common.cpf_state_info_select_pc_menuBorderRadius,
        memuBg: _common.cpf_state_info_select_memuBg,
        activeColor: _common.cpf_state_info_select_activeColor,
        activeBg: _common.cpf_state_info_select_activeBg,
        activeFontSize: this.isApp
          ? _common.cpf_state_info_select_app_activeFontSize
          : _common.cpf_state_info_select_pc_activeFontSize,
        options: options
      }
    }
  },
  watch: {
    // 
    popupState(n) {
      if (!n) {
        this.state = false
        if (this.remove) this.remove()
      }
    }
  },
  methods: {
    commonStyle: k => _common[k],
    // 
    show() {
      this.state = !this.state
      this.$nextTick(() => {
        const cpfStateInfoRef = this.$refs['cpfStateInfoRef']
        if (cpfStateInfoRef) this.$anim.insert(cpfStateInfoRef)
      })
      return Promise.resolve(this)
    },
    // /
    hide(callback) {
      this.$nextTick(() => {
        const cpfStateInfoRef = this.$refs['cpfStateInfoRef']
        if (cpfStateInfoRef) this.$anim.close(cpfStateInfoRef)

        setTimeout(() => {
          this.state = false
          if (this.remove) this.remove()
          if (typeof callback == 'function') callback()
        }, POP_TIME)
      })
    },
    // 
    async onBtnPrimay() {
      const options = this.selectStyle.options
      const index = this.checked - 1
      const params = {
        user_state_name: options[index]?.label,
        token: this.user.token,
        language: this.languageType
      }
      const res = await saveUserStateName(params).catch(e => e)
      if (res.code != 0) return
      this.hide(this.onSubmit)
    },
    onChange(value) {
      this.checked = value
    }
  }
}
</script>
<style scoped lang="scss">
@import '@style/components/dialog.modules.scss';
.cpf_state_info_comp {
  width: $cpf_state_info_pc_width;
  position: fixed;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  .cpf_state_info_box {
    width: $cpf_state_info_pc_width;
    height: $cpf_state_info_pc_height;
    padding: $cpf_state_info_pc_padding;
    background: $cpf_state_info_pc_background;
    border-radius: $cpf_state_info_pc_radius;
    margin: $cpf_state_info_pc_margin;
    .text {
      font-weight: $cpf_state_info_text_pc_weight;
      font-size: $cpf_state_info_text_pc_size;
      color: $cpf_state_info_text_pc_color;
      line-height: $cpf_state_info_text_pc_line;
      margin: $cpf_state_info_text_pc_margin;
    }
    .btn_submit {
      width: $cpf_state_info_text_btn_pc_width;
      height: $cpf_state_info_text_btn_pc_height;
      border-radius: $cpf_state_info_text_btn_pc_radius;
      margin: $cpf_state_info_text_btn_pc_margin;
      font-weight: $cpf_state_info_text_btn_pc_weight;
      font-size: $cpf_state_info_text_btn_pc_size;
      color: $cpf_state_info_text_btn_pc_color;
      &.v-btn--disabled {
        background: $cpf_state_info_text_btn_disabled_pc_background;
        color: $cpf_state_info_text_btn_disabled_pc_color;
      }
    }
    .tips {
      font-weight: $cpf_state_info_text_tips_pc_weight;
      font-size: $cpf_state_info_text_tips_pc_size;
      color: $cpf_state_info_text_tips_pc_color;
      line-height: $cpf_state_info_text_tips_pc_line;
    }
  }
}

@include breakpoint-only-xs() {
  .cpf_state_info_comp {
    width: $cpf_state_info_app_width;
    .cpf_state_info_box {
      width: $cpf_state_info_box_app_width;
      height: $cpf_state_info_app_height;
      padding: $cpf_state_info_app_padding;
      border-radius: $cpf_state_info_app_radius;
      .text {
        font-size: $cpf_state_info_text_app_size;
        line-height: $cpf_state_info_text_app_line;
        margin: $cpf_state_info_text_app_margin;
      }
      .btn_submit {
        width: $cpf_state_info_text_btn_app_width;
        height: $cpf_state_info_text_btn_app_height;
        margin: $cpf_state_info_text_btn_app_margin;
        font-size: $cpf_state_info_text_btn_app_size;
      }
      .tips {
        font-weight: $cpf_state_info_text_tips_app_weight;
        font-size: $cpf_state_info_text_tips_app_size;
        line-height: $cpf_state_info_text_tips_app_line;
      }
    }
  }
}
</style>
