<template>
  <div v-zinx v-if="state">
    <div class="wrapper-mask" @click="onCancel"></div>
    <div class="remind_resp_comp" :style="`top: ${top};`">
      <div ref="remindRespRef" class="remind_resp_box">
        <!--  -->
        <template v-if="!activeTime">
          <div class="remind_one_title">
            <div class="lt text_1">{{ $t('UI.Resp_Remind_Title') }}</div>
            <icon-font
              class="rt"
              name="icon-Icon_Cloose"
              :size="commonStyle('remind_title_icon_size')"
              :dark="commonStyle('remind_title_icon_color')"
              @click="onCancel"
            />
          </div>
          <div class="remind_one_text break_word">
            {{ $t('UI.Resp_Remind_Content') }}
          </div>
          <div class="remind_one_list">
            <div
              class="remind_item"
              :class="{ active: activeIndex == i }"
              v-for="(item, i) in remindTimeArr"
              :key="i"
              @click="activeIndex = i"
            >
              <span class="text">{{ remindTimeFilt(item) }}</span>
              <icon-font
                v-if="activeIndex == i"
                class="icon"
                name="icon-check_rightup"
                :size="commonStyle('remind_item_icon_size')"
              />
            </div>
          </div>
          <div class="remind_one_btn">
            <v-app light style="background: transparent">
              <v-btn
                class="btn_radius"
                :class="{ disabled_btn: activeIndex == null }"
                :color="commonStyle('remind_btn_color')"
                @click="onSubmitTime"
              >
                {{ $t('UI.Resp_Remind_Continue') }}
              </v-btn>
            </v-app>
          </div>
        </template>
        <!--  -->
        <template v-if="activeTime">
          <div class="remind_two_title">
            <div class="lt">
              <div class="lt_back" @click="onTwoBack">
                <icon-font
                  name="icon-Icon_left"
                  :size="commonStyle('remind_two_title_back_icon_size')"
                  :dark="commonStyle('remind_two_title_back_icon_color')"
                />
              </div>
              <div class="text text_1">
                {{ $t('UI.Resp_Remind_Confirmation') }}
              </div>
            </div>
            <icon-font
              class="rt"
              name="icon-Icon_Cloose"
              :size="commonStyle('remind_title_icon_size')"
              :dark="commonStyle('remind_title_icon_color')"
              @click="onCancel"
            />
          </div>
          <div class="remind_two_content">
            {{ $t('UI.Resp_Remind_Two_Submit_Text') }}
          </div>
          <div class="remind_two_set">
            <span class="set_time_sign">
              {{ $t('UI.Resp_Remind_Two_Selected_Time') }}
            </span>
            <div class="set_time_icon">
              <icon-font
                name="icon-icon_Selected_time"
                :size="commonStyle('remind_two_set_icon_size')"
                :dark="commonStyle('remind_two_set_icon_color')"
              />
              <span class="set_time_text">
                {{ $t('UI.Resp_Remind_Two_Every_Time', { time: remindTimeFilt(activeTime) }) }}
              </span>
            </div>
          </div>
          <div class="remind_two_text">
            {{ $t('UI.Resp_Remind_Two_Every_Sign', { time: remindTimeFilt(remindItem.edit_time) }) }}
          </div>
          <div class="remind_two_btn">
            <v-app light style="background: transparent">
              <v-btn class="btn_radius" :color="commonStyle('remind_two_btn_cancel_color')" @click="onCancel">
                {{ $t('UI.Resp_Remind_Cancel') }}
              </v-btn>
              <v-btn class="btn_radius" :color="commonStyle('remind_two_btn_submit_color')" @click="onSubmit">
                {{ $t('UI.Resp_Remind_Confirm') }}
              </v-btn>
            </v-app>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { POP_TIME } from '@/api/setting'
import { datetime } from '@/utils/helper.js'
import _common from '@style/components/dialog.modules.scss'

export default {
  props: {
    top: {
      type: String,
      default: '50%'
    },
    // 
    cancel: {
      type: Function,
      default: () => {}
    },
    // 
    submit: {
      type: Function,
      default: () => {}
    }
  },
  data() {
    return {
      state: false,
      activeIndex: null, // 
      activeTime: 0 //  
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'languageType']),
    ...mapState('responsible', ['remindItem']),
    ...mapState('dialog', ['popupState']),
    // 
    remindTimeArr() {
      const { config = [] } = this.remindItem
      return config
    }
  },
  watch: {
    // 
    popupState(n) {
      if (!n) {
        this.state = false
        if (this.remove) this.remove()
      }
    }
  },
  methods: {
    commonStyle: k => _common[k],
    // 
    remindTimeFilt(seco = 0) {
      return datetime.getFormatTime(seco)
    },
    // 
    show() {
      this.state = !this.state
      this.$nextTick(() => {
        const remindRespRef = this.$refs['remindRespRef']
        if (remindRespRef) this.$anim.insert(remindRespRef)
      })
    },
    // 
    hide(callback) {
      this.$nextTick(() => {
        const remindRespRef = this.$refs['remindRespRef']
        if (remindRespRef) this.$anim.close(remindRespRef)

        setTimeout(() => {
          this.state = false
          if (this.remove) this.remove()
          if (typeof callback == 'function') callback()
        }, POP_TIME)
      })
    },
    // 
    onSubmitTime() {
      if (this.activeIndex == null) return

      const time = this.remindTimeArr[this.activeIndex]
      this.activeTime = time
    },
    // 
    onTwoBack() {
      this.activeTime = 0
      this.activeIndex = null
    },
    // 
    onCancel() {
      this.hide(() => {
        if (this.cancel) this.cancel()
      })
    },
    // 
    async onSubmit() {
      const params = {
        token: this.user.token,
        language: this.languageType,
        time: this.activeTime
      }
      const res = await this.$store.dispatch('responsible/setReminderTime', params).catch(e => e)
      if (res.code != 0) return this.$pop.message().show({ content: res.msg })

      this.hide(() => {
        if (this.submit) this.submit()
      })
      this.$pop.message().show({ content: res.msg })
    }
  }
}
</script>

