<template>
  <div v-zinx v-if="state" class="comp notices">
    <div class="wrapper-mask" @click="onClose"></div>
    <div class="notices_box" :style="`top: ${top};`">
      <div ref="noticesRef" class="column_flex">
        <div class="carousel_wrap" :style="noticesBg">
          <div class="carousel_title">
            <div class="carousel_title_text text_1" :class="{ text_all: swiperList.length <= 1 }">
              {{ swiperList[activeIndex]?.title }}
            </div>
          </div>
          <!--  -->
          <template v-if="swiperList.length > 1">
            <swiper :options="swiperOptions" ref="noticeSwiper" @click.native="goLink('swiper')">
              <swiper-slide v-for="(item, i) in swiperList" :key="i">
                <div class="carousel_img" :style="`width: ${cardWidth}; height: ${cardHeight};`">
                  <img :class="`bg_img`" :src="item.content" alt="" />
                </div>
              </swiper-slide>
            </swiper>
            <div class="swiper-button-prev">
              <icon-font
                name="icon-Icon_left"
                :size="commonStyle('notices_arrow_prev_size')"
                :dark="commonStyle('notices_arrow_prev_color')"
              />
            </div>
            <div class="swiper-button-next">
              <icon-font
                name="icon-Icon_Right"
                :size="commonStyle('notices_arrow_next_size')"
                :dark="commonStyle('notices_arrow_next_color')"
              />
            </div>
          </template>
          <!--  -->
          <template v-else>
            <div class="carousel_img" :style="`width: ${cardWidth}; height: ${cardHeight};`" @click="goLink('image')">
              <img :class="`bg_img`" :src="swiperList[0]?.content" alt="" />
            </div>
          </template>
        </div>
        <icon-font
          class="close_icon cursor_point"
          name="icon-icon_pop_close"
          :size="commonStyle('notices_icon_close_size')"
          :dark="commonStyle('notices_icon_close_color')"
          @click="onClose"
        >
        </icon-font>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import { POP_TIME } from '@/api/setting'
import { getCacheIcon } from '@/assets/imgCache.js'
import _common from '@style/components/dialog.modules.scss'
import 'swiper/css/swiper.css'

export default {
  components: {
    Swiper,
    SwiperSlide
  },
  props: {
    top: {
      type: [Number, String],
      default: '50%'
    },
    // 
    bannerArr: {
      type: Array,
      default() {
        return []
      }
    },
    // 
    onCancel: {
      type: Function,
      default() {
        return () => {}
      }
    },
    // 
    onLink: {
      type: Function,
      default() {
        return () => {}
      }
    }
  },
  data() {
    return {
      state: false,
      swiperList: [], // 
      activeIndex: 0, // 
      swiperOptions: {
        // 
        slidesPerView: 'auto',
        loop: true,
        autoplay: {
          delay: 3000, // 
          stopOnLastSlide: false, // trueslide
          disableOnInteraction: false // swiperautoplaytrue
        },
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        },
        on: {
          slideChange: () => {
            const swiper = this.$refs['noticeSwiper']?.$swiper
            this.activeIndex = swiper?.realIndex || 0
          }
        }
      }
    }
  },
  computed: {
    ...mapState('dialog', ['popupState']),
    ...mapState('base', ['isApp', 'user', 'languageType', 'showRedDot']),
    cardWidth() {
      const { notices_app_width = '92%', notices_pc_width = '380px' } = _common
      const body = document.body.offsetWidth || document.body.clientWidth || 375

      let card_w = ''
      if (this.isApp) {
        const app_w = notices_app_width.includes('%')
          ? (parseInt(notices_app_width) / 100) * body
          : parseInt(notices_app_width)
        card_w = app_w
      } else {
        card_w = parseInt(notices_pc_width)
      }

      return `${card_w - 24}px`
    },
    cardHeight() {
      // const rate = 480 / 700;                             // 
      const rate = 420 / 674
      return `${parseInt(this.cardWidth) * rate}px`
    },
    // 
    noticesBg() {
      const result = getCacheIcon('notices_bg')
      if (result) {
        const src = typeof result === 'string' ? result : result.value.src

        return {
          background: `url(${src})`,
          backgroundSize: '100% 100%',
          backgroundRepeat: 'no-repeat'
        }
      }

      return {}
    }
  },
  watch: {
    // 
    popupState(n) {
      if (!n) {
        this.state = false
        if (this.remove) this.remove()
      }
    }
  },
  methods: {
    commonStyle: (k = '') => _common[k],
    // 
    bannerListFilter() {
      let list = JSON.parse(JSON.stringify(this.bannerArr))
      // list = list
      //   .map(item => {
      //     const { start_time_stamp = 0, end_time_stamp = 0 } = item
      //     const time = Math.round(Date.now() / 1000)
      //     if (time >= start_time_stamp && time < end_time_stamp) {
      //       const countTime = end_time_stamp - time

      //       return {
      //         ...item,
      //         countTime
      //       }
      //     }

      //     return null
      //   })
      //   .filter(f => f)

      this.swiperList = list
    },
    async show() {
      this.state = !this.state
      // 
      this.bannerListFilter()

      this.$nextTick(() => {
        const noticesRef = this.$refs['noticesRef']
        if (noticesRef) {
          this.$anim.insert(noticesRef)

          //  DOM 
          this.$refs['noticeSwiper']?.$swiper?.update()
        }
      })
    },
    hide(callback) {
      this.$nextTick(() => {
        const noticesRef = this.$refs['noticesRef']
        if (noticesRef) this.$anim.close(noticesRef)

        setTimeout(() => {
          this.state = false
          if (this.remove) this.remove()
          if (typeof callback == 'function') callback()
        }, POP_TIME)
      })
    },
    /**
     * 
     * @param {*} sign swiper: image:
     */
    goLink(sign = '') {
      let item = {}

      if (sign == 'swiper') {
        const index = this.$refs['noticeSwiper']?.$swiper?.realIndex
        item = this.swiperList[index] ? this.swiperList[index] : {}
      } else {
        item = this.swiperList[0] ? this.swiperList[0] : {}
      }

      this.hide(() => {
        this.onLink(item)
      })
    },
    // 
    onClose() {
      this.hide(() => {
        this.onCancel()
      })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/dialog.modules.scss';

// 
.notices_box {
  width: $notices_app_width;
  position: fixed;
  left: $notices_app_fixed_left;
  transform: translateY(-50%);
  .carousel_wrap {
    width: 100%;
    background: $notices_bg;
    min-height: $notices_app_min_height;
    padding: $notices_padding;
    border-radius: $notices_radius_app;
    position: relative;
    cursor: pointer;
    .carousel_title {
      width: $notices_title_width;
      display: flex;
      justify-content: $notices_title_justify_content;
      .carousel_title_text {
        width: $notices_title_text_width;
        font-size: $notices_title_text_size;
        font-weight: $notices_title_text_bold;
        padding: $notices_title_text_padding;
        color: $notices_title_text_color;
        text-align: $notices_title_text_align;
        text-transform: uppercase;
        &.text_all {
          width: $notices_title_text_one_width;
        }
      }
    }
    .carousel_img {
      border-radius: $notices_radius_app;
      overflow: hidden;
      .bg_img {
        width: $notices_title_img_width;
        height: $notices_title_img_height;
        object-fit: cover;
      }
    }
    .swiper-button-prev,
    .swiper-button-next {
      width: auto;
      height: auto;
      &::after {
        display: none;
      }
    }
    .swiper-button-prev {
      background: $notices_arrow_bg;
      position: absolute;
      top: $notices_arrow_prev_top;
      left: $notices_arrow_prev_left;
      right: $notices_arrow_prev_right;
      margin: $notices_arrow_prev_margin;
      padding: $notices_arrow_prev_padding;
      border-radius: $notices_arrow_prev_radius;
    }
    .swiper-button-next {
      background: $notices_arrow_bg;
      position: absolute;
      top: $notices_arrow_next_top;
      left: $notices_arrow_next_left;
      right: $notices_arrow_next_right;
      margin: $notices_arrow_next_margin;
      padding: $notices_arrow_next_padding;
      border-radius: $notices_arrow_next_radius;
    }
  }
  .close_icon {
    margin: $notices_icon_close_margin;
  }
}

// pc
@include breakpoint-only-up() {
  .notices_box {
    width: $notices_pc_width;
    position: fixed;
    left: $notices_pc_fixed_left;
    transform: translate(-50%, -50%);
    .carousel_wrap {
      min-height: $notices_pc_min_height;
      border-radius: $notices_radius_pc;
      .carousel_img {
        border-radius: $notices_radius_pc;
      }
    }
  }
}
</style>
