<template>
  <div v-zinx v-if="state">
    <div class="wrapper-mask" @click="hide"></div>
    <div class="download_box" :class="{ hide: !isState }">
      <div class="download_box_title_one">
        {{ $t('UI_Download_Tips1') }}
        <div class="title_icon">
          <icon-font name="icon-fenxiang" dark="t_white" :size="commonStyle('download_title_one_icon_size')" />
        </div>
      </div>
      <div class="download_box_title_two">
        {{ $t('UI_Download_Tips2') }}
        <icon-font
          class="title_icon_two"
          name="icon-a-tianjia1"
          dark="t_white"
          :size="commonStyle('download_title_two_icon_size')"
        />
        <div>"{{ $t('UI_Judge_AddToHome') }}"</div>
      </div>
      <div class="screen_list">
        <div
          v-for="(item, inx) in screenList"
          :key="inx"
          class="screen_list_item"
          :class="{ screen_list_active: inx == screenList.length - 1 }"
        >
          <div class="lt">{{ item.lable }}</div>
          <div class="rt">
            <icon-font :name="item.icon" :dark="item.color" :size="commonStyle('download_list_icon_size')" />
          </div>
        </div>
      </div>
      <div>
        <icon-font name="icon-icon_desktop_pulldown" dark="t_white" :size="commonStyle('download_arrow_icon_size')" />
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import loadIcon from '@/assets/icons'
import _common from '@style/components/dialog.modules.scss'

export default {
  data() {
    return {
      state: false,
      isState: false
    }
  },
  computed: {
    ...mapState('dialog', ['popupState']),
    screenList() {
      return [
        {
          lable: this.$t('UI_Add_To_Reading_List'),
          icon: 'icon-icon_desktop_readinglist',
          color: 't_text_color_5'
        },
        {
          lable: this.$t('UI_Add_Bookmark'),
          icon: 'icon-icon_desktop_bookmark',
          color: 't_text_color_5'
        },
        {
          lable: this.$t('UI_Add_To_Favourites'),
          icon: 'icon-icon_desktop_favourites',
          color: 't_text_color_5'
        },
        {
          lable: this.$t('UI_Find_On_Page'),
          icon: 'icon-icon_desktop_find',
          color: 't_text_color_5'
        },
        {
          lable: this.$t('UI_Add_To_Home_Screen'),
          icon: 'icon-a-tianjia1',
          color: 't_primary_dark_color'
        }
      ]
    }
  },
  watch: {
    // 
    popupState(n) {
      if (!n) {
        this.state = false
        if (this.remove) this.remove()
      }
    }
  },
  methods: {
    commonStyle: k => _common[k],
    loadIcon: key => loadIcon(key, false),
    show() {
      this.state = !this.state
      this.isState = !this.isState
    },
    hide() {
      this.isState = false
      setTimeout(() => {
        this.state = false
        if (this.remove) this.remove()
      }, 200)
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/dialog.modules.scss';

.download_box {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  width: $download_box_width;
  background-color: $download_box_bg;
  padding: $download_box_padding;
  font-size: $download_box_size;
  font-weight: $download_box_bold;
  @include setBox(column, flex-start, center);
  animation: download-show 0.2s linear 1 both;
  @keyframes download-show {
    0% {
      transform: translateY(100%);
    }
    50% {
      transform: translateY(20%);
    }
    100% {
      transform: translateY(0);
    }
  }
  &.hide {
    animation-name: download-hide;
    @keyframes download-hide {
      0% {
        transform: translateY(0);
      }
      50% {
        transform: translateY(80%);
      }
      100% {
        transform: translateY(100%);
      }
    }
  }
  .download_box_title_one {
    @include setBox(row, center, center);
    color: $download_title_one_color;
    .title_icon {
      font-size: 0;
      margin: $download_title_icon_one_margin;
    }
  }
  .download_box_title_two {
    @include setBox(row, center, center);
    color: $download_title_two_color;
    font-size: $download_title_two_size;
    font-weight: $download_title_two_bold;
    margin: $download_title_two_margin;
    .title_icon_two {
      font-size: 0;
      margin: $download_title_icon_two_margin;
    }
  }
  .screen_list {
    width: 100%;
    background: $download_list_bg;
    margin: $download_list_margin;
    font-size: $download_list_size;
    font-weight: $download_list_bold;
    padding: $download_list_padding;
    border-radius: $download_list_radius;
    .screen_list_item {
      @include setBox(row, space-between, center);
      padding: $download_list_item_padding;
      &.screen_list_active {
        width: 100%;
        background: $download_list_item_active_bg;
        border-radius: $download_list_item_active_radius;
        border: $download_list_item_active_border;
      }
      .lt {
        color: $download_list_color;
      }
      .rt {
        font-size: 0;
      }
    }
  }
}
</style>
