<template>
  <div v-zinx v-if="state">
    <div class="wrapper-mask" @click="onCancel"></div>
    <div class="loss_resp_comp" :style="`top: ${top};`">
      <div ref="lossLimitRef" class="loss_resp_box">
        <!--  -->
        <template v-if="!isConfirm">
          <div class="loss_one_title">
            <div class="lt text_1">{{ $t('UI.Resp_Loss_Limit') }}</div>
            <icon-font
              class="rt"
              name="icon-Icon_Cloose"
              :size="commonStyle('loss_title_icon_size')"
              :dark="commonStyle('loss_title_icon_color')"
              @click="onCancel"
            />
          </div>
          <div class="loss_one_text break_word">
            {{ $t('UI.Resp_Loss_Limit_Text_1') }}
          </div>
          <v-form ref="form" class="form">
            <div class="form_item">
              <label>{{ $t('UI.Resp_Loss_Limit_Form_1') }}</label>
              <v-text-field
                v-model="form.day_limit"
                solo
                full-width
                type="number"
                class="text_field"
                :placeholder="$t('UI.System_22')"
                flat
              />
            </div>
            <div class="form_item">
              <label>{{ $t('UI.Resp_Loss_Limit_Form_2') }}</label>
              <v-text-field
                v-model="form.week_limit"
                solo
                full-width
                type="number"
                class="text_field"
                :placeholder="$t('UI.System_22')"
                flat
              />
            </div>
            <div class="form_item">
              <label>{{ $t('UI.Resp_Loss_Limit_Form_3') }}</label>
              <v-text-field
                v-model="form.month_limit"
                solo
                full-width
                type="number"
                class="text_field"
                :placeholder="$t('UI.System_22')"
                flat
              />
            </div>
          </v-form>
          <div class="loss_one_btn">
            <v-app light style="background: transparent">
              <v-btn
                class="btn_radius"
                :class="{ disabled_btn: !effectTime }"
                :color="commonStyle('loss_btn_color')"
                @click="onContinue"
              >
                {{ $t('UI.Resp_Remind_Continue') }}
              </v-btn>
            </v-app>
          </div>
        </template>
        <!--  -->
        <template v-else>
          <div class="loss_two_title">
            <div class="lt">
              <div class="lt_back" @click="onTwoBack">
                <icon-font
                  name="icon-Icon_left"
                  :size="commonStyle('loss_two_title_back_icon_size')"
                  :dark="commonStyle('loss_two_title_back_icon_color')"
                />
              </div>
              <div class="text text_1">{{ $t('UI.Resp_Remind_Confirmation') }}</div>
            </div>
            <icon-font
              class="rt"
              name="icon-Icon_Cloose"
              :size="commonStyle('loss_title_icon_size')"
              :dark="commonStyle('loss_title_icon_color')"
              @click="onCancel"
            />
          </div>
          <div class="loss_two_content">{{ $t('UI.Resp_Loss_Limit_Tips_1') }}</div>
          <div class="loss_two_set">
            <div class="set_time_sign">{{ $t('UI.Resp_Loss_Limit') }}:</div>
            <div class="set_row">
              <div class="label">{{ $t('UI.Resp_Loss_Limit_Form_1') }}</div>
              <div class="val">{{ form.day_limit || '' }}</div>
            </div>
            <div class="set_row">
              <div class="label">{{ $t('UI.Resp_Loss_Limit_Form_2') }}</div>
              <div class="val">{{ form.week_limit || '' }}</div>
            </div>
            <div class="set_row">
              <div class="label">{{ $t('UI.Resp_Loss_Limit_Form_3') }}</div>
              <div class="val">{{ form.month_limit || '' }}</div>
            </div>
          </div>
          <div class="loss_two_text">
            {{ `${$t('UI.Resp_Loss_Limit_Tips_2')}: ${effectTime}` }}
          </div>
          <div class="loss_two_btn">
            <v-app light style="background: transparent">
              <v-btn class="btn_radius" :color="commonStyle('loss_two_btn_cancel_color')" @click="onCancel">
                {{ $t('UI.Resp_Remind_Cancel') }}
              </v-btn>
              <v-btn class="btn_radius" :color="commonStyle('loss_two_btn_submit_color')" @click="onSubmit">
                {{ $t('UI.Resp_Remind_Confirm') }}
              </v-btn>
            </v-app>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { POP_TIME } from '@/api/setting'
import { datetime } from '@/utils/helper.js'
import _common from '@style/components/dialog.modules.scss'

export default {
  props: {
    top: {
      type: String,
      default: '50%'
    },
    // 
    cancel: {
      type: Function,
      default: () => {}
    },
    // 
    submit: {
      type: Function,
      default: () => {}
    }
  },
  data() {
    return {
      state: false,
      activeIndex: null, // 
      activeTime: 0, //  

      form: {
        day_limit: '',
        week_limit: '',
        month_limit: ''
      },
      isConfirm: false, // 
      startTime: Date.now()
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'languageType']),
    ...mapState('responsible', ['lossLimitItem']),
    ...mapState('dialog', ['popupState']),
    effectTime() {
      const d = new Date(this.startTime)
      return datetime.formatDateTime(d, 'yyyy/MM/dd HH:mm')
    }
  },
  watch: {
    // 
    popupState(n) {
      if (!n) {
        this.state = false
        if (this.remove) this.remove()
      }
    }
  },
  methods: {
    commonStyle: k => _common[k],
    // 
    show(isEdit = false) {
      this.state = !this.state
      if (this.state && isEdit) {
        const def = { day_limit: '', week_limit: '', month_limit: '' }
        const form = this.lossLimitItem?.loss_limit || def
        this.form = Object.assign({}, form)
      }
      this.$nextTick(() => {
        const lossLimitRef = this.$refs['lossLimitRef']
        if (lossLimitRef) this.$anim.insert(lossLimitRef)
      })
    },
    // 
    hide(callback) {
      this.$nextTick(() => {
        const lossLimitRef = this.$refs['lossLimitRef']
        if (lossLimitRef) this.$anim.close(lossLimitRef)

        setTimeout(() => {
          this.state = false
          if (this.remove) this.remove()
          if (typeof callback == 'function') callback()
        }, POP_TIME)
      })
    },
    // 
    onContinue() {
      // 
      this.isConfirm = true
      this.startTime = Date.now()
    },
    // 
    onTwoBack() {
      this.isConfirm = false
    },
    // 
    onCancel() {
      this.hide(() => {
        if (this.cancel) this.cancel()
      })
    },
    // 
    async onSubmit() {
      const params = {
        token: this.user.token,
        language: this.languageType,
        ...this.form
      }
      const res = await this.$store.dispatch('responsible/setLoss', params).catch(e => e)
      if (res.code != 0) return this.$pop.message().show({ content: res.msg })

      this.hide(() => {
        if (this.submit) this.submit()
      })
      this.$pop.message().show({ content: res.msg })
    }
  }
}
</script>

