<template>
  <div v-if="state" v-zinx="{ page: 'perform' }" class="comp perform">
    <div class="wrapper-mask"></div>
    <div class="perform_box_comp" :style="`top: ${top}`">
      <div ref="performBoxRef" class="perform_box column_flex">
        <div id="upgradeIds" ref="performRef" :style="`width: ${animationSize.w}; height: ${animationSize.h};`"></div>
        <div class="perform_centent">
          <div class="perform_text text_center line_3">
            {{ $t('UI_VIP_upgrade') }}
          </div>
          <div class="perform_level text_center line_3">{{ $t('UI_VIP_Share.grade') }} {{ level }}</div>
          <div class="perform_btn">
            <div class="pri_btn_text btn_radius" @click="onBtnClick">
              {{ $t('UI_Privileges') }}
            </div>
            <icon-font
              class="pri_btn_cancel cursor_point"
              name="icon-icon_pop_close"
              :size="commonStyle('perform_btn_colse_size')"
              :dark="commonStyle('perform_btn_colse_color')"
              @click="onBtnCancel"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { getResour } from '@/utils/loadServer.js'
import { POP_TIME } from '@/api/setting'
import _common from '@style/components/dialog.modules.scss'
let performAnimation = null
let performApp = null

export default {
  props: {
    top: {
      type: String,
      default: '50%'
    },
    level: {
      // vip 
      type: [Number, String],
      default: 1
    },
    onSubmit: {
      // 
      type: Function,
      default() {
        return () => {}
      }
    },
    onCancel: {
      // 
      type: Function,
      default() {
        return () => {}
      }
    }
  },
  data() {
    return {
      state: false
    }
  },
  computed: {
    ...mapState('dialog', ['popupState']),
    ...mapState('base', ['isApp']),
    // 
    animationSize() {
      const { perform_animation_width = '330px', perform_animation_height = '330px' } = _common
      return {
        w: perform_animation_width,
        h: perform_animation_height
      }
    }
  },
  watch: {
    // 
    popupState(n) {
      if (!n) {
        this.state = false
        if (this.remove) this.remove()
      }
    }
  },
  beforeDestroy() {
    if (performApp) performApp.stop()
  },
  methods: {
    commonStyle: (k = '') => _common[k],
    show() {
      const { libsPath = '' } = JSON.parse(process.env.VUE_APP_PATH)
      getResour('pixi', [`${libsPath}/pixi.min.js`, `${libsPath}/pixi-spine.js`], r => {
        if (r) {
          this.state = !this.state
          setTimeout(_ => this.getPerform(), 207)
        } else {
          this.onBtnCancel()
        }
      })
    },
    hide(callback) {
      this.$nextTick(() => {
        const performBoxRef = this.$refs['performBoxRef']
        if (performBoxRef) this.$anim.close(performBoxRef)

        setTimeout(() => {
          this.state = false
          if (this.remove) this.remove()
          if (typeof callback == 'function') callback()
        }, POP_TIME)
      })
    },
    getPerform() {
      if (!this.$refs['performRef']) return

      const containerX = parseInt(this.animationSize.w) // 
      const containerY = parseInt(this.animationSize.h) // 

      performApp = new window.PIXI.Application({
        width: containerX,
        height: containerY,
        transparent: true
      })
      this.$refs['performRef'].appendChild(performApp.view)

      const { spinePath = '' } = JSON.parse(process.env.VUE_APP_PATH)
      if (!performAnimation) performAnimation = performApp.loader.add('container', `${spinePath}/upgrade.json`)
      performAnimation.load((loader, resources) => {
        let dt = resources.container.spineData
        const animation = new window.PIXI.spine.Spine(dt)
        animation.width = containerX
        animation.height = containerY
        animation.position.set(containerX / 2, containerY / 2)
        if (animation.state.hasAnimation('animation')) {
          animation.state.setAnimation(0, 'animation', true)
          animation.state.timeScale = 1
        }

        performApp.start()
        performApp.stage.addChild(animation)

        // 
        // animation.state.tracks[0].listener = {
        // 	complete: function (trackEntry, count) {
        // 		performApp.stop();
        // 	},
        // };
      })
    },
    // 
    onBtnClick() {
      this.hide(() => {
        this.onSubmit()
      })
    },
    // 
    onBtnCancel() {
      this.hide(() => {
        this.onCancel()
      })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/dialog.modules.scss';

// 
.perform_box_comp {
  width: $perform_app_width;
  position: fixed;
  left: $perform_app_fixed_left;
  transform: translateY(-50%);
  .perform_box {
    width: 100%;
    .perform_centent {
      position: relative;
      top: $perform_centent_top;
      .text_center {
        font-size: $perform_text_size;
        font-weight: $perform_text_bold;
        color: $perform_text_color;
      }
      .perform_level {
        font-size: $perform_level_size;
        font-weight: $perform_level_bold;
        color: $perform_level_color;
      }
      .perform_btn {
        margin: 40px 0 0 0;
        @include setBox(column, center, center);
        .pri_btn_text {
          font-size: $perform_btn_size;
          font-weight: $perform_btn_bold;
          padding: $perform_btn_padding;
          background: $perform_btn_bg;
          color: $perform_btn_color;
        }
        .pri_btn_cancel {
          margin: $perform_btn_colse_margin;
        }
      }
    }
  }
}
// pc
@include breakpoint-only-up() {
  .perform_box_comp {
    width: $perform_pc_width;
    left: $perform_pc_fixed_left;
    transform: translate(-50%, -50%);
  }
}
</style>
