<template>
  <div v-zinx class="comp aviso" v-if="state">
    <div class="wrapper-mask" @click="hide"></div>
    <div class="aviso_box_comp" :style="`top: ${top};`">
      <div ref="avisoRef" class="aviso_box column_flex">
        <div class="aviso_wrap column_flex" :style="avisoBg">
          <div class="aviso_title flex_row">
            <span class="aviso_title_lt text_1" :class="`text_${titleAlign}`">
              {{ notice.title }}
            </span>
          </div>
          <div class="aviso_content">
            <div class="aviso_text line_3">
              <div class="content" v-html="content(notice.content)"></div>
              <div class="text-decoration-underline link" @click.stop="goLink">
                {{ notice.url_text }}
              </div>
            </div>
          </div>
        </div>
        <icon-font
          class="aviso_close cursor_point"
          name="icon-icon_pop_close"
          :dark="commonStyle('aviso_box_title_icon_color')"
          :size="commonStyle('aviso_box_title_icon_size')"
          @click="hide"
        ></icon-font>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { POP_TIME } from '@/api/setting'
import { getCacheIcon } from '@/assets/imgCache.js'
import _common from '@style/components/dialog.modules.scss'

export default {
  props: {
    top: {
      type: [Number, String],
      default: '50%'
    },
    notices: {
      // 
      type: Array,
      default() {
        return []
      }
    },
    titleAlign: {
      // 
      type: String,
      default: _common.aviso_props_title_align
    },
    onCancel: {
      // 
      type: Function,
      default() {
        return () => {}
      }
    },
    onLink: {
      // 
      type: Function,
      default() {
        return () => {}
      }
    }
  },
  data() {
    return {
      noticeArr: [], // 
      notice: {}, // 
      state: false
    }
  },
  computed: {
    ...mapState('base', ['isApp']),
    ...mapState('dialog', ['popupState']),
    // 
    avisoBg() {
      const result = getCacheIcon('aviso_bg')
      if (result) {
        const src = typeof result === 'string' ? result : result.value.src

        return {
          background: `url(${src})`,
          backgroundSize: '100% 100%',
          backgroundRepeat: 'no-repeat'
        }
      }
    },
    // 
    content() {
      return content => {
        return content && content.replace(/\\"/g, '')
      }
    }
  },
  watch: {
    // 
    popupState(n) {
      if (!n) {
        this.state = false
        if (this.remove) this.remove()
      }
    }
  },
  methods: {
    commonStyle: k => _common[k],
    // 
    goLink() {
      this.hide()
      this.onLink(this.notice)
    },
    // 
    show() {
      // 
      if (!this.notices.length || this.noticeArr.length > 0) return

      this.noticeArr = JSON.parse(JSON.stringify(this.notices))
      // is_show == 1 
      this.noticeArr[0].is_show = 1
      this.notice = this.noticeArr[0]
      this.open()
    },
    // 
    open() {
      this.state = !this.state
      this.$nextTick(() => {
        const avisoRef = this.$refs['avisoRef']
        if (avisoRef) this.$anim.insert(avisoRef)
      })
    },
    hide() {
      this.$nextTick(async () => {
        const avisoRef = this.$refs['avisoRef']
        if (avisoRef) this.$anim.close(avisoRef)

        const resShow = await new Promise((resolve, reject) => {
          setTimeout(() => {
            this.state = false

            // 
            const noticeIndex = this.noticeArr.findIndex(f => f.is_show != 1)
            if (noticeIndex == -1) return reject({ code: 'cancel' })

            resolve({ code: 'ok', index: noticeIndex })
          }, POP_TIME)
        }).catch(e => e)

        if (resShow.code == 'ok') {
          this.noticeArr[resShow.index].is_show = 1
          this.notice = this.noticeArr[resShow.index]
          this.open()
        } else {
          if (this.remove) this.remove()
          this.noticeArr = []
          this.onCancel()
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/dialog.modules.scss';

// 
.aviso_box_comp {
  width: $aviso_app_width;
  position: fixed;
  left: $aviso_app_fixed_left;
  transform: translateY(-50%);
  .aviso_box {
    width: 100%;
    height: 100%;
    overflow: hidden;
    .aviso_wrap {
      height: $aviso_app_height;
      border-radius: $aviso_radius_app;
      background: $aviso_box_bg;
      .aviso_title {
        flex-shrink: 0;
        flex-grow: 0;
        background: $aviso_box_title_bg;
        width: $aviso_box_width;
        padding: $aviso_box_padding;
        margin: $aviso_box_margin;
        .aviso_title_lt {
          width: 100%;
          color: $aviso_box_title_lt_color;
          font-size: $aviso_title_lt_size;
          font-weight: $aviso_title_lt_bold;
        }
      }
      .aviso_content {
        width: $aviso_content_width;
        padding: $aviso_content_padding;
        flex-shrink: 1;
        flex-grow: 1;
        overflow-y: auto;
        .aviso_text {
          width: 100%;
          height: 100%;
          color: $aviso_content_text_color !important;
          overflow-y: auto;
          .content {
            font-size: $aviso_content_text_size;
            font-weight: $aviso_content_text_bold;
            word-break: break-word;
            word-wrap: break-word;
          }
          .link {
            text-decoration: underline;
            font-size: $aviso_content_link_size;
            font-weight: $aviso_content_link_bold;
            margin: $aviso_content_link_margin;
            color: $aviso_content_link_color;
          }
        }
      }
    }
    .aviso_close {
      margin: $aviso_box_title_icon_margin;
    }
  }
}

// pc
@include breakpoint-only-up() {
  .aviso_box_comp {
    width: $aviso_pc_width;
    left: $aviso_pc_fixed_left;
    transform: translate(-50%, -50%);
    .aviso_box {
      .aviso_wrap {
        height: $aviso_pc_height;
        border-radius: $aviso_radius_pc;
      }
    }
  }
}
</style>
