<template>
  <div class="comp lucky_turntable">
    <div
      id="luckyTurntableId"
      class="lucky_turntable_box"
      :style="loadIcon(isApp ? 'game_lucky_turntable_app' : 'game_lucky_turntable_pc', true)"
    >
      <!--  -->
      <div ref="luckyWithinRef" class="lucky_turntable_within">
        <div v-for="item in list" :key="item.stall_id" class="lucky_turntable_li">
          <div class="lucky_turntable_li_content" :class="{ max: item.stall_id == 1 }">
            <div class="lucky_turntable_li_content_item">
              <img class="item_icon" :src="itemIcon(item)" alt="" />
              <div class="item_money">{{ item.gold }}</div>
            </div>
            <div class="lucky_divider"></div>
          </div>
        </div>
      </div>
      <!--  -->
      <div class="lucky_turntable_pointer">
        <img :src="loadIcon(isApp ? 'game_licky_pointer_app' : 'game_licky_pointer_pc')" alt="" />
      </div>
      <!--  -->
      <div class="lucky_turntable_gold_mask" :class="isInLottery ? 'start' : 'stop'">
        <img :src="loadIcon(isApp ? 'game_lucky_mask_app' : 'game_lucky_mask_pc')" alt="" />
      </div>
      <!-- / -->
      <div
        id="luckyPointerId"
        class="lucky_pointer"
        :style="loadIcon(isApp ? 'game_lucky_pointer_app' : 'game_lucky_pointer_pc', true)"
      >
        <span class="lucky_pointer_text">VIP{{ getVipCurrentLevel() }}</span>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapState } from 'vuex'
import loadIcon from '@/assets/icons'
import _common from '@style/components/dialog.lucky.modules.scss'

export default {
  data() {
    return {
      list: [], // 
      oldStallId: 0, // 
      isInLottery: false // 
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'systemConfig', 'languageType']),
    ...mapGetters('base', ['getVipCurrentLevel']),
    //  4-8
    num() {
      return Math.round(Math.floor(Math.random() * 4 + 4) + Math.ceil(Math.random() * 8) / 8)
    }
  },
  beforeDestroy() {
    const luckyWithinRef = this.$refs['luckyWithinRef']
    if (luckyWithinRef) {
      luckyWithinRef.removeEventListener(
        'animationend',
        typeof this.animationendFun == 'function' ? this.animationendFun : () => {}
      )
    }
  },
  methods: {
    commonStyle: k => _common[k],
    loadIcon: (key, bool = false) => loadIcon(key, bool),
    // 
    itemIcon(item = {}) {
      let icon = this.isApp ? 'game_lucky_species_app' : 'game_lucky_species_pc'
      if (item.lucky_wheel_icon == 1) icon = this.isApp ? 'game_lucky_gift_app' : 'game_lucky_gift_pc'
      if (item.lucky_wheel_icon == 2) icon = this.isApp ? 'game_lucky_purse_app' : 'game_lucky_purse_pc'

      return this.loadIcon(icon)
    },
    /**
     * 
     * list: 
     */
    init({ list = [] } = {}) {
      this.list = list
      this.$nextTick(() => {
        //  - 
        const angle = 360 / (list.length || 12)

        const luckyTurntableId = document.getElementById('luckyTurntableId')
        if (!luckyTurntableId) return
        const luckyTurntableId_w = luckyTurntableId.clientWidth || luckyTurntableId.offsetWidth
        const rtion = this.commonStyle(this.isApp ? 'lucky_turntable_app_rtion' : 'lucky_turntable_pc_rtion')
        luckyTurntableId.style.height = `${luckyTurntableId_w / rtion}px`

        const lucky_turntable_within = luckyTurntableId.querySelectorAll('.lucky_turntable_within')[0]
        const lucky_turntable_within_w = lucky_turntable_within.clientWidth || lucky_turntable_within.offsetWidth
        lucky_turntable_within.style.height = `${lucky_turntable_within_w}px`

        const lucky_turntable_li = lucky_turntable_within.querySelectorAll('.lucky_turntable_li')
        lucky_turntable_li.forEach((dom, i) => {
          const dom_w = lucky_turntable_within_w / 2
          const width = dom_w * Math.tan((angle / 2) * (Math.PI / 180)) * 2
          dom.style.width = `${width}px`
          dom.style.height = `${dom_w}px`
          dom.style.left = '50%'
          dom.style.top = '0'
          dom.style.transformOrigin = '50% bottom'
          dom.style.transform = `translate(-50%, 0) rotateZ(${i * angle}deg)`
          dom.style.clipPath = 'polygon(0 0, 50% 100%, 100% 0)'

          const lucky_divider = dom.querySelectorAll('.lucky_divider')[0]
          lucky_divider.style.width = this.commonStyle(
            this.isApp
              ? 'lucky_turntable_li_content_item_divider_app_width'
              : 'lucky_turntable_li_content_item_divider_pc_width'
          )
          lucky_divider.style.height = `${dom_w}px`
          lucky_divider.style.left = '50%'
          lucky_divider.style.top = 0
          lucky_divider.style.transformOrigin = '50% bottom'
          lucky_divider.style.transform = `translate(-50%, 0) rotate(${angle / 2}deg)`
        })

        const lucky_pointer = luckyTurntableId.querySelectorAll('.lucky_pointer')[0]
        const lucky_pointer_w = lucky_pointer.clientWidth || lucky_pointer.offsetWidth
        lucky_pointer.style.height = `${lucky_pointer_w / (110 / 120)}px`
      })
    },
    /**
     * 
     * 
     * stall_id id
     */
    rotate({ stall_id = 1 }) {
      console.log('stall_id', JSON.parse(JSON.stringify(this.list.find(f => f.stall_id == stall_id))))

      const totalLength = this.list.length || 12
      // 
      const angle = 360 / totalLength
      // 
      const index = this.list.findIndex(f => f.stall_id == stall_id)
      if (index == -1) return
      //  =  - 
      const intervalNum = totalLength - index
      //  =  - 
      let interval = intervalNum - this.oldStallId
      interval = interval <= 0 ? totalLength + interval : interval

      // 
      const lucky_turntable_within = document.querySelectorAll('.lucky_turntable_within')[0]
      if (!lucky_turntable_within) return
      // 
      let oldRotate = lucky_turntable_within.style.transform
      oldRotate = !oldRotate ? 0 : parseFloat(oldRotate.match(/\((\S*)\)/)[1])
      // 
      const rotateAngle = this.num * 360 + interval * angle

      this.startRotate(lucky_turntable_within, oldRotate, rotateAngle, intervalNum)
    },
    /**
     * 
     * @param {*} dom 
     * @param {*} oldRotate 
     * @param {*} rotateAngle 
     * @param {*} intervalNum 
     */
    startRotate(dom, oldRotate, rotateAngle, intervalNum) {
      dom.removeEventListener(
        'animationend',
        typeof this.animationendFun == 'function' ? this.animationendFun : () => {}
      )
      this.animationendFun = null

      // 
      const luckyPointerId = document.getElementById('luckyPointerId')
      if (luckyPointerId) luckyPointerId.style.animationPlayState = 'paused'

      // 
      dom.style.animation = 'lucky-rotate 5s ease 1 both'
      dom.style.setProperty('--turntable-start', `${oldRotate}deg`)
      dom.style.setProperty('--turntable-end', `${oldRotate + rotateAngle}deg`)

      this.animationendFun = () => this.startEnd(dom, oldRotate, rotateAngle, intervalNum)
      // 
      dom.addEventListener('animationend', this.animationendFun)
      this.isInLottery = true
    },
    /**
     * 
     * @param {*} dom 
     * @param {*} oldRotate 
     * @param {*} rotateAngle 
     * @param {*} intervalNum 
     */
    startEnd(dom, oldRotate, rotateAngle, intervalNum) {
      let currentRotate = (oldRotate + rotateAngle) % 360
      currentRotate = currentRotate.toFixed(2)

      dom.style.transform = `rotate(${currentRotate}deg)`
      dom.style.animation = 'none'
      this.oldStallId = intervalNum

      // 
      const luckyPointerId = document.getElementById('luckyPointerId')
      if (luckyPointerId) luckyPointerId.style.animationPlayState = 'running'

      this.$emit('end')
      this.isInLottery = false
    }
  }
}
</script>

<style lang="scss">
@import '@style/components/dialog.lucky.modules.scss';

.comp.lucky_turntable {
  width: 100%;
  @include setBox(row, center, center);
  // 
  .lucky_turntable_box {
    width: $lucky_turntable_app_width;
