    background-size: $lucky_turntable_app_bg_size;
    background-repeat: $lucky_turntable_app_bg_repeat;
    border-radius: $lucky_turntable_app_radius;
    position: relative;
    @include setBox(row, center, center);
    .lucky_turntable_within {
      width: $lucky_turntable_within_app_width;
      border-radius: $lucky_turntable_within_app_radius;
      position: relative;
      top: $lucky_turntable_within_app_top;
      z-index: 3;
      background: $lucky_turntable_within_app_bg;
      overflow: hidden;
      @keyframes lucky-rotate {
        0% {
          transform: rotate(var(--turntable-start));
        }

        100% {
          transform: rotate(var(--turntable-end));
        }
      }
      .lucky_turntable_li {
        position: absolute;
        .lucky_turntable_li_content {
          width: $lucky_turntable_li_content_app_width;
          height: $lucky_turntable_li_content_app_height;
          position: relative;
          &.max {
            background: $lucky_turntable_li_content_max_app_bg;
          }
          .lucky_turntable_li_content_item {
            width: $lucky_turntable_li_content_item_app_width;
            height: $lucky_turntable_li_content_item_app_height;
            position: absolute;
            left: $lucky_turntable_li_content_item_app_left;
            transform: $lucky_turntable_li_content_item_app_transform;
            padding: $lucky_turntable_li_content_item_app_padding;
            @include setBox(column, flex-start, center);
            .item_icon {
              width: $lucky_turntable_li_content_item_icon_app_width;
            }
            .item_money {
              margin: $lucky_turntable_li_content_item_money_app_margin;
              font-size: $lucky_turntable_li_content_item_money_app_size;
              font-weight: $lucky_turntable_li_content_item_money_app_weight;
              color: $lucky_turntable_li_content_item_money_app_color;
              transform: $lucky_turntable_li_content_item_money_app_transform;
            }
          }
          .lucky_divider {
            background: $lucky_turntable_li_content_item_divider_app_bg;
            position: absolute;
          }
        }
      }
    }
    .lucky_turntable_pointer {
      display: $lucky_turntable_pointer_app_display;
      width: $lucky_turntable_pointer_app_width;
      position: absolute;
      top: $lucky_turntable_pointer_app_top;
      z-index: 6;
      img {
        width: 100%;
      }
    }
    .lucky_turntable_gold_mask {
      display: $lucky_turntable_gold_mask_app_display;
      width: $lucky_turntable_gold_mask_app_width;
      position: absolute;
      top: $lucky_turntable_gold_mask_app_top;
      left: $lucky_turntable_gold_mask_app_left;
      right: $lucky_turntable_gold_mask_app_right;
      z-index: 7;
      animation-name: lucky-gold-mask-start;
      animation-duration: 1s;
      animation-timing-function: linear;
      animation-iteration-count: 1;
      animation-fill-mode: both;
      &.start {
        animation-name: lucky-gold-mask-end;
      }
      &.stop {
        animation-name: lucky-gold-mask-start;
      }
      img {
        width: 100%;
      }
      @keyframes lucky-gold-mask-start {
        0% {
          opacity: 0;
        }
        60% {
          opacity: 0;
          transform: scale(0.9);
        }
        100% {
          opacity: 1;
          transform: scale(1);
        }
      }
      @keyframes lucky-gold-mask-end {
        0% {
          opacity: 1;
          transform: scale(1);
        }
        60% {
          opacity: 0;
          transform: scale(0.9);
        }
        100% {
          opacity: 0;
        }
      }
    }
    .lucky_pointer {
      width: $lucky_pointer_app_width;
      background-size: $lucky_pointer_app_bg_size;
      background-repeat: $lucky_pointer_app_bg_repeat;
      position: absolute;
      top: $lucky_pointer_app_top;
      left: $lucky_pointer_app_left;
      transform: $lucky_pointer_app_transform;
      z-index: 5;
      @include setBox(row, center, center);
      animation: lucky-pointer linear 3s infinite both;
      .lucky_pointer_text {
        font-size: $lucky_pointer_text_app_size;
        font-weight: $lucky_pointer_text_app_weight;
        color: $lucky_pointer_text_app_color;
        position: relative;
        top: $lucky_pointer_text_app_top;
      }
    }
  }
  // pc
  @include breakpoint-only-up() {
    .lucky_turntable_box {
      width: $lucky_turntable_pc_width;
      background: $lucky_turntable_pc_bg;
      background-size: $lucky_turntable_pc_bg_size;
      background-repeat: $lucky_turntable_pc_bg_repeat;
      border-radius: $lucky_turntable_pc_radius;
      position: relative;
      @include setBox(row, center, center);
      .lucky_turntable_within {
        width: $lucky_turntable_within_pc_width;
        border-radius: $lucky_turntable_within_pc_radius;
        position: relative;
        z-index: 3;
        background: $lucky_turntable_within_pc_bg;
        overflow: hidden;
        @keyframes lucky-rotate {
          0% {
            transform: rotate(var(--turntable-start));
          }

          100% {
            transform: rotate(var(--turntable-end));
          }
        }
        .lucky_turntable_li {
          position: absolute;
          .lucky_turntable_li_content {
            width: $lucky_turntable_li_content_pc_width;
            height: $lucky_turntable_li_content_pc_height;
            position: relative;
            &.max {
              background: $lucky_turntable_li_content_max_pc_bg;
            }
            .lucky_turntable_li_content_item {
              width: $lucky_turntable_li_content_item_pc_width;
              height: $lucky_turntable_li_content_item_pc_height;
              position: absolute;
              left: $lucky_turntable_li_content_item_pc_left;
              transform: $lucky_turntable_li_content_item_pc_transform;
              padding: $lucky_turntable_li_content_item_pc_padding;
              @include setBox(column, flex-start, center);

              .item_icon {
                width: $lucky_turntable_li_content_item_icon_pc_width;
              }
              .item_money {
                margin: $lucky_turntable_li_content_item_money_pc_margin;
                font-size: $lucky_turntable_li_content_item_money_pc_size;
                font-weight: $lucky_turntable_li_content_item_money_pc_weight;
                color: $lucky_turntable_li_content_item_money_pc_color;
                transform: $lucky_turntable_li_content_item_money_pc_transform;
              }
            }
            .lucky_divider {
              background: $lucky_turntable_li_content_item_divider_pc_bg;
              position: absolute;
            }
          }
        }
      }
      .lucky_turntable_pointer {
        display: $lucky_turntable_pointer_pc_display;
        width: $lucky_turntable_pointer_pc_width;
        position: absolute;
        top: $lucky_turntable_pointer_pc_top;
        z-index: 6;
        img {
          width: 100%;
        }
      }
      .lucky_turntable_gold_mask {
        display: $lucky_turntable_gold_mask_pc_display;
        width: $lucky_turntable_gold_mask_pc_width;
        position: absolute;
        top: $lucky_turntable_gold_mask_pc_top;
        left: $lucky_turntable_gold_mask_pc_left;
        right: $lucky_turntable_gold_mask_pc_right;
        z-index: 7;
        animation-name: lucky-gold-mask-start;
        animation-duration: 1s;
        animation-timing-function: linear;
        animation-iteration-count: 1;
        animation-fill-mode: both;
        &.start {
          animation-name: lucky-gold-mask-end;
        }
        &.stop {
          animation-name: lucky-gold-mask-start;
        }
        img {
          width: 100%;
        }
        @keyframes lucky-gold-mask-start {
          0% {
            opacity: 0;
          }
          60% {
            opacity: 0;
            transform: scale(0.9);
          }
          100% {
            opacity: 1;
            transform: scale(1);
          }
        }
        @keyframes lucky-gold-mask-end {
          0% {
            opacity: 1;
            transform: scale(1);
          }
          60% {
            opacity: 0;
            transform: scale(0.9);
          }
          100% {
            opacity: 0;
          }
        }
      }
      .lucky_pointer {
        width: $lucky_pointer_pc_width;
        background-size: $lucky_pointer_pc_bg_size;
        background-repeat: $lucky_pointer_pc_bg_repeat;
        position: absolute;
        top: $lucky_pointer_pc_top;
        left: $lucky_pointer_pc_left;
        transform: $lucky_pointer_pc_transform;
        z-index: 5;
        @include setBox(row, center, center);
        animation: lucky-pointer linear 3s infinite both;
        .lucky_pointer_text {
          font-size: $lucky_pointer_text_pc_size;
          font-weight: $lucky_pointer_text_pc_weight;
          color: $lucky_pointer_text_pc_color;
          position: relative;
          top: $lucky_pointer_text_pc_top;
        }
      }
    }
  }

  @keyframes lucky-pointer {
    0% {
      transform: translate(-50%, -50%) scale(1);
    }
    50% {
      transform: translate(-50%, -50%) scale(0.85);
    }
    100% {
      transform: translate(-50%, -50%) scale(1);
    }
  }
}
</style>
