<template>
  <div v-if="state" class="comp guide_mask"></div>
</template>

<script>
import { mapState } from 'vuex'
import { jump } from '@/utils/linkMap.js'

// '13001' => '-',
// '13002' => '-',
// '13003' => '-',
// '13004' => '-',
// '13005' => '-',
// '13006' => '-',
// '13007' => '',
// '13008' => '',
// '13009' => '-Bonus',
// '13010' => '-',
// '13011' => '',
// '13012' => '',
// 
const insideCode = [
  {
    code: 13001, // code
    appId: 'depositRecordId', // id - app
    pcId: 'depositRecordId', // id - pc
    x: 'center', //  x 
    y: 'center' //  y 
  },
  {
    code: 13002,
    appId: 'withdrawRecordId',
    pcId: 'withdrawRecordId',
    x: 'center',
    y: 'center'
  },
  {
    code: 13003,
    appId: 'accountPwdId',
    pcId: 'accountPwdId',
    x: 'center',
    y: 'center'
  },
  {
    code: 13004,
    appId: 'washScheduleId',
    pcId: 'washScheduleId',
    x: 'center',
    y: 'center'
  },
  {
    code: 13005,
    appId: 'bankAccountId',
    pcId: 'bankAccountId',
    x: 'center',
    y: 'start'
  },
  {
    code: 13006,
    appId: 'fabDownloadAppId',
    pcId: 'fabDownloadPcId',
    x: 'end',
    y: 'end'
  },
  {
    code: 13007,
    appId: 'languageSelectId',
    pcId: 'languageSelectId',
    x: 'center',
    y: 'center'
  },
  {
    code: 13008,
    appId: '',
    pcId: '',
    x: 'center',
    y: 'center'
  },
  {
    code: 13009,
    appId: '',
    pcId: '',
    x: 'center',
    y: 'center'
  },
  {
    code: 13010,
    appId: 'chestCardId',
    pcId: 'chestCardId',
    x: 'center',
    y: 'center'
  },
  {
    code: 13011,
    appId: '',
    pcId: '',
    x: 'center',
    y: 'center'
  },
  {
    code: 13012,
    appId: 'moneySignCardId',
    pcId: 'moneySignCardId',
    x: 'center',
    y: 'center'
  }
]

export default {
  props: {
    // 
    item: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  data() {
    return {
      state: false
    }
  },
  computed: {
    ...mapState('base', ['isApp'])
  },
  methods: {
    // 
    show() {
      this.state = true
      // 
      this.$store.dispatch('modal/setDepositWithdrawShow', { show: false })

      jump(this.item, this)

      this.$nextTick(() => {
        setTimeout(() => {
          const { inside_data = {} } = this.item
          const { id = '' } = inside_data
          const insideItem = insideCode.find(f => f.code == id)
          if (!insideItem) return

          const { appId, pcId, x, y } = insideItem
          const dom = document.getElementById(this.isApp ? appId : pcId)

          // 
          if (dom) dom.scrollIntoView({ block: x || 'center', inline: y || 'center' })
        }, 200)
      })

      this.hide()

      /**
       *  --> 
       */
      // const { inside } = this.item;
      // const insideItem = insideCode.find(f => f.code == inside);
      // if (!insideItem) return;

      // const { appId, pcId } = insideItem;
      // this.guideHighlight(this.isApp ? appId : pcId);
    },
    // 
    hide() {
      this.state = false
      if (this.remove) this.remove()
    },
    // 
    guideHighlight(domId = '') {
      if (!domId) return

      this.$nextTick(() => {
        const dom = document.getElementById(domId)
        if (!dom) return

        // 
        setTimeout(() => {
          dom.scrollIntoView({
            behavior: 'smooth',
            block: 'center',
            inline: 'center'
          })
        }, 200)

        //  class
        dom.classList.add('guide_highlight')
        // 
        this.domId = domId
        const appBox = document.getElementById('app_box')
        appBox.addEventListener('click', this.guideFun)
      })
    },
    // 
    guideFun() {
      // 
      this.hide()
      // 
      const appBox = document.getElementById('app_box')
      appBox.removeEventListener('click', this.guideFun)

      /**
       * 
       */
      const dom = document.getElementById(this.domId)
      if (!dom) return
      //  class
      dom.classList.remove('guide_highlight')
    }
  }
}
</script>

<style lang="scss" scoped></style>
