<template>
  <div v-zinx v-if="state">
    <div class="wrapper-mask"></div>
    <div class="vip_reward_seat" :style="`top: ${top};`">
      <div ref="vipRewardBgRef" class="column_flex">
        <div class="vip_reward_bg column_flex" :style="getRewardBg">
          <div class="vip_reward_box column_flex">
            <div class="vip_reward_content line_3">{{ content }}</div>
            <div class="vip_reward_btn">
              <v-app light style="background: transparent">
                <v-btn color="t_btn_color_3" class="btn_lt btn_radius" @click="onBtnCancel">
                  {{ cancelText }}
                </v-btn>
              </v-app>
              <v-app class="submit_btn" light style="background: transparent">
                <v-btn color="t_btn_host_color" class="btn_rt btn_radius" @click="onBtnPrimay">
                  {{ primaryText }}
                </v-btn>
              </v-app>
            </div>
          </div>
        </div>
        <icon-font
          class="cursor_point icon_close"
          name="icon-icon_pop_close"
          :size="commonStyle('vip_reward_icon_size')"
          :dark="commonStyle('vip_reward_icon_color')"
          @click="onBtnCancel"
        >
        </icon-font>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { POP_TIME } from '@/api/setting'
import { getCacheIcon } from '@/assets/imgCache.js'
import _common from '@style/components/dialog.modules.scss'

export default {
  props: {
    top: {
      type: [Number, String],
      default: '50%'
    },
    content: {
      // 
      type: String,
      default: ''
    },
    cancelText: {
      // 
      type: String,
      default: ''
    },
    primaryText: {
      // 
      type: String,
      default: ''
    },
    onSubmit: {
      // 
      type: Function,
      default() {
        return () => {}
      }
    },
    onCancel: {
      // 
      type: Function,
      default() {
        return () => {}
      }
    }
  },
  data() {
    return {
      state: false
    }
  },
  computed: {
    ...mapState('dialog', ['popupState']),
    ...mapState('base', ['isApp']),
    // 
    getRewardBg() {
      const result = getCacheIcon('reward_bg')
      if (result) {
        const src = typeof result === 'string' ? result : result.value.src

        return {
          background: `url(${src})`,
          backgroundSize: '100% 100%',
          backgroundRepeat: 'no-repeat'
        }
      }

      return {}
    }
  },
  watch: {
    // 
    popupState(n) {
      if (!n) {
        this.state = false
        if (this.remove) this.remove()
      }
    }
  },
  methods: {
    commonStyle: k => _common[k],
    // 
    show() {
      this.state = !this.state
      this.$nextTick(() => {
        const vipRewardBgRef = this.$refs['vipRewardBgRef']
        if (vipRewardBgRef) this.$anim.insert(vipRewardBgRef)
      })

      return Promise.resolve(this)
    },
    // /
    hide(callback) {
      this.$nextTick(() => {
        const vipRewardBgRef = this.$refs['vipRewardBgRef']
        if (vipRewardBgRef) this.$anim.close(vipRewardBgRef)

        setTimeout(() => {
          this.state = false
          if (this.remove) this.remove()
          if (typeof callback == 'function') callback()
        }, POP_TIME)
      })
    },
    // 
    onBtnPrimay() {
      this.hide(() => {
        this.onSubmit()
      })
    },
    // 
    onBtnCancel() {
      this.hide(() => {
        this.onCancel()
      })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/dialog.modules.scss';

// 
.vip_reward_seat {
  width: $vip_reward_app_width;
  position: fixed;
  left: $vip_reward_app_fixed_left;
  transform: translateY(-50%);
  .vip_reward_bg {
    width: 100%;
    padding: $vip_reward_padding;
    background: $vip_reward_bg;
    border-radius: $vip_reward_radius_app;
    .vip_reward_box {
      padding: $vip_reward_box_padding;
      width: 100%;
      .vip_reward_content {
        width: $vip_reward_content_width;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        color: $vip_reward_content_color;
        font-size: $vip_reward_content_size;
        font-weight: $vip_reward_content_bold;
        padding: $vip_reward_content_padding;
      }
      .vip_reward_btn {
        @include setBox(row, space-between, center);
        width: $vip_reward_btn_width;
        .submit_btn {
          margin: $vip_reward_submit_btn_margin;
        }
        .btn_lt,
        .btn_rt {
          font-size: $vip_reward_submit_btn_size !important;
          height: $vip_reward_submit_btn_height !important;
        }
      }
    }
  }
  .icon_close {
    margin: $vip_reward_icon_close_margin;
  }
}

// pc
@include breakpoint-only-up() {
  .vip_reward_seat {
    width: $vip_reward_pc_width;
    position: fixed;
    left: $vip_reward_pc_fixed_left;
    transform: translate(-50%, -50%);
    .vip_reward_bg {
      border-radius: $vip_reward_radius_pc;
    }
  }
}
</style>
