<template>
  <div class="comp lucky_record_list">
    <div class="lucky_record_box">
      <div class="record_top">
        <div class="winning_record_close">
          <icon-font
            name="icon-Icon_left"
            :size="colorStyle(isApp ? 'lucky_record_close_app_size' : 'lucky_record_close_pc_size')"
            :dark="colorStyle(isApp ? 'lucky_record_close_app_color' : 'lucky_record_close_pc_color')"
            @click="onClose"
          />
        </div>
        <div class="winning_record_tab">
          <div
            class="history"
            :class="{ active: tab == item.type }"
            v-for="item in tabList"
            :key="item.type"
            @click="onRecordTab(item)"
          >
            {{ item.text }}
          </div>
        </div>
      </div>
      <div class="record_list">
        <template v-if="recordList.length > 0">
          <div v-if="tab == 2" class="record_head tab-2">
            <span class="text player">
              {{ $t('UI.Lucky_Record_List_Player') }}
            </span>
            <span class="text reward">{{ $t('UI.Lucky_Record_List_Rewards') }}</span>
            <span class="text time">{{ $t('UI.Lucky_Record_List_Time') }}</span>
          </div>
          <div v-else class="record_head tab-1">
            <span class="text time">
              {{ $t('UI.Lucky_Record_List_Time') }}
            </span>
            <span class="text reward">{{ $t('UI.Lucky_Record_List_Rewards') }}</span>
          </div>

          <transition-group name="lucky-record" tag="div">
            <template v-if="tab == 2">
              <div class="record_li tab-2" v-for="item in rendList" :key="item.id">
                <div class="player">
                  <user-sprite-image :value="item.headimg || 1" size="32" />
                  <span class="name">{{ item.nickname }}</span>
                </div>
                <div class="reward">{{ item.gold }}</div>
                <div class="time">{{ $u.useTimestamp(item.create_time, 'yyyy-MM-dd HH:mm') }}</div>
              </div>
            </template>
            <template v-else>
              <div class="record_li tab-1" v-for="item in rendList" :key="item.id">
                <div class="time">{{ $u.useTimestamp(item.create_time, 'yyyy-MM-dd HH:mm') }}</div>
                <div class="reward">{{ item.gold }}</div>
              </div>
            </template>
          </transition-group>
        </template>
        <template v-else>
          <empty />
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { getLuckyRecord } from '@/api/modules/game.js'
import UserSpriteImage from '@/components/ui/userSpriteImage/user-sprite-image'
import Empty from '@/components/ui/empty'
import _color from '@style/components/dialog.lucky.modules.scss'

export default {
  components: {
    UserSpriteImage,
    Empty
  },
  data() {
    return {
      tab: 2, // 2: 1:
      limit: 50, // 
      recordList: [] // 
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'languageType']),
    // tab list
    tabList() {
      return [
        {
          type: 2,
          text: this.$t('UI.Lucky_Record_List_History')
        },
        {
          type: 1,
          text: this.$t('UI.Lucky_Record_List_My_Rewards')
        }
      ]
    },
    // 
    rendList() {
      const list = JSON.parse(JSON.stringify(this.recordList))
      return list.splice(0, 15)
    }
  },
  created() {
    this.getList()
  },
  methods: {
    colorStyle: k => _color[k],
    // 
    async getList() {
      const params = {
        language: this.languageType,
        token: this.user.token,
        type: this.tab,
        limit: this.limit
      }
      const res = await getLuckyRecord(params).catch(e => e)
      if (!res || res.code != 0) return

      const list = res.data && Array.isArray(res.data) ? res.data : []
      this.recordList = this.tab == 2 ? list.sort(() => (Math.random() > 0.5 ? -1 : 1)) : list
      this.getRenew()
    },
    /**
     * 
     * 
     */
    getRenew() {
      if (this.tab != 2) return
      if (this.timer) clearTimeout(this.timer)

      //  1-5 
      let time = Math.random() * 5
      time = time < 1 ? 1 : time

      this.timer = setTimeout(() => {
        // 
        let num = parseInt(Math.random() * this.recordList.length)
        num = num < 1 ? 1 : num
        // 
        let item = this.recordList[num] ? JSON.parse(JSON.stringify(this.recordList[num])) : ''
        if (item) {
          item.id = parseInt(Math.random() * 666666666666)
          this.recordList.unshift(item)
        }

        this.getRenew()
      }, time * 1000)
    },
    // tab
    onRecordTab({ type = 0 }) {
      if (this.tab == type) return

      if (this.timer) clearTimeout(this.timer)
      this.recordList = []
      this.tab = type
      this.getList()
    },
    // 
    onClose() {
      this.$emit('recordClose')
    }
  },
  beforeDestroy() {
    if (this.timer) clearTimeout(this.timer)
  }
}
</script>

