<template>
  <div v-if="state" v-zinx="{ page: 'max' }">
    <div class="wrapper-mask"></div>
    <div class="screen_site_comp">
      <div ref="screenSiteBoxRef" class="screen_site_box">
        <div v-if="!isApp" class="screen_logo">
          <img :src="loadIcon('screen_logo')" alt="" />
        </div>
        <div class="screen_site_box_dom">
          <div class="screen_site_img">
            <img :src="loadIcon('logo_lg')" alt="" />
          </div>
          <div class="screen_site_content">
            <div class="screen_site_title">{{ $t('UI.Currency_Site_Title') }}</div>
            <div class="screen_site_select">
              <div class="screen_site_select_box" @click="onSiteShow">
                <div class="lt">
                  <img class="currency_img" :src="getSelectSite.icon" alt="" />
                  <div class="currency_text text_1">
                    {{ getSelectSite.currency || '' }}&nbsp;{{ getSelectSite.country || '' }}
                  </div>
                </div>
                <div class="rt" :class="{ show: siteListStatus == 'open', hide: siteListStatus == 'close' }">
                  <icon-font name="icon-Icon_Down" size="16" dark="t_text_color_2" />
                </div>
              </div>
              <site-dialog
                ref="siteDialogRef"
                zinx="max"
                @select-site="onSelectSite"
                @close="siteListStatus = 'close'"
              />
            </div>
            <div v-zoom class="screen_site_btn" @click="onSubmit">
              <span class="text">{{ $t('UI.Screen_Site_Lobby_Btn_Text') }}</span>
              <icon-font class="icon" name="icon-Icon_Path" :size="isApp ? 20 : 24" dark="t_white" />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import { POP_TIME } from '@/api/setting.js'
import loadIcon from '@/assets/icons.js'
import SiteDialog from '@/components/ui/site-dialog'

export default {
  components: {
    SiteDialog
  },
  props: {
    // 
    submit: {
      type: Function,
      default() {
        return () => {}
      }
    }
  },
  data() {
    return {
      state: false,
      //  open: close:
      siteListStatus: '',
      // 
      selectSite: null
    }
  },
  computed: {
    ...mapState('base', ['isApp']),
    ...mapState('dialog', ['popupState']),
    ...mapGetters('base', ['getCurSiteInfo']),
    // 
    getSelectSite() {
      return this.selectSite || this.getCurSiteInfo
    }
  },
  watch: {
    // 
    popupState(n) {
      if (!n) {
        this.state = false
        if (this.remove) this.remove()
      }
    }
  },
  methods: {
    loadIcon: key => loadIcon(key, false),
    // 
    show() {
      this.state = !this.state
      this.$nextTick(() => {
        const screenSiteBoxRef = this.$refs['screenSiteBoxRef']
        if (screenSiteBoxRef) this.$anim.insert(screenSiteBoxRef)
      })
    },
    // 
    hide(callback) {
      this.$nextTick(() => {
        const screenSiteBoxRef = this.$refs['screenSiteBoxRef']
        if (screenSiteBoxRef) this.$anim.close(screenSiteBoxRef)

        setTimeout(() => {
          this.state = false
          if (this.remove) this.remove()
          if (typeof callback == 'function') callback()
        }, POP_TIME)
      })
    },
    // 
    onSiteShow() {
      this.$nextTick(() => {
        const siteDialogRef = this.$refs['siteDialogRef']
        if (siteDialogRef) {
          siteDialogRef.show()
          this.siteListStatus = 'open'
        }
      })
    },
    // 
    onSelectSite(item = {}) {
      this.selectSite = item
    },
    // 
    onSubmit() {
      this.hide()
      if (this.submit) this.submit(this.selectSite || this.getCurSiteInfo)
    }
  }
}
</script>

<style lang="scss" scoped>
.screen_site_comp {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: $project_bg;
  @include setBox(row, center, center);
  user-select: none;
  .screen_site_box {
    width: 100%;
    .screen_site_box_dom {
      width: 100%;
      @include setBox(column, center, center);
      gap: 106px 0;
      .screen_site_img {
        width: 56%;
        aspect-ratio: 662/176;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .screen_site_content {
        width: 100%;
        padding: 0 40px;
        .screen_site_title {
          width: 100%;
          font-size: 14px;
          font-weight: 600;
          color: $text_color_4;
          line-height: 1;
        }
        .screen_site_select {
          width: 100%;
          height: 48px;
          margin: 16px 0 0 0;
          background: $card_bg;
          cursor: pointer;
          .screen_site_select_box {
            width: 100%;
            height: 100%;
            padding: 0 18px;
            @include setBox(row, center, center);
            .lt {
              flex-grow: 1;
              flex-shrink: 1;
              overflow: hidden;
              @include setBox(row, flex-start, center);
              .currency_img {
                flex-grow: 0;
                flex-shrink: 0;
                width: 18px;
                height: 18px;
                border-radius: 50%;
              }
              .currency_text {
                flex-grow: 1;
                flex-shrink: 1;
                margin: 0 0 0 8px;
                font-size: 14px;
                color: $white;
                font-weight: 400;
                line-height: 1;
              }
            }
            .rt {
              flex-grow: 0;
              flex-shrink: 0;
              animation-duration: 0.2s;
              animation-timing-function: linear;
              animation-iteration-count: 1;
              animation-fill-mode: both;
              &.show {
                animation-name: down-show;
                @keyframes down-show {
                  0% {
                    transform: rotate(0);
                  }
                  100% {
                    transform: rotate(180deg);
                  }
                }
              }
              &.hide {
                animation-name: down-hide;
                @keyframes down-hide {
                  0% {
                    transform: rotate(180deg);
                  }
                  100% {
                    transform: rotate(0);
                  }
                }
              }
            }
          }
        }
        .screen_site_btn {
          width: 100%;
          height: 40px;
          margin: 24px 0 0 0;
          border-radius: 4px;
          background: $primary_dark_color;
          @include setBox(row, center, center);
          gap: 0 6px;
          cursor: pointer;
          .text {
            font-size: 12px;
            font-weight: 400;
            color: $white;
            line-height: 1;
          }
          .icon {
            transform: rotate(90deg);
          }
        }
      }
    }
  }
}

// pc
@include breakpoint-only-up() {
  .screen_site_comp {
    width: 560px;
    top: calc(50% - 280px);
    left: calc(50% - 280px);
    right: unset;
    bottom: unset;
    background: none;
    .screen_site_box {
      width: 100%;
      display: flex;
      .screen_logo {
        flex-grow: 0;
        flex-shrink: 0;
        width: 160px;
        aspect-ratio: 160/390;
        border-radius: 20px 0 0 20px;
        overflow: hidden;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .screen_site_box_dom {
        flex-grow: 1;
        flex-shrink: 1;
        padding: 0 50px;
        gap: 56px 0;
        background: $card_bg;
        border-radius: 0 20px 20px 0;
        .screen_site_img {
          width: 54%;
        }
        .screen_site_content {
          width: 100%;
          padding: 0;
          .screen_site_title {
            color: $white;
          }
          .screen_site_select {
            background: $project_bg;
            position: relative;
          }
          .screen_site_btn {
            height: 48px;
            margin: 36px 0 0 0;
            gap: 0 6px;
            .text {
              font-size: 14px;
            }
          }
        }
      }
    }
  }
}
</style>
